import React from "react";
import { useMemo } from "react";

import { Typography } from "@mui/material";

const CurrentTime = (props: { currentTimeSecs: number }) => {
  const { currentTimeSecs } = props;
  const playingTime = useMemo(() => {
    const mins = Math.floor(currentTimeSecs / 60);
    const secs = Math.floor(currentTimeSecs % 60);
    return `${String(mins).padStart(2, "0")}:${String(secs).padStart(2, "0")}`;
  }, [currentTimeSecs]);

  return (
    <Typography
      variant="body1"
      sx={{
        p: 0,
        m: 0,
        height: "100%",
      }}
    >
      {playingTime}
    </Typography>
  );
};

export default CurrentTime;
