import React from "react";
import { Box } from "@mui/material";

const LetterIcon = (props: {
  letter: string;
  color?: string;
  backgroundColor?: string;
  size?: number;
}) => {
  const { letter, color, backgroundColor, size } = props;
  return (
    <Box
      sx={{
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        borderRadius: 2,
        p: 0,
        m: 0,
        color,
        backgroundColor,
        width: size,
        height: size,
      }}
    >
      {letter}
    </Box>
  );
};

export default LetterIcon;
