import React from "react";
import { useCallback, useEffect, useMemo, useRef, useState } from "react";

import {
  Box,
  Button,
  Checkbox,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
  Drawer,
  FormControl,
  FormControlLabel,
  Grid,
  InputLabel,
  MenuItem,
  Select,
  Stack,
} from "@mui/material";
import { EditorViewMode, LeftPanelMode, ViewMode } from "@/lib/types/main";
import { Track } from "@/lib/types/editor";
import TrackList, { EditSurface } from "@/components/editor/TrackList";
import { BaseAnimationOptions } from "@/lib/types/BaseAnimationOptions";
import { FileBundle } from "@/lib/types/load";
import hacks from "@/lib/hacks";
import usePrefs from "@/hooks/usePrefs";
import WavesurferAudioPlayer from "@/components/WavesurferAudioPlayer";
import { createAnimationsFromTemplate } from "@/lib/midi/animations/util";
import TemplatesSelect from "@/components/TemplatesSelect";
import useEditor from "@/hooks/useEditor";
import MetadataPanel from "./editor/MetadataPanel";
import useAudioPlayer from "@/hooks/useAudioPlayer";
import useRegions from "@/hooks/useRegions";
import useMessages from "@/hooks/useMessages";
import AdvancedAnimationEditorPanel from "./editor/AdvancedAnimationEditorPanel";
import * as flags from "@/lib/flags";

interface EditorProps {
  fileBundle: Required<FileBundle>;
  audioName?: string;
  flatTracks: Track[];
}

const Editor = (props: EditorProps) => {
  const { fileBundle, flatTracks } = props;

  const {
    midi: midiUrl,
    audio: audioUrl,
    midiz: intialMidizUrl,
    selectedTemplate: templateName,
  } = fileBundle;

  const [selectedTrackIndex, setSelectedTrackIndex] = useState<number | undefined>(0);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [editDrawerOpen, setEditDrawerOpen] = useState(false);
  const [complexEditTrackIndex, setComplexEditTrackIndex] = useState<number>(undefined);
  const [settingsDrawerOpen, setSettingsDrawerOpen] = useState(false);
  const [templatesDialogOpen, setTemplatesDialogOpen] = useState(false);
  const [hasStarted, setHasStarted] = useState(false);
  const [mutedAnimationIds, setMutedAnimationIds] = useState<string[]>([]);
  const [soloedAnimationIds, setSoloedAnimationIds] = useState<string[]>([]);
  const [activeGroupedTrackNums, setActiveGroupedTrackNums] = useState<number[]>([]);
  const [verboseAnimationIds, setVerboseAnimationIds] = useState<string[]>([]);
  const [activeEditSurfaceAnimationIds, setActiveEditSurfaceAnimationIds] = useState<{ id: string, editSurface: EditSurface }[]>([]);
  const [isLoaded, setIsLoaded] = useState(false);
  const [onlyShowActive, setOnlyShowActive] = useState(false);
  const [wasPlayingBeforeEdit, setWasPlayingBeforeEdit] = useState(false);

  const {
    viewMode,
    setViewMode,
    editorViewMode,
    setEditorViewMode,
    leftPanelMode,
    setLeftPanelMode,
    showTrackDetails,
    setShowTrackDetails,
  } = usePrefs();

  const {
    audioElm,
    divElm,
    canvasElm,
    midizUrl,
    saveEnabled,
    animationToEdit,
    animations,
    registry,
    tracks,
    upload,
    saveAll,
    download,
    moveAnimationToEdit,
    changeAnimationToEditType,
    doneEditAnimation,
    changeAnimationType,
    setSaveEnabled,
    setAnimationToEdit,
    setAnimations,
    muteAnimationById,
    soloAnimationById,
    verboseAnimationById,
    isPlaying,
    setIsPlaying,
  } = useEditor({
    fileBundle,
    flatTracks,
  });

  useEffect(() => {
    if (!isPlaying) return;
    setHasStarted(true);
  }, [isPlaying]);

  const selectedTrack = useMemo(() => {
    return selectedTrackIndex === undefined ? undefined : tracks[selectedTrackIndex];
  }, [tracks, selectedTrackIndex]);

  const {
    previewedTracks: previewGroupedTrackNums,
    regions,
    setRegions,
    previewTrack: previewAnimation,
  } = useRegions({
    selectedTrack,
    tracks,
  });

  const wavesurferRef = useRef<WaveSurfer>();
  const { pauseAudio, playAudio, stopAudio, restartAudio, rewindAudio, fastFowrardAudio, setDurationSecs } =
    useAudioPlayer({
      wavesurferRef,
    });

  const handleSettingsDrawerClose = useCallback(() => {
    setSettingsDrawerOpen(false);
  }, [setSettingsDrawerOpen]);

  const handleTemplatesDialogClose = useCallback(() => {
    setTemplatesDialogOpen(false);
  }, [setTemplatesDialogOpen]);

  const isEditDialogOpen = useMemo(
    () => !!animationToEdit && editDialogOpen,
    [animationToEdit, editDialogOpen]
  );

  const isComplexEditDrawerOpen = useMemo(
    () => !!animationToEdit && complexEditTrackIndex !== undefined,
    [animationToEdit, complexEditTrackIndex]
  );

  const isSettingsDrawerOpen = useMemo(() => !!settingsDrawerOpen, [settingsDrawerOpen]);

  const isTemplatesDialogOpen = useMemo(() => !!templatesDialogOpen, [templatesDialogOpen]);

  const openTrackToEdit = useCallback(
    (animation: number, surface) => {
      const anim = animations.current[animation];
      console.log("editopenTrackToEdit", surface, anim);
      if (surface === "dialog") {
        setEditDialogOpen(true);
        setEditDrawerOpen(false);
        setComplexEditTrackIndex(undefined);
      }
      if (surface === "drawer") {
        setEditDrawerOpen(true);
        setEditDialogOpen(false);
        setComplexEditTrackIndex(undefined);
      }
      if (surface === "complexDrawer") {
        setWasPlayingBeforeEdit(isPlaying);
        pauseAudio();
        setEditDrawerOpen(false);
        setEditDialogOpen(false);
        setComplexEditTrackIndex(animation);
      }
      setAnimationToEdit(anim);
    },
    [animations.current, setEditDialogOpen, setEditDrawerOpen, setAnimationToEdit, pauseAudio, setWasPlayingBeforeEdit, isPlaying]
  );

  const handleComplexEditDrawerReset = (newOptions: BaseAnimationOptions) => {
    // TODO
  }

  const closeComplexEditDrawer = useCallback((newOptions: BaseAnimationOptions) => {
    setAnimationToEdit(null);
    setSaveEnabled(true); //????
    setComplexEditTrackIndex(undefined);
    if (wasPlayingBeforeEdit) {
      playAudio();
    }
    setWasPlayingBeforeEdit(false);
  }, [setAnimationToEdit, setSaveEnabled, setComplexEditTrackIndex, wasPlayingBeforeEdit]);

  const handleComplexEditDrawerClose = useCallback((newOptions: BaseAnimationOptions) => {
    closeComplexEditDrawer(newOptions);
  }, [closeComplexEditDrawer]);


  const handleComplexEditDrawerSave = useCallback((newOptions: BaseAnimationOptions) => {
    if (!newOptions) return;

    console.log("handleEditDialogSave newOptions", newOptions);

    const id = animationToEdit?.id;

    setAnimationToEdit((prev) => {
      if (!prev) return prev;
      prev.options = newOptions;
      return { ...prev };
    });
    setAnimations((prev) => {
      if (!prev) return prev;
      const res = prev.map((anim, i) => {
        if (anim.id === id) {
          anim.options = newOptions;
        }
        return anim;
      });
      return [...res];
    });
    setSaveEnabled(true);
    closeComplexEditDrawer(newOptions);
  },
    [setAnimations, closeComplexEditDrawer, setAnimationToEdit]);

  const openSettings = useCallback(() => {
    setSettingsDrawerOpen(true);
  }, [setSettingsDrawerOpen]);

  const openTemplates = useCallback(() => {
    setTemplatesDialogOpen(true);
  }, [setTemplatesDialogOpen]);

  const { leftXS, rightXS, editDrawerWidth, complexEditDrawerWidth } = useMemo(() => {
    switch (leftPanelMode) {
      case "small":
        return { leftXS: 2, rightXS: 10, editDrawerWidth: 250, complexEditDrawerWidth: 1200 };
      case "normal":
        return { leftXS: 3, rightXS: 9, editDrawerWidth: 450, complexEditDrawerWidth: 1200 };
      case "wide":
        return { leftXS: 6, rightXS: 6, editDrawerWidth: 650, complexEditDrawerWidth: 1200 };
    }
  }, [leftPanelMode]);

  const trackName = useMemo(() => {
    if (complexEditTrackIndex === undefined) return "";
    const track = tracks[complexEditTrackIndex];
    return track?.name || "";
  }, [tracks, complexEditTrackIndex]);

  const ViewModeForm = useCallback(() => {
    return (
      <FormControl size="small" sx={{ width: 100 }}>
        <InputLabel id="view-mode-select-label">Display</InputLabel>
        <Select
          labelId="view-mode-select-label"
          id="view-mode-select"
          value={viewMode}
          label="Display"
          onChange={(e) => setViewMode(e.target.value as ViewMode)}
          sx={{ textAlign: "center" }}
        >
          <MenuItem value={"laptop"}>Small</MenuItem>
          <MenuItem value={"widescreen"}>Big</MenuItem>
        </Select>
      </FormControl>
    );
  }, [viewMode, setViewMode]);

  const TracksForm = useCallback(() => {
    return (
      <FormControl size="small" sx={{ width: 100 }}>
        <InputLabel id="view-mode-select-label">Tracks</InputLabel>
        <Select
          labelId="view-mode-select-label"
          id="view-mode-select"
          value={leftPanelMode}
          label="Tracks"
          onChange={(e) => setLeftPanelMode(e.target.value as LeftPanelMode)}
          sx={{ textAlign: "center" }}
        >
          <MenuItem value={"small"}>Small</MenuItem>
          <MenuItem value={"normal"}>Normal</MenuItem>
          <MenuItem value={"wide"}>Wide</MenuItem>
        </Select>
      </FormControl>
    );
  }, [leftPanelMode, setLeftPanelMode]);

  const EditorViewModeForm = useCallback(() => {
    return (
      <FormControl size="small" sx={{ width: 100 }}>
        <InputLabel id="editor-view-mode-select-label">Editor</InputLabel>
        <Select
          labelId="editor-view-mode-select-label"
          id="editor-view-mode-select"
          value={editorViewMode}
          label="Editor"
          onChange={(e) => setEditorViewMode(e.target.value as EditorViewMode)}
          sx={{ textAlign: "center" }}
        >
          <MenuItem value={"tree"}>Tree</MenuItem>
          <MenuItem value={"view"}>View</MenuItem>
          <MenuItem value={"form"}>Form</MenuItem>
          <MenuItem value={"code"}>Code</MenuItem>
          <MenuItem value={"text"}>Text</MenuItem>
        </Select>
      </FormControl>
    );
  }, [editorViewMode, setEditorViewMode]);

  const ShowTrackDetailsForm = useCallback(() => {
    return (
      <FormControlLabel
        control={
          <Checkbox
            checked={showTrackDetails}
            onChange={(e) => {
              console.log(e.target.checked);
              setShowTrackDetails(e.target.checked);
            }}
          />
        }
        label={"Show track details?"}
      />
    );
  }, [showTrackDetails, setShowTrackDetails]);

  const settingsDialog = useMemo(() => {
    const isOpen = isSettingsDrawerOpen;
    const onClose = handleSettingsDrawerClose;
    return (
      <Dialog fullWidth open={isOpen} onClose={onClose}>
        <DialogTitle>Settings</DialogTitle>
        <DialogContent>
          <Stack spacing={1} direction={"column"} sx={{ p: 1, m: 1 }}>
            <ViewModeForm />
            <EditorViewModeForm />
            <TracksForm />
            <ShowTrackDetailsForm />
          </Stack>
        </DialogContent>
        <DialogActions>
          <Button
            variant="contained"
            onClick={(e) => {
              e.preventDefault();
              onClose();
            }}
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    );
  }, [
    isSettingsDrawerOpen,
    handleSettingsDrawerClose,
    ViewModeForm,
    EditorViewModeForm,
    TracksForm,
    ShowTrackDetailsForm,
  ]);

  const TemplatesDialog = (props: { isOpen: boolean; onClose: () => void; close: () => void }) => {
    const { isOpen, onClose, close } = props;

    const [selectedTemplate, setSelectedTemplate] = useState<string | undefined>();
    const onSubmit = useCallback(() => {
      registry.current.clearMidiAnimations();
      const anims = createAnimationsFromTemplate(selectedTemplate, tracks);
      animations.current = anims;
      registry.current.addMidiAnimations(anims);
      setSaveEnabled(true);
      close();
    }, [selectedTemplate, close, tracks, setSaveEnabled, animations.current, registry]);

    return (
      <Dialog
        fullWidth
        open={isOpen}
        onClose={onClose}
        PaperProps={{
          component: "form",
          onSubmit,
        }}
      >
        <DialogTitle>Choose a template</DialogTitle>
        <DialogContent>
          <Box>
            <TemplatesSelect
              selectedTemplate={selectedTemplate}
              setSelectedTemplate={setSelectedTemplate}
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Button
            variant="contained"
            onClick={(e) => {
              e.preventDefault();
              onSubmit();
            }}
          >
            Use
          </Button>
          <Button
            variant="contained"
            onClick={(e) => {
              e.preventDefault();
              onClose();
            }}
          >
            Close
          </Button>
        </DialogActions>
      </Dialog>
    );
  };

  const templatesDialog = useMemo(
    () => (
      <TemplatesDialog
        isOpen={isTemplatesDialogOpen}
        onClose={handleTemplatesDialogClose}
        close={handleTemplatesDialogClose}
      />
    ),
    [isTemplatesDialogOpen, handleTemplatesDialogClose, tracks]
  );

  const recalculateSoloed = useCallback(() => {
    if (!registry.current) return;
    if (!animations.current) return;

    const soloed = animations.current.filter((anim) => registry.current.isSolo(anim));
    setSoloedAnimationIds(soloed.map((anim) => anim.id));
  }, [setSoloedAnimationIds]);

  const recalculateMuted = useCallback(() => {
    if (!registry.current) return;
    if (!animations.current) return;

    const muted = animations.current.filter((anim) => registry.current.isMute(anim));
    setMutedAnimationIds(muted.map((anim) => anim.id));
  }, [setMutedAnimationIds]);

  const recalculateVerbose = useCallback(() => {
    if (!registry.current) return;
    if (!animations.current) return;

    const verboses = animations.current.filter((anim) => anim.verbose);
    setVerboseAnimationIds(verboses.map((anim) => anim.id));
  }, [setVerboseAnimationIds]);

  const recalculateActiveEdits = useCallback(() => {
    if (!registry.current) return;
    if (!animations.current) return;

    console.log("recalculateActiveEdits");

    // This is terrible, but we only know one
    const ids = [];
    if (complexEditTrackIndex !== undefined) {
      const id = animations.current[complexEditTrackIndex].id;
      ids.push({ id, editSurface: "complexDrawer" });
    }
    setActiveEditSurfaceAnimationIds(ids);
  }, [setActiveEditSurfaceAnimationIds, complexEditTrackIndex]);

  useEffect(() => {
    recalculateMuted();
    recalculateSoloed();
  }, [recalculateSoloed, recalculateMuted]);

  // TODO: Change these to take ids and move to useEditor
  const muteAnimation = useCallback(
    (id: string, newVal: boolean) => {
      console.log("muteAnimation", id, newVal);
      if (muteAnimationById(id, newVal)) {
        recalculateMuted();
      }
    },
    [muteAnimationById, recalculateMuted]
  );

  const muteAnimations = useCallback(
    (newVal: boolean) => {
      console.log("muteAnimations", newVal);
      for (const anim of animations.current) {
        registry.current.mute(anim, newVal);
      }
      recalculateMuted();
    },
    [recalculateMuted]
  );

  const soloAnimation = useCallback(
    (id: string, newVal: boolean) => {
      console.log("soloAnimation", id, newVal);
      if (soloAnimationById(id, newVal)) {
        recalculateSoloed();
      }
    },
    [soloAnimationById, recalculateSoloed]
  );

  const soloAnimations = useCallback(
    (newVal: boolean) => {
      console.log("soloAnimations", newVal);
      for (const anim of animations.current) {
        registry.current.solo(anim, newVal);
      }
      recalculateSoloed();
    },
    [recalculateSoloed]
  );

  const verboseAnimation = useCallback(
    (id: string, newVal: boolean) => {
      console.log("verboseAnimation", id, newVal);
      if (verboseAnimationById(id, newVal)) {
        recalculateVerbose();
      }
    },
    [verboseAnimationById, recalculateVerbose]
  );

  const doneEdit = useCallback(() => {
    doneEditAnimation();
    setEditDialogOpen(false);
  }, [doneEditAnimation, setEditDialogOpen]);

  const audioName = useMemo(() => audioUrl?.name, [audioUrl?.name]);
  const midiName = useMemo(() => midiUrl?.name, [midiUrl?.name]);
  const midizName = useMemo(() => midizUrl?.name, [midizUrl?.name]);
  const anySoloed = useMemo(() => soloedAnimationIds.length > 0, [soloedAnimationIds]);
  const anyMuted = useMemo(() => mutedAnimationIds.length > 0, [mutedAnimationIds]);

  const wavesurferHeight = 87;

  const Metadata = () => {
    return (
      <MetadataPanel
        isPlaying={isPlaying}
        isLoaded={isLoaded}
        saveEnabled={saveEnabled}
        audioName={audioName}
        midiName={midiName}
        midizName={midizName}
        workspaceId={fileBundle.workspaceId}
        hasStarted={hasStarted}
        onlyShowActive={onlyShowActive}
        play={playAudio}
        stop={stopAudio}
        restart={restartAudio}
        rewind={rewindAudio}
        fastForward={fastFowrardAudio}
        upload={upload}
        saveAll={saveAll}
        download={download}
        setOnlyShowActive={setOnlyShowActive}
        openSettings={openSettings}
        openTemplates={openTemplates}
        anySoloed={anySoloed}
        anyMuted={anyMuted}
        soloAnimations={soloAnimations}
        muteAnimations={muteAnimations}
      />
    );
  };

  const trackListHeight = useMemo(() => hacks[viewMode].TRACKLIST_HEIGHT, [viewMode]);

  const Tracks = () => {
    return (
      <TrackList
        tracks={tracks}
        animations={animations}
        selectedTrack={selectedTrackIndex}
        setSelectedTrack={setSelectedTrackIndex}
        edit={openTrackToEdit}
        doneEdit={doneEdit}
        mute={muteAnimation}
        solo={soloAnimation}
        preview={previewAnimation}
        verbose={verboseAnimation}
        changeAnimationType={changeAnimationType}
        mutedAnimationIds={mutedAnimationIds}
        soloedAnimationIds={soloedAnimationIds}
        activeTrackNums={activeGroupedTrackNums}
        previewTrackNums={previewGroupedTrackNums}
        verboseAnimationIds={verboseAnimationIds}
        activeEditSurfaceAnimationIds={activeEditSurfaceAnimationIds}
        onlyShowActive={onlyShowActive}
        showTrackDetails={showTrackDetails}
        viewMode={viewMode}
        height={trackListHeight}
      />
    );
  };

  const timeAndCanvasHeight = useMemo(() => hacks[viewMode].TIMELINE_AND_CANVAS_HEIGHT, [viewMode]);

  const hideSampleCanvas = false;
  return (
    <>
      {templatesDialog}
      {flags.showComplexEditorDrawer ? (
        <Box sx={{ width: hideSampleCanvas ? 500 : complexEditDrawerWidth }}>
          <Drawer
            anchor={"right"}
            open={isComplexEditDrawerOpen}
            onClose={handleComplexEditDrawerClose}
          >
            <Box
              sx={{
                width: hideSampleCanvas ? 500 : complexEditDrawerWidth,
              }}
            >
              {animationToEdit ? (
                <AdvancedAnimationEditorPanel
                  trackName={trackName}
                  animationName={animationToEdit.name}
                  options={animationToEdit.options}
                  onClose={handleComplexEditDrawerClose}
                  onSave={handleComplexEditDrawerSave}
                  onReset={handleComplexEditDrawerReset}
                  onAnimationTypeChange={changeAnimationToEditType}
                  move={moveAnimationToEdit}
                  hideSampleCanvas={hideSampleCanvas}
                  closeOnSave={true}
                />
              ) : undefined}
            </Box>
          </Drawer>
        </Box>
      ) : undefined}
      {settingsDialog}
      <Grid container spacing={0}>
        <Grid item lg={4} xs={leftXS} sx={{ maxWidth: 400 }}>
          <Metadata />
          <Box sx={{ mr: 1 }}>
            <Tracks />
          </Box>
        </Grid>
        <Grid item lg={8} xs={rightXS}>
          {/* 
        XXX: 3.5 puts the top of the canvas exactly at the top of the tracks.
        */}
          <Box sx={{ height: wavesurferHeight, maxHeight: wavesurferHeight, mb: 3.5 }}>
            {audioUrl ? (
              <WavesurferAudioPlayer
                audioElm={audioElm}
                src={audioUrl.url}
                regions={regions}
                setRegions={setRegions}
                wavesurferRef={wavesurferRef}
                isLoaded={isLoaded}
                setIsLoaded={setIsLoaded}
                setIsPlaying={setIsPlaying}
                setDurationSecs={setDurationSecs}
                setActiveTrackNums={setActiveGroupedTrackNums}
                height={wavesurferHeight}
                tracks={tracks}
              />
            ) : undefined}
          </Box>
          <Stack spacing={0} sx={{ overflow: "hidden" }}>
            <Box sx={{ height: timeAndCanvasHeight, p: 0, m: 0 }}>
              <Box
                sx={{
                  // XXX
                  height: timeAndCanvasHeight + 100, // XXX: Why do we add 100?
                  width: "100%",
                  overflow: "always",
                  p: 0,
                  m: 0,
                }}
              >
                <main
                  style={{
                    margin: 0,
                    height: "100%",
                    width: "100%",
                    display: "flex",
                    flexWrap: "wrap",
                  }}
                >
                  <section
                    style={{
                      margin: 0,
                      display: "flex",
                      flexDirection: "column",
                      height: "85%",
                      width: "100%",
                    }}
                  >
                    <div
                      style={{
                        borderRadius: 0,
                        background: "rgba(10, 10, 10, 0.8)",
                        height: "100%",
                        overflow: "hidden",
                      }}
                    >
                      <div id="divElm" ref={divElm} style={{ overflow: "auto" }} />
                      <canvas id="canvasElm" ref={canvasElm} style={{ overflow: "auto" }} />
                    </div>
                  </section>
                </main>
              </Box>
            </Box>
          </Stack>
        </Grid>
      </Grid>
    </>
  );
};

export default Editor;
