import React from "react";
import { Checkbox, CheckboxProps, Tooltip } from "@mui/material";
import LetterIcon from "./LetterIcon";

interface InnerProps extends CheckboxProps {
  letter: string;
  checkedColor: string;
  disabled?: boolean;
  title: string;
}

const Inner = (props: InnerProps) => {
  const { title, letter, checkedColor, sx, ...rest } = props;
  return (
    <Checkbox
      size={"small"}
      sx={{ ...{ m: 0, p: 0, width: 25, height: 25 }, ...sx }}
      icon={<LetterIcon size={25} letter={letter} />}
      checkedIcon={
        <LetterIcon size={25} letter={letter} color={"#fff"} backgroundColor={checkedColor} />
      }
      inputProps={{ "aria-label": title }}
      {...rest}
    />
  );
};

type LetterCheckBoxProps = InnerProps;

const LetterCheckBox = (props: LetterCheckBoxProps) => {
  const { title, disabled } = props;
  if (disabled) {
    return <Inner {...props} />;
  }
  return (
    <Tooltip title={title}>
      <Inner {...props} title={undefined} />
    </Tooltip>
  );
};

export default LetterCheckBox;
