import { MidiAnimation } from "@/lib/types/main";
import { FileSource, IMidiAnimationJSON } from "../types/animations";
import { allAnimations } from "../../midi/base";
import { BaseAnimationOptions } from "../types/BaseAnimationOptions";
import { BaseUtil } from "./animations/base";
import { getDrawUtil } from "@/midi/animations/factory";

export const serializeAnimation = (midiAnimation: MidiAnimation): IMidiAnimationJSON => {
  const { id, name, criteria, options, } = midiAnimation;
  return {
    id,
    name,
    criteria,
    options,
  };
};

export const animationMetadata = allAnimations
  .map((a) => ({ name: a.name, title: a.title }))
  .sort((a, b) => a.title.localeCompare(b.title));

export const deserializeAnimation = (json: IMidiAnimationJSON): MidiAnimation => {
  const { id, name, criteria, options } = json;
  const anim = allAnimations.find((a) => a.name === name);
  if (!anim) {
    throw new Error(`Unknown animation type: ${name}`);
  }
  return anim.ctor(criteria, options, id);
};

export const createFileSource = (props: {
  name: string;
  uri?: string;
  content?: string;
  fileId?: number;
  workspaceId?: number;
}): FileSource => {
  return {
    name: props.name,
    uri: props.uri,
    content: props.content,
    fileId: props.fileId,
    workspaceId: props.workspaceId,
  };
};
