import React from "react";
import { Box, List, ListItem, ListItemAvatar, ListItemText } from "@mui/material";
import { User } from "./common";
import { ServerIcon, User1Icon, UserUnknownIcon } from "tdesign-icons-react";
import { Typography } from "@mui/material";
import { useMemo } from "react";

// https://coolors.co/6290c3-c2e7da-f1ffe7-1a1b41-baff29
const Colors = {
  background: "rgba(255,255,255,0.3)",
  host: "#baff29", //"rgba(255,255,0,0.9)",
  me: "#f1ffe7", //"rgba(255,255,255,0.6)",
  other: "#c2e7da", //"rgba(255,255,255,1)",
};

const Users = (props: {
  hostId: string;
  username: string;
  users: User[];
  showId?: boolean;
  allKnownUsers?: boolean;
  itemBackgroundColor?: string;
}) => {
  const { username, users, showId = false, allKnownUsers = false, itemBackgroundColor } = props;

  const sortedUsers = useMemo(() => {
    const isMe = (u: User) => u.name === username;
    const isHost = (u: User) => u.userType === "host";
    const me = users.find(isMe);
    const host = users.find((u) => !isMe(u) && isHost(u));
    const rest = users.filter((u) => !isMe(u) && !isHost(u));
    const sortedRest = rest.sort((a, b) => a.name.localeCompare(b.name));
    return [host, me, ...sortedRest].filter(Boolean);
  }, [users, username]);

  return (
    <Box
      sx={{
        borderRadius: 2,
      }}
    >
      <List>
        {sortedUsers.map((u: any, i) => {
          const { id, name, userType } = u;
          const { color, icon: Icon } =
            userType === "host"
              ? { color: Colors.host, icon: ServerIcon }
              : allKnownUsers || name === username
              ? { color: Colors.me, icon: User1Icon }
              : { color: Colors.other, icon: User1Icon };
          return (
            <ListItem
              key={id}
              sx={{
                mb: 2,
                backgroundColor: itemBackgroundColor ?? Colors.background,
                borderRadius: 2,
              }}
            >
              <ListItemAvatar>
                <Icon size={28} color={color} />
              </ListItemAvatar>
              <ListItemText
                primary={
                  <Typography color={color} variant="subtitle2">
                    {name}
                  </Typography>
                }
                secondary={
                  showId && (
                    <Typography color={color} variant="caption">
                      {id}
                    </Typography>
                  )
                }
              />
            </ListItem>
          );
        })}
      </List>
    </Box>
  );
};

export default Users;
