import React from "react";
import { useEffect, useState } from "react";

import { EditorViewMode, ViewMode, LeftPanelMode } from "@/lib/types/main";
import prefsLib from "@/midi/prefs";

const usePrefs = () => {
  const [viewMode, setViewMode] = useState<ViewMode>("widescreen");
  const [editorViewMode, setEditorViewMode] = useState<EditorViewMode>("tree");
  const [leftPanelMode, setLeftPanelMode] = useState<LeftPanelMode>("normal");
  const [showTrackDetails, setShowTrackDetails] = useState(false);

  useEffect(() => {
    const viewMode = prefsLib.get<ViewMode>("viewMode");
    setViewMode(viewMode);
    const editorViewMode = prefsLib.get<EditorViewMode>("editorViewMode");
    setEditorViewMode(editorViewMode);
    const leftPanelMode = prefsLib.get<LeftPanelMode>("leftPanelMode");
    setLeftPanelMode(leftPanelMode);
    const showTrackDetails = prefsLib.get<boolean>("showTrackDetails");
    setShowTrackDetails(showTrackDetails);
  }, [setViewMode, setEditorViewMode, setLeftPanelMode, setShowTrackDetails]);

  useEffect(() => prefsLib.put("viewMode", viewMode), [viewMode]);
  useEffect(() => prefsLib.put("editorViewMode", editorViewMode), [editorViewMode]);
  useEffect(() => prefsLib.put("leftPanelMode", leftPanelMode), [leftPanelMode]);
  useEffect(() => prefsLib.put("showTrackDetails", showTrackDetails), [showTrackDetails]);

  return {
    viewMode,
    setViewMode,
    editorViewMode,
    setEditorViewMode,
    leftPanelMode,
    setLeftPanelMode,
    showTrackDetails,
    setShowTrackDetails,
  };
};

export default usePrefs;
