import {
  MidiAnimation,
  IMidiAnimationDrawData,
  Note,
  NoteFilterCriteria,
  AnimationOptions,
} from "@/lib/types/main";
import { CubesOptions } from "@/lib/types/BaseAnimationOptions";
import { BaseAnimationOptions } from "../../lib/types/BaseAnimationOptions";
import { BaseMidiAnimation } from "@/lib/midi/animations/BaseMidiAnimation";
import { CubesUtil } from "./CubesUtil";

export interface Options extends BaseAnimationOptions, CubesOptions { }
const defaults = {
  side: { type: "size", default: 0 },
  scale: { type: "size", default: 1 },
  lineWidth: { type: "size", default: 5 },
  fillColor: { type: "color", default: "#fff" },
  lineColor: { type: "color", default: "#000" },
} as AnimationOptions<Options>;

class Cubes extends BaseMidiAnimation<Options> implements MidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, defaults, CubesUtil.NAME, CubesUtil.getInstance(), id);
  }
}

export default Cubes;
