import RefreshIcon from "@mui/icons-material/Refresh";
import PauseIcon from "@mui/icons-material/Pause";

const Wrapper = (props: { children: JSX.Element | JSX.Element[] | string }) => {
  const { children } = props;
  return (
    <Box
      sx={{
        color: "#000",
        backgroundColor: "rgba(250, 250, 250, 0.1)",
        maxWidth: 600,
        padding: 1,
        borderRadius: 2,
      }}
    >
      {children}
    </Box>
  );
};

const Debug = (props: { metadata: any; timeUpdate: any }) => {
  const { metadata, timeUpdate } = props;
  const Code = (props: { value: any }) => {
    const { value } = props;
    return (
      <pre
        style={{
          fontSize: "0.8em",
          fontFamily: "monospace",
          fontWeight: "bold",
        }}
      >
        {JSON.stringify(value, null, 2)}
      </pre>
    );
  };

  const ms = (n: number | undefined) => (n ? `${n.toFixed(1)}ms` : "N/A");

  return (
    <Box
      sx={{
        color: "rgba(255,255,255,0.8)",
        background: canvasBackgroundColor,
        width: "100%",
        height: "100%",
        overflow: "auto",
        borderRadius: 2,
        p: 1,
      }}
    >
      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle2" color="#fff">
          Metadata
        </Typography>
        <Wrapper>
          <Code value={metadata} />
        </Wrapper>
      </Box>
      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle2" color="#fff">
          Time update
        </Typography>
        <Wrapper>
          <Code value={timeUpdate} />
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Real</TableCell>
                <TableCell>Pred</TableCell>
                <TableCell>Skew</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              <TableRow>
                <TableCell>
                  {formatDuration(timeUpdate.currentTimeMillis ?? 0, { ms: true })}
                </TableCell>
                <TableCell>
                  {formatDuration(timeUpdate.predictedCurrentTimeMillis ?? 0, { ms: true })}
                </TableCell>
                <TableCell>{ms(timeUpdate.skew)}</TableCell>
              </TableRow>
            </TableBody>
          </Table>
        </Wrapper>
      </Box>
      <Box sx={{ mb: 2 }}>
        <Typography variant="subtitle2" color="#fff">
          WS URL
        </Typography>
        <Wrapper>
          <Code value={WS_URL} />
        </Wrapper>
      </Box>
    </Box>
  );
};

const CurrentTime = (props: { currentTime: number; height?: number | string }) => {
  const { currentTime, height } = props;
  const playingTime = useMemo(() => {
    const mins = Math.floor((currentTime ?? 0) / 60);
    const secs = Math.floor((currentTime ?? 0) % 60);
    return `${String(mins).padStart(2, "0")}:${String(secs).padStart(2, "0")}`;
  }, [currentTime]);

  return (
    <Typography variant="subtitle2" color="#fff">
      {playingTime}
    </Typography>
  );
};

const Guest = (props: {
  currentTime: number;
  username: string;
  users: User[];
  metadata: any;
  timeUpdate: any;
  divElm: Ref<HTMLDivElement>;
  hostId: string;
  isPlaying: boolean;
  play: () => void;
  pause: () => void;
  restart: () => void;
}) => {
  const {
    currentTime,
    username,
    users,
    metadata,
    timeUpdate,
    divElm,
    hostId,
    isPlaying,
    play,
    pause,
    restart,
  } = props;

  console.log("isPlaying", timeUpdate, isPlaying);

  const [searchParams] = useSearchParams();

  const [showDebugButtons, setShowDebugButtons] = useState(false);
  const [showDebug, setShowDebug] = useState(false);
  const [showUsers, setShowUsers] = useState(true);
  const [showAudioControls, setShowAudioControls] = useState(true);

  useEffect(() => {
    if (!searchParams) return;
    if (searchParams.has("debug")) {
      setShowDebugButtons(true);
      setShowUsers(false);
    }
    if (searchParams.has("noAudioControls")) {
      setShowAudioControls(false);
    }
    if (searchParams.has("audioControls")) {
      setShowAudioControls(true);
    }
  }, [searchParams, setShowDebugButtons, setShowUsers, setShowAudioControls]);

  const positionStyle = { position: "absolute", bottom: 0, left: 0 };
  const top = 10000;

  return (
    <>
      <Box
        sx={{
          position: "absolute",
          top: 0,
          right: 0,
          zIndex: top,
          p: 2,
        }}
      >
        {!showAudioControls ? undefined : (
          <Box
            sx={{
              width: "100%",
              mb: 2,
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
            }}
          >
            <Box
              sx={{
                height: "100%",
                display: "flex",
                flexDirection: "column",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <CurrentTime currentTime={currentTime} />
            </Box>
            <IconButton>
              <RefreshIcon htmlColor="#fff" onClick={restart} />
            </IconButton>
            <IconButton>
              {isPlaying ? (
                <PauseIcon htmlColor="#fff" onClick={pause} />
              ) : (
                <PlayArrowIcon htmlColor="#fff" onClick={play} />
              )}
            </IconButton>
          </Box>
        )}
      </Box>
      <Box
        sx={{
          ...positionStyle,
          display: "flex",
          justifyContent: "right",
          width: "100%",
          zIndex: top,
          // m: 2,
          mb: 1,
        }}
      >
        <Box
          sx={{
            pr: 3,
            display: "flex",
            justifyContent: "right",
            flexDirection: "column",
          }}
        >
          {!showUsers ? undefined : (
            <Box sx={{ width: "100%" }}>
              <Users hostId={hostId} username={username} users={users} />
            </Box>
          )}
          {!showDebug ? undefined : (
            <Box sx={{ width: "100%", mb: 2 }}>
              <Debug metadata={metadata} timeUpdate={timeUpdate} />
            </Box>
          )}
          {!showDebugButtons ? undefined : (
            <Stack
              direction="row"
              spacing={2}
              sx={{
                display: "flex",
                justifyContent: "right",
                width: "100%",
              }}
            >
              <Fab size="small" aria-label="share">
                <DebugIcon onClick={() => setShowDebug(!showDebug)} />
              </Fab>
              <Fab size="small" aria-label="debug">
                <PeopleIcon onClick={() => setShowUsers(!showUsers)} />
              </Fab>
            </Stack>
          )}
        </Box>
      </Box>
      <Box
        sx={{
          ...positionStyle,
          zIndex: top,
          m: 2,
        }}
      >
        <Box sx={{ pr: 3, maxHeight: 80, maxWidth: 80 }}>
          {/* 80 makes this the same height as one users row on mobile */}
          <QrCode hostId={hostId} />
        </Box>
      </Box>
      <Box
        sx={{
          ...positionStyle,
          zIndex: top - 1,
          width: "100%",
          height: "100%",
        }}
      >
        <Canvas divElm={divElm} />
      </Box>
    </>
  );
};

const Page = () => {
  const [searchParams, setSearchParams] = useSearchParams();

  const [hostId, setHostId] = useState<string | undefined>();

  useEffect(() => {
    if (!searchParams) return;
    const hostId = searchParams.get("hostId");
    if (hostId) {
      setHostId(hostId);
    }
  }, [searchParams, setHostId]);

  return <>{hostId ? <PageWithValues hostId={hostId} /> : <>bad monkey</>}</>;
};

const PageWithValues = (props: { hostId: string }) => {
  const { hostId } = props;

  const { randomName } = useRandomName();
  const [username, setUsername] = useState(randomName());
  const [userId, setUserId] = useState(uuidv4());
  const [metadata, setMetadata] = useState<any>({});
  const [timeUpdate, setTimeUpdate] = useState<any>({});
  const [currentTime, setCurrentTime] = useState<number | undefined>();

  const { lastJsonMessage, readyState, sendJsonMessage } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log("[Listen] WebSocket connection established.");
    },
    share: true,
    filter: () => true,
    retryOnError: true,
    shouldReconnect: () => true,
  });

  const { users } = useUsers();
  const { divElm, wrapper, animationsRef, notesSource } = useCanvas();

  useEffect(() => {
    if (!wrapper) return;

    sendJsonMessage({
      type: "sendToUsers",
      data: {
        toUserIds: [hostId],
        msg: {
          type: "broadcastAnimationsAndMidiData",
          data: {},
        },
      },
    });
  }, [wrapper]);

  useEffect(() => {
    if (!lastJsonMessage) return;

    const { type, data } = lastJsonMessage as any;

    switch (type) {
      case "updateAnimationsAndMidiData":
        const { animationJSONs, midi, metadata = {} } = data as AnimationsAndMidiData;
        const animations = animationJSONs.map((json) => deserializeAnimation(json));
        animationsRef.current = animations;
        const source = midi as Midi;
        const getNotes = (props: GetNotesProps) => {
          const { startMillis, durationMillis, criteria } = props;
          const notes = getNotesFromSource(source, { startMillis, durationMillis, criteria });
          return notes;
        };
        notesSource.current = { getNotes, source };
        setMetadata({
          ...metadata,
          lastUpdated: new Date().toISOString(),
        });
        break;

      case "timeUpdate":
        const { reason, timeSecs, isPlaying } = data as TimeUpdateData;
        const timeUpdate = wrapper.setCurrentTimeMillis(timeSecs * 1000, isPlaying, reason);
        setTimeUpdate({
          ...timeUpdate,
          lastUpdated: new Date().toISOString(),
        });
        setCurrentTime(timeUpdate.currentTimeMillis / 1000);
        break;
    }
  }, [lastJsonMessage]);

  useEffect(() => {
    if (readyState === ReadyState.OPEN) {
      sendJsonMessage({
        type: "newUser",
        data: {
          username,
          userId,
          hostUserId: hostId,
          userType: "guest",
        },
      });
    }
  }, [readyState]);

  const play = useCallback(() => {
    console.log("play");
    sendJsonMessage({
      type: "sendToUsers",
      data: {
        toUserIds: [hostId],
        msg: {
          type: "play",
          data: {},
        },
      },
    });
  }, [sendJsonMessage]);
  const pause = useCallback(() => {
    console.log("pause");
    sendJsonMessage({
      type: "sendToUsers",
      data: {
        toUserIds: [hostId],
        msg: {
          type: "pause",
          data: {},
        },
      },
    });
  }, [sendJsonMessage]);

  const restart = useCallback(() => {
    console.log("restart");
    sendJsonMessage({
      type: "sendToUsers",
      data: {
        toUserIds: [hostId],
        msg: {
          type: "seekTo",
          data: {
            position: 0,
          },
        },
      },
    });
  }, [sendJsonMessage]);

  return (
    <Guest
      currentTime={currentTime}
      username={username}
      users={users}
      metadata={metadata}
      timeUpdate={timeUpdate}
      isPlaying={timeUpdate.isPlaying}
      divElm={divElm}
      hostId={hostId}
      play={play}
      pause={pause}
      restart={restart}
    />
  );
};

const useCanvas = () => {

  const [wrapperToReturn, setWrapperToReturn] = useState<TickManager>();

  const notesSource = useRef<MidiNotesSource>();
  const animationsRef = useRef<MidiAnimation[]>();

  const { two, divElm } = useTwo<HTMLDivElement>();
  const { wrapper } = useLooper();

  // Draw something to show we didn't fuck up the interaction with two
  useEffect(() => {
    // @ts-ignore
    two.current.clear();
    // @ts-ignore
    const circ = two.current.makeCircle(72, 100, 50);
    circ.fill = "#FF8000";
    circ.stroke = "orangered";
    // @ts-ignore
    two.current.update();
  }, [two]);

  useEffect(() => {
    if (!wrapper) return;

    const tick = (props: { currentTimeMillis: number }) => {
      const { currentTimeMillis } = props;
      if (!notesSource.current) return;
      if (!animationsRef.current) return;
      if (!divElm) return;

      const { width, height } = divElm.current.getBoundingClientRect();

      // @ts-ignore
      two.current.clear();
      for (const anim of animationsRef.current) {
        const notes = notesSource.current.getNotes({
          startMillis: currentTimeMillis,
          durationMillis: tickDurationMillis,
          criteria: anim.criteria,
        });
        if (notes.length) {
          anim.draw(notes, { width, height, canvas: null, canvasElement: null, two: two.current });
        }
      }
      // @ts-ignore
      two.current.update();
    };
    wrapper.setCallback({ tick });
    setWrapperToReturn(wrapper);
  }, [wrapper]);

  return {
    divElm,
    wrapper: wrapperToReturn,
    animationsRef,
    notesSource,
  };
};

const Canvas = (props: { divElm: Ref<HTMLDivElement> }) => {
  const { divElm } = props;

  return (
    <div
      style={{
        backgroundColor: canvasBackgroundColor,
        borderRadius: 0,
        height: "100%",
        width: "100%",
        overflow: "hidden",
      }}
    >
      <div id="divElm" ref={divElm} style={{ overflow: "auto" }} />
    </div>
  );
};

export default Page;
