import React from "react";
import { FileBundle } from "@/lib/types/load";
import { Box, Dialog, DialogContent } from "@mui/material";
import { useEffect, useMemo } from "react";
import { useSearchParams } from "react-router-dom";
import Editor from "@/components/Editor";
import FileUploadPanel from "@/components/FileUploadPanel";
import useFileUploader from "@/hooks/useFileUploader";

const Edit = () => {
  const [searchParams] = useSearchParams();

  const {
    output: { fileBundle, searchedForSavedFiles, tracks: flatTracks, reset: resetFileUploader },
    input: { setAutoloadOption, setTestFilesOption },
    fileUploaderProps,
  } = useFileUploader();

  useEffect(() => {
    if (!fileBundle) return;
  }, [fileBundle]);

  useEffect(() => {
    if (!searchParams) return;

    if (searchParams.has("autoload")) {
      setAutoloadOption(true);
      return;
    }
  }, [setTestFilesOption, setAutoloadOption, searchParams]);

  const isFileUploadDialogOpen = useMemo(() => {
    if (!searchedForSavedFiles) return true;
    if (!fileBundle) {
      return true;
    }
    return false;
  }, [searchedForSavedFiles, fileBundle]);

  const fileUploadDialog = useMemo(
    () => (
      <Dialog maxWidth={"lg"} open={isFileUploadDialogOpen}>
        <DialogContent>
          <Box sx={{ width: 800, maxWidth: 800, minWidth: 800 }}>
            <FileUploadPanel fileUploaderProps={fileUploaderProps} reset={resetFileUploader} />
          </Box>
        </DialogContent>
      </Dialog>
    ),
    [isFileUploadDialogOpen, fileUploaderProps]
  );

  const editor = useMemo(
    () =>
      fileBundle ? (
        <Editor fileBundle={fileBundle as Required<FileBundle>} flatTracks={flatTracks} />
      ) : undefined,
    [fileBundle]
  );

  return (
    <>
      {editor}
      {fileUploadDialog}
    </>
  );
};

export default Edit;
