import React from "react";
﻿import { useRef, useState, useEffect, useCallback, useMemo } from "react";
import useWebSocket, { ReadyState } from "react-use-websocket";
import useAudio from "@/hooks/useAudio";
import { Midi, MidiAnimation } from "@/lib/types/main";
import { createMidi } from "@/midi/impl";
import { Box, Button, Grid, Typography } from "@mui/material";
import { serializeAnimation } from "@/lib/midi/lib";
import { WS_URL } from "@/lib/websocket";
import { canvasBackgroundColor, User } from "@/routes/share/common";
import QrCode from "@/routes/share/QrCode";
import { v4 as uuid } from "uuid";
import useUsers from "@/routes/share/useUsers";
import Users from "@/routes/share/Users";
import { IMidiAnimationJSON } from "@/lib/types/animations";
import { loadAnimations } from "@/lib/load";
import useWorkspaceFromSearchParams from "@/hooks/useWorkspaceFromSearchParams";
import "react-h5-audio-player/lib/styles.css";
import useMessages from "@/hooks/useMessages";
import { useSearchParams } from "react-router-dom";
import useRandomName from "@/hooks/useRandomName";
import WorkspaceChooser from "@/components/WorkspaceChooser";
import useFiles from "@/hooks/useFiles";
import { ResolvedWorkspace } from "@/lib/types/files";
import { Setter } from "@/lib/types/util";
import { useNavigate } from "react-router-dom";

const Page = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  const [hostName, setHostName] = useState<string | undefined>();
  const [hostId, setHostId] = useState<string | undefined>();
  const { randomName } = useRandomName();

  const navigate = useNavigate();

  useEffect(() => {
    const name = searchParams.get("hostName");
    if (name) {
      setHostName(name);
      return;
    }
    setSearchParams((prevParams) => {
      prevParams.set("hostName", randomName());
      return prevParams;
    });
  }, [searchParams, hostName, setSearchParams]);

  useEffect(() => {
    const id = searchParams.get("hostId");
    if (id) {
      setHostId(id);
      return;
    }
    setSearchParams((prevParams) => {
      prevParams.set("hostId", uuid());
      return prevParams;
    });
  }, [searchParams, hostId, setSearchParams]);

  useEffect(() => {
    const ws = searchParams.get("ws");
    if (ws) return;
    setSearchParams((prevParams) => {
      prevParams.set("ws", "demo");
      return prevParams;
    });
  }, [searchParams, setSearchParams]);

  const fs = useFiles();

  const [workspaces, setWorkspaces] = useState<ResolvedWorkspace[]>([]);

  useEffect(() => {
    const getWorkspaces = async () => {
      const wss = await fs.getAllResolveWorkspaces();
      setWorkspaces(wss);
    };
    getWorkspaces();
  }, [setWorkspaces]);

  const { showMessage, showError } = useMessages();
  const { users } = useUsers();
  const urlsToUse = useUrlsToUse();
  const { audioElm, animationJSONs, midi, sendTimeUpdate } = useAnimationsAndMidi({
    hostId,
    files: urlsToUse,
  });

  const { lastJsonMessage, readyState, sendJsonMessage } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log("[Serve] WebSocket connection established.");
    },
    share: true,
    filter: () => true,
    retryOnError: true,
    shouldReconnect: () => true,
  });

  useEffect(() => {
    console.log("sending new user", ReadyState.OPEN, readyState, hostName, hostId);

    if (readyState === ReadyState.OPEN) {
      sendJsonMessage({
        type: "newUser",
        data: {
          username: hostName,
          userId: hostId,
          hostUserId: hostId,
          userType: "host",
        },
      });
    }
  }, [readyState]);

  useEffect(() => {
    if (!urlsToUse) return;

    if (urlsToUse.error) {
      showError(urlsToUse.error);
    }
    if (urlsToUse.description) {
      showMessage(urlsToUse.description);
    }
  }, [urlsToUse]);

  const sendAnimationsAndMidiData = useCallback(async () => {
    if (!animationJSONs) return;
    if (!midi) return;

    const msgs = divideAnimations(users, animationJSONs, midi);
    msgs.forEach(({ userId, msg }) => {
      sendJsonMessage({
        type: "sendToUsers",
        data: {
          toUserIds: [userId],
          msg,
        },
      });
    });
  }, [users, animationJSONs, midi]);

  useEffect(() => {
    if (lastJsonMessage) {
      const { type, data } = lastJsonMessage as any;
      if (["broadcastAnimationsAndMidiData", "groupUpdate"].includes(type)) {
        sendAnimationsAndMidiData();
        sendTimeUpdate("broadcast");
        return;
      }
      if (type === "play") {
        audioElm.current?.play();
        return;
      }
      if (type === "pause") {
        audioElm.current?.pause();
        return;
      }
      if (type === "seekTo") {
        const { position } = data;
        // TODO: Seek
        audioElm.current?.fastSeek && audioElm.current?.fastSeek(Number(position));
        return;
      }
    }
  }, [lastJsonMessage, sendAnimationsAndMidiData]);

  useEffect(() => {
    if (!animationJSONs?.length) return;
    sendAnimationsAndMidiData();
  }, [animationJSONs, sendAnimationsAndMidiData]);

  const [chosen, setChosen] = useState<Urls | undefined>();
  useEffect(() => {
    const go = async () => {
      if (!urlsToUse) return;
      if (urlsToUse.workspaceId) {
        const wsId = urlsToUse.workspaceId;
        const ws = await fs.getWorkspaceById(wsId);
        const resWs = await fs.getWorkspace(ws);
        setInitialWorkspace({ workspace: resWs, found: true });
      } else {
        setInitialWorkspace({ workspace: undefined, found: false });
      }
      setChosen(urlsToUse);
    };
    go();
  }, [urlsToUse]);

  const [initialWorkspace, setInitialWorkspace] = useState<
    { workspace: ResolvedWorkspace; found: boolean } | undefined
  >();

  const choseWorkspace = useCallback(
    (workspace: ResolvedWorkspace) => {
      setChosen({
        audio: { url: workspace.audio.url, name: workspace.audio.name },
        midi: { url: workspace.midi.url, name: workspace.midi.name },
        midiz: { url: workspace.midiz.url, name: workspace.midiz.name },
        error: undefined,
        description: "selected",
        workspaceId: workspace.id,
        createdISO: workspace.createdISO,
      });
    },
    [setChosen]
  );

  const onEditWorkspace = useCallback((workspace: ResolvedWorkspace) => {
    const { id } = workspace;
    const url = `/edit?ws=${id}&autoload`;
    navigate(url);
  }, []);

  return (
    <>
      {hostId && hostName && chosen && initialWorkspace ? (
        <PageWithValues
          hostId={hostId}
          hostName={hostName}
          workspaces={workspaces}
          initialWorkspace={initialWorkspace.workspace}
          choseWorkspace={choseWorkspace}
          setWorkspaceToEdit={onEditWorkspace}
          chosen={chosen}
          audioElm={audioElm}
          users={users}
        />
      ) : (
        <>uh</>
      )}
    </>
  );
};

const PageWithValues = (props: {
  hostId: string;
  hostName: string;
  workspaces: ResolvedWorkspace[];
  initialWorkspace: ResolvedWorkspace | null;
  choseWorkspace: Setter<ResolvedWorkspace>;
  setWorkspaceToEdit: (workspace: ResolvedWorkspace) => void;
  chosen: Urls;
  audioElm: React.RefObject<HTMLAudioElement>;
  users: User[];
}) => {
  const {
    hostId,
    hostName,
    workspaces,
    initialWorkspace,
    choseWorkspace,
    setWorkspaceToEdit,
    chosen,
    audioElm,
    users,
  } = props;

  if (!hostId) {
    throw new Error("No hostId");
  }
  if (!hostName) {
    throw new Error("No hostName");
  }
  if (!chosen) {
    throw new Error("No chosen");
  }

  const [selectedWorkspace, setSelectedWorkspace] = useState<ResolvedWorkspace | null>(
    initialWorkspace
  );

  const haveSelectedWorkspace = useMemo(() => !!selectedWorkspace, [selectedWorkspace]);
  const haveDifferentWorkspace = useMemo(
    () => selectedWorkspace?.id !== initialWorkspace?.id,
    [selectedWorkspace?.id, initialWorkspace?.id]
  );

  const choseSelectedWorkspace = useCallback(() => {
    choseWorkspace(selectedWorkspace);
  }, [choseWorkspace, selectedWorkspace]);

  return (
    <>
      <Box
        sx={{
          height: "100%",
          width: "100%",
          overflow: "hidden",
        }}
      >
        <Grid container spacing={0}>
          <Grid item xs={12}>
            <Box sx={{ p: 2, pb: 0, width: "100%", display: "flex", flexDirection: "column" }}>
              {chosen?.audio ? (
                <audio
                  controls
                  src={chosen?.audio.url}
                  ref={audioElm}
                  style={{
                    width: "100%",
                    margin: "auto",
                  }}
                ></audio>
              ) : undefined}
              <Box sx={{ p: 2, pt: 0, width: "100%", display: "flex", flexDirection: "row" }}>
                <Typography variant="subtitle2" sx={{ fontWeight: "bold" }}>
                  Workspace #{chosen.workspaceId} @ {chosen.createdISO}:
                </Typography>
                <Typography variant="body2" sx={{ ml: 1 }}>
                  {chosen.audio.name} // {chosen.midi.name} // {chosen.midiz.name}
                </Typography>
                <Typography variant="body2" sx={{ fontStyle: "italic" }}>
                  {chosen.createdISO}:
                </Typography>
              </Box>
            </Box>
          </Grid>
          <Grid item xs={12} lg={4}>
            <Box sx={{ p: 2, pr: 1, pt: 1, width: "100%" }}>
              <QrCode hostName={hostName} hostId={hostId} />
            </Box>
          </Grid>
          <Grid item xs={12} lg={8}>
            <Box sx={{ px: 2, pl: 1, width: "100%" }}>
              <div style={{ height: "100%", width: "100%", overflow: "auto" }}>
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "row",
                    alignContent: "center",
                  }}
                >
                  {workspaces.length === 0 ? undefined : (
                    <>
                      <Box sx={{ flexGrow: 1, m: 0, p: 0, mr: 1 }}>
                        {/* <Box> */}
                        <WorkspaceChooser
                          workspaces={workspaces}
                          selected={selectedWorkspace}
                          setSelected={setSelectedWorkspace}
                        />
                      </Box>
                      <Box sx={{ height: "100%" }}>
                        <Button
                          sx={{ mr: 1 }}
                          variant="contained"
                          onClick={() => {
                            if (selectedWorkspace) choseSelectedWorkspace();
                          }}
                          disabled={!(haveSelectedWorkspace && haveDifferentWorkspace)}
                        >
                          Use
                        </Button>
                        <Button
                          variant="contained"
                          onClick={() => {
                            if (!selectedWorkspace) return;
                            setWorkspaceToEdit(selectedWorkspace);
                          }}
                          disabled={!haveSelectedWorkspace}
                        >
                          Edit
                        </Button>
                      </Box>
                    </>
                  )}
                </Box>
              </div>
              <Box>
                {!users.length ? undefined : (
                  <Users
                    username={hostName}
                    hostId={hostId}
                    users={users}
                    allKnownUsers
                    itemBackgroundColor={canvasBackgroundColor}
                  />
                )}
              </Box>
            </Box>
          </Grid>
        </Grid>
      </Box>
    </>
  );
};

const divideAnimations = (
  users: { userType: string; id: string }[],
  animationJSONs: IMidiAnimationJSON[],
  midi: Midi
) => {
  const userIds = users.filter((u) => u.userType !== "host").map((u) => u.id);
  const numUsers = userIds.length;
  const minNumEach = Math.floor(animationJSONs.length / numUsers);
  const maxNumEach = Math.ceil(animationJSONs.length / numUsers);
  const total = animationJSONs.length;
  const type = "updateAnimationsAndMidiData";
  const msgs = userIds
    .map((userId, i) => {
      const start = i * minNumEach;
      const end = i == userIds.length - 1 ? total : start + minNumEach;
      const filtered = animationJSONs.slice(start, end);
      const metadata = {
        num: i,
        total,
        minNumEach,
        maxNumEach,
        numUsers,
        start,
        end,
        numFiltered: filtered.length,
      };
      const msg = {
        type,
        data: {
          midi,
          animationJSONs: filtered,
          metadata,
        },
      };
      return { userId, msg };
    })
    .filter(Boolean) as { userId: string; msg: any }[];
  return msgs;
};
interface UrlAndName {
  url: string;
  name: string;
}
interface Urls {
  audio: UrlAndName;
  midi: UrlAndName;
  midiz: UrlAndName;
  error: string | undefined;
  description: string | undefined;
  workspaceId: number;
  createdISO: string;
}
const useUrlsToUse = () => {
  const [urls, setUrls] = useState<Urls | undefined>();

  const defaultAudioUrl = { url: `${process.env.PUBLIC_URL}/assets/nogo.mp3`, name: "nogo.mp3" };
  const defaultMidiUrl = { url: `${process.env.PUBLIC_URL}/assets/nogo.mid`, name: "nogo.mid" };
  const defaultMidizUrl = {
    url: `${process.env.PUBLIC_URL}/assets/nogo.mdz.json`,
    name: "nogo.mdz.json",
  };

  const wsFiles = useWorkspaceFromSearchParams();

  useEffect(() => {
    if (!wsFiles) return;
    if (wsFiles?.found === undefined) return;

    if (!wsFiles.found) {
      setUrls({
        audio: defaultAudioUrl,
        midi: defaultMidiUrl,
        midiz: defaultMidizUrl,
        error: wsFiles.error,
        description: wsFiles.description,
        workspaceId: 0,
        createdISO: new Date().toISOString(),
      });
      return;
    }

    const {
      urls: { audioUrl, midiUrl, midizUrl, workspaceId, createdISO },
      error,
      description,
    } = wsFiles;
    setUrls({
      audio: audioUrl,
      midi: midiUrl,
      midiz: midizUrl,
      error,
      description,
      workspaceId,
      createdISO,
    });
  }, [wsFiles?.found]);

  return urls;
};

const useAnimationsAndMidi = (props: { hostId: string; files: Urls | undefined }) => {
  const { files: urls, hostId } = props;

  const { sendJsonMessage } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log("[Audio] WebSocket connection established.");
    },
    share: true,
    filter: () => false,
    retryOnError: true,
    shouldReconnect: () => true,
  });

  const [midi, setMidi] = useState<Midi | undefined>();
  const { animations } = useAnimations({ urls });

  useEffect(() => {
    if (!urls) return;
    const init = async () => {
      const midiUrl = urls.midi.url;
      const midiBuffer = midiUrl.startsWith("blob:")
        ? await (await (await fetch(midiUrl)).blob()).arrayBuffer()
        : await (await fetch(midiUrl)).arrayBuffer();
      const midi = createMidi(midiBuffer, { noOriginal: true });
      setMidi(midi);
    };
    init();
  }, [urls]);

  class Syncer {
    // TODO: Do everything in seconds
    private _lastSyncMillis: number = 0;
    private _thresholdMillis: number;
    private _id: string = uuid();
    private _hostId: string;

    constructor(hostId: string, thresholdMillis?: number) {
      this._thresholdMillis = thresholdMillis || 1000;
      this._hostId = hostId;
      console.log("new syncer", this._id);
    }

    public get intervalMillis() {
      return this._thresholdMillis;
    }

    get hostId() {
      return this._hostId;
    }

    public tick(currenTimeMillis: number) {
      if (Math.abs(currenTimeMillis - this._lastSyncMillis) > this._thresholdMillis) {
        console.log("yes", this._id, this._lastSyncMillis, currenTimeMillis, this._thresholdMillis);
        this._lastSyncMillis = currenTimeMillis;
        this.sendTimeUpdate("sync");
        return true;
      }
      return false;
    }

    public onPlayChanged(reason: string) {
      this.sendTimeUpdate(reason);
    }

    sendTimeUpdate(reason: string) {
      const data = {
        reason,
        timeSecs: audioElm?.current?.currentTime,
        isPlaying: !audioElm?.current?.paused,
        intervalSecs: this.intervalMillis,
      };
      sendJsonMessage({
        type: "sendToGroup",
        data: {
          groupId: this._hostId,
          msg: {
            type: "timeUpdate",
            data,
          },
        },
      });
    }
  }

  const syncer = useRef<Syncer>();
  const tick = (audioProps: {
    audioBufferData: Uint8Array;
    currentTimeMillis: number;
    durationMillis: number;
  }) => {
    syncer.current?.tick(audioProps.currentTimeMillis);
  };

  const onPlayChanged = (reason: string) => {
    syncer.current?.onPlayChanged(reason);
  };

  const callback = {
    tick,
    onPlayStopped: onPlayChanged,
    onPlayStarted: onPlayChanged,
  };
  const { audioElm, wrapper } = useAudio({ callback, ignoreCreateErrors: true });

  useEffect(() => {
    if (!animations) return;
    if (!audioElm.current) return;

    console.log("setting the syncer", audioElm.current);
    syncer.current = new Syncer(hostId);
    syncer.current.tick(audioElm.current.currentTime * 1000);
  }, [animations]);

  const [animationJSONs, setAnimationJSONs] = useState<IMidiAnimationJSON[] | undefined>();
  useEffect(() => {
    if (!animations) return;
    const animationJSONs = animations.map((a) => serializeAnimation(a));
    setAnimationJSONs(animationJSONs);
  }, [animations]);

  const sendTimeUpdate = (reason: string) => {
    syncer.current?.sendTimeUpdate(reason);
  };

  return { audioElm, animationJSONs, midi, sendTimeUpdate };
};

const useAnimations = (props: { urls: Urls | undefined }) => {
  const { urls } = props;

  const [animations, setAnimations] = useState<MidiAnimation[] | undefined>();
  useEffect(() => {
    if (!urls) return;
    const init = async () => {
      const midizUrl = urls.midiz.url;
      const animations = await loadAnimations(midizUrl);
      setAnimations(animations);
    };
    init();
  }, [urls]);

  return { animations };
};

export default Page;
