import React from "react";
import {
  List,
  ListItemIcon,
  ListItemButton,
  ListItem,
  ListItemText,
  Typography,
  Box,
} from "@mui/material";
import PlayCircleOutlineIcon from "@mui/icons-material/PlayCircleOutline";
import EditIcon from "@mui/icons-material/Edit";
import HeadphonesIcon from "@mui/icons-material/Headphones";
import CellTowerIcon from "@mui/icons-material/CellTower";
import HomeIcon from "@mui/icons-material/Home";
import PrivacyTipIcon from "@mui/icons-material/PrivacyTip";
import SimplePageWrapper from "@/components/SimplePageWrapper";
const Home = () => {
  const nav = [
    {
      path: "/edit",
      text: "Edit",
      icon: EditIcon,
      description: "Create animations from a MIDI file and accompanying audio",
    },
    {
      path: "/host",
      text: "Host",
      icon: CellTowerIcon,
      description: "Broadcast these across multiple devices on the link below",
    },
    {
      path: "/guest",
      text: "Guest",
      icon: HeadphonesIcon,
      description: "See link above",
    },
    {
      path: "/privacy",
      text: "Privacy",
      icon: PrivacyTipIcon,
      description: "Privacy notice",
    },
    // {
    //   path: "/play",
    //   text: "Play",
    //   icon: PlayCircleOutlineIcon,
    // },
    // {
    //   path: "/home",
    //   text: "Home",
    //   icon: HomeIcon,
    // },
  ];
  return (
    <SimplePageWrapper>
      <Box
        sx={{
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
          justifyContent: "top",
        }}
      >
        <List>
          {nav.map((item, index) => {
            const { icon: Icon, text, path, description } = item;
            return (
              <ListItem key={`nav-${index}`}>
                <ListItemButton
                  onClick={() => {
                    const url = `${process.env.PUBLIC_URL}/#${path}`;
                    window.location.replace(url);
                  }}
                >
                  <ListItemIcon>
                    <Icon />
                  </ListItemIcon>
                  <ListItemText primary={text} secondary={description} />
                </ListItemButton>
              </ListItem>
            );
          })}
        </List>
        <Typography variant="caption" color="info">
          <span style={{ color: "red" }}>*</span> If you're reading this, I declare victory. Don't
          expect anything else to work.
        </Typography>
      </Box>
    </SimplePageWrapper>
  );
};

export default Home;
