import React from "react";
import useFiles from "@/hooks/useFiles";
import { Track } from "@/lib/types/editor";
import { FileBundle, NamedUrl } from "@/lib/types/load";
import dayjs from "dayjs";
import pluralize from "pluralize";
import { useCallback, useEffect, useState } from "react";
import { FileType } from "@/lib/types/files";
import { loadFiles } from "@/lib/load";
import useWorkspaceFromSearchParams from "./useWorkspaceFromSearchParams";
import useLatestWorkspace from "./useLatestWorkspace";
import useMessages from "./useMessages";

const useFileUploader = () => {
  const { showMessage } = useMessages();

  const [midiUrl, setMidiUrl] = useState<NamedUrl | undefined>();
  const [audioUrl, setAudioUrl] = useState<NamedUrl | undefined>();
  const [midizUrl, setMidizUrl] = useState<NamedUrl | undefined>();
  const [selectedTemplate, setSelectedTemplate] = useState<string | undefined>();
  const [workspaceId, setWorkspaceId] = useState<number | undefined>();
  const [shouldLoadFiles, setShouldLoadFiles] = useState(false);
  const [tracks, setTracks] = useState<Track[]>([]);
  const [fileBundle, setFileBundle] = useState<FileBundle | null>(null);
  const [searchedForSavedFiles, setSearchedForSavedFiles] = useState(false);
  const [testFiles, setTestFiles] = useState(false);
  const [autoload, setAutoload] = useState(false);
  const [seededDB, setSeededDB] = useState(false);

  const { ok: dbOK, getLatestWorkspace, findFiles, getAllFiles, createFile } = useFiles();

  const reset = useCallback(() => {
    console.log("useFileUploader.reset");
    setMidiUrl(undefined);
    setAudioUrl(undefined);
    setMidizUrl(undefined);
    setWorkspaceId(undefined);
    setSelectedTemplate(undefined);
    setShouldLoadFiles(false);
    setTracks([]);
    setFileBundle(null);
    setSeededDB(false);
    setAutoload(false);
  }, [
    setMidiUrl,
    setAudioUrl,
    setMidizUrl,
    setShouldLoadFiles,
    setSelectedTemplate,
    setTracks,
    setFileBundle,
    setSeededDB,
    setAutoload,
  ]);

  const loadFileBundle = useCallback(
    async (f: FileBundle) => {
      const tracks = await loadFiles(f);

      setTracks(tracks);
      setFileBundle(f);
    },
    [setTracks]
  );

  const createFileFromUrl = useCallback(
    async ({ url, type }: { url: string; type: FileType }) => {
      const name = url.split("/").pop();
      const input = await (await fetch(url)).blob();
      const res = await createFile({
        name,
        type,
        input,
      });
      return res;
    },
    [createFile]
  );

  const seed = async () => {
    const fs = await getAllFiles();

    {
      const type = "audio";
      const have = fs.some((f) => f.type === type);
      if (!have) {
        const url = `${process.env.PUBLIC_URL}/assets/branching.mp3`;
        const created = await createFileFromUrl({ url, type });
        console.log("created audio", created);
      }
    }
    {
      const type = "midi";
      const have = fs.some((f) => f.type === type);
      if (!have) {
        const url = `${process.env.PUBLIC_URL}/assets/branching.mid`;
        const created = await createFileFromUrl({ url, type });
        console.log("created midi", created);
      }
    }
  };
  useEffect(() => {
    if (!dbOK) {
      throw new Error("DB not OK");
    }
  }, [dbOK]);

  useEffect(() => {
    if (!dbOK) return;

    const run = async () => {
      await seed();
      setSeededDB(true);
    };

    run();
  }, [dbOK]);

  const wsFromParams = useWorkspaceFromSearchParams();
  const latestWs = useLatestWorkspace();

  useEffect(() => {
    if (!dbOK) return;
    if (!seededDB) return;
    if (!wsFromParams) return;
    if (!latestWs) return;

    console.log("wsFromParams", wsFromParams);

    if (wsFromParams.found) {
      const { audioUrl, midiUrl, midizUrl, workspaceId } = wsFromParams.urls;
      setAudioUrl(audioUrl);
      setMidiUrl(midiUrl);
      setMidizUrl(midizUrl);
      setWorkspaceId(workspaceId);
      setSearchedForSavedFiles(true);
      return;
    }

    if (wsFromParams.error) {
      showMessage(wsFromParams.error, { severity: "error" });
      return;
    }

    if (latestWs.found) {
      const { audioUrl, midiUrl, midizUrl, workspaceId } = latestWs.urls;
      setAudioUrl(audioUrl);
      setMidiUrl(midiUrl);
      setMidizUrl(midizUrl);
      setWorkspaceId(workspaceId);
      setSearchedForSavedFiles(true);
      return;
    }

    if (latestWs.error) {
      showMessage(latestWs.error, { severity: "error" });
      return;
    }
  }, [dbOK, seededDB, wsFromParams, latestWs, showMessage]);

  useEffect(() => {
    if (!searchedForSavedFiles) return;
    if (!shouldLoadFiles) return;
    if (!midiUrl) return;
    if (!audioUrl) return;
    if (!workspaceId) return;
    const load = async () => {
      const b = {
        midi: midiUrl,
        audio: audioUrl,
        midiz: midizUrl,
        workspaceId,
        selectedTemplate,
      };
      await loadFileBundle(b);
    };
    load();
  }, [searchedForSavedFiles, shouldLoadFiles, loadFileBundle, midiUrl, audioUrl, midizUrl]);

  // TODO: Remove this.
  useEffect(() => {
    if (shouldLoadFiles) return;
    if (!testFiles) return;

    setMidiUrl({
      url: `${process.env.PUBLIC_URL}/assets/nogo.mid`,
      name: "/assets/nogo.mid",
      createdISO: dayjs().toISOString(),
      updatedISO: dayjs().toISOString(),
      dbInfo: { fileId: 0 },
      type: "audio",
    });
    setAudioUrl({
      url: `${process.env.PUBLIC_URL}/assets/nogo.mp3`,
      name: "/assets/nogo.mp3",
      createdISO: dayjs().toISOString(),
      updatedISO: dayjs().toISOString(),
      dbInfo: { fileId: 0 },
      type: "midi",
    });
    setMidizUrl({
      url: `${process.env.PUBLIC_URL}/assets/nogo.midiz`,
      name: "/assets/nogo.midiz",
      createdISO: dayjs().toISOString(),
      updatedISO: dayjs().toISOString(),
      dbInfo: { fileId: 0 },
      type: "midiz",
    });
    setShouldLoadFiles(true);
  }, [setMidiUrl, setAudioUrl, setMidizUrl, testFiles, shouldLoadFiles]);

  useEffect(() => {
    if (shouldLoadFiles) return;
    if (!autoload) return;

    setShouldLoadFiles(true);
  }, [setMidiUrl, setAudioUrl, setMidizUrl, autoload, shouldLoadFiles]);

  return {
    input: {
      setAutoloadOption: setAutoload,
      setTestFilesOption: setTestFiles,
    },
    output: {
      fileBundle,
      searchedForSavedFiles,
      tracks,
      reset,
    },
    fileUploaderProps: {
      midiUrl,
      midizUrl,
      audioUrl,
      selectedTemplate,
      selectedWorkspaceId: workspaceId,
      setWorkspaceId,
      setAudioUrl,
      setMidiUrl,
      setMidizUrl,
      setSelectedTemplate,
      setShouldLoadFiles,
      setUseTestFiles: setTestFiles,
    },
  };
};

export default useFileUploader;
