import {
  MidiAnimation,
  IMidiAnimationDrawData,
  Note, AnimationOptions,
  NoteFilterCriteria
} from "@/lib/types/main";
import { StarsOptions } from "@/lib/types/BaseAnimationOptions";
import { BaseAnimationOptions } from "../../lib/types/BaseAnimationOptions";
import { BaseMidiAnimation } from "@/lib/midi/animations/BaseMidiAnimation";
import { StarsUtil } from "./StarsUtil";

export interface Options extends BaseAnimationOptions, StarsOptions { }

const defaults = {
  or: { type: "size", default: 0 },
  ir: { type: "size", default: 0 },
  sides: { type: "size", default: 0 },
  scale: { type: "size", default: 1 },
  lineWidth: { type: "size", default: 5 },
  fillColor: { type: "color", default: "#fff" },
  lineColor: { type: "color", default: "#000" },
} as AnimationOptions<Options>;

class Stars extends BaseMidiAnimation<Options> implements MidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, defaults, StarsUtil.NAME, StarsUtil.getInstance(), id);
  }
}

export default Stars;
