import { GroupedTrack } from "@/lib/types/editor";
import { MidiAnimation } from "@/lib/types/main";
import { BaseAnimationTemplate } from "./base";
import { createCircles, createOrigins } from "./rand";
import { AnimationsTemplate } from "./types";

class Circles extends BaseAnimationTemplate implements AnimationsTemplate {
  constructor() {
    super("circles");
  }

  createAnimations = (tracks: GroupedTrack[]): MidiAnimation[] => {
    const origins = createOrigins(tracks);
    return tracks.map((track, i) => createCircles(track, origins[i % origins.length]));
  };
}

export default Circles;
