import React from "react";
import { useEffect, useState } from "react";
import useFiles from "./useFiles";
import { NamedUrl } from "@/lib/types/load";

interface Result {
  urls?: {
    audioUrl: NamedUrl;
    midiUrl: NamedUrl;
    midizUrl?: NamedUrl;
    workspaceId: number;
  };
  found: boolean;
  description: string;
  error?: string;
}

const useLatestWorkspace = () => {
  const files = useFiles();

  const [result, setResult] = useState<Result | undefined>();

  useEffect(() => {
    const findInDB = async () => {
      const latestWorkspace = await files.getLatestWorkspace();
      if (!latestWorkspace) return;
      const { audioUrl, midiUrl, midizUrl } = await files.findFiles(latestWorkspace);

      if (!audioUrl) return;
      if (!midiUrl) return;

      return {
        audioUrl,
        midiUrl,
        midizUrl,
        workspaceId: latestWorkspace.id,
      };
    };
    findInDB()
      .then((res) => {
        if (!res) {
          setResult({
            description: "No workspace found",
            found: false,
          });
          return;
        }
        setResult({
          description: "Loaded latest workspace",
          found: true,
          urls: res,
        });
      })
      .catch((e) => {
        console.error("Error finding files in db", e);
        setResult({
          description: "Error finding files in db",
          found: false,
          error: e.message,
        });
      });
  }, []);

  return result;
};

export default useLatestWorkspace;
