import React, { useCallback } from "react";
import { useMemo, useState } from "react";

import { Box, Button, Grid, IconButton, Stack, Typography } from "@mui/material";
import pluralize from "pluralize";
import EditIcon from "@mui/icons-material/Edit";
import AnimationTypeSelector from "@/components/editor/AnimationTypeSelector";
import * as flags from "@/lib/flags";
import { Point } from "@/lib/types/main";
import ChangeCircleIcon from '@mui/icons-material/ChangeCircle';
interface Instrument {
  name: string;
}
interface Track {
  index: number;
  name: string;
}

interface CombinedTrack {
  tracks: Track[];
}

interface GroupedTrack {
  name: string;
  tracks: CombinedTrack[];
  instrument: Instrument;
}

interface MidiAnimation {
  id: string;
  name: string;
  mute: boolean;
  solo: boolean;
  origin: Point;
}

const findTrackCounts = (track: GroupedTrack) => {
  const realTrackCount = track.tracks.map((t) => t.tracks.length).reduce((a, b) => a + b, 0);
  const realTrackIds = track.tracks.map((t) => t.tracks.map((t) => Number(t.index))).flat();
  realTrackIds.sort((a, b) => a - b);
  const regionCount = track.tracks.length;
  // Replace triples or more with ranges
  // This is so bad.
  const realTrackIdsStringParts = [];
  let realTrackIdsStringAccum = [];
  const dumpRealTrackIdsStringAccum = () => {
    switch (realTrackIdsStringAccum.length) {
      case 0:
        break;
      case 1:
        realTrackIdsStringParts.push(realTrackIdsStringAccum[0]);
        break;
      case 2:
        realTrackIdsStringParts.push(realTrackIdsStringAccum[0]);
        realTrackIdsStringParts.push(realTrackIdsStringAccum[1]);
        break;
      default:
        realTrackIdsStringParts.push(
          `${realTrackIdsStringAccum[0]}-${realTrackIdsStringAccum[realTrackIdsStringAccum.length - 1]
          }`
        );
        break;
    }
  };
  realTrackIds.forEach((id) => {
    if (realTrackIdsStringAccum.length === 0) {
      realTrackIdsStringAccum.push(id);
      return;
    }
    const last = realTrackIdsStringAccum[realTrackIdsStringAccum.length - 1];
    if (last + 1 === id) {
      realTrackIdsStringAccum.push(id);
      return;
    }
    dumpRealTrackIdsStringAccum();
    realTrackIdsStringAccum = [id];
  });
  dumpRealTrackIdsStringAccum();
  const realTrackIdsString = realTrackIdsStringParts.join(", ");
  return {
    realTrackCount,
    regionCount,
    realTrackIdsString,
  };
};

interface AnimationTypeSelectorToggleProps {
  animation: MidiAnimation;
  onAnimationTypeChange: (name: string) => void;
  isEdit: boolean;
  setIsEdit: (isEdit: boolean) => void;
}

const AnimationTypeSelectorToggle: React.FC<AnimationTypeSelectorToggleProps> = (props) => {
  const { isEdit, setIsEdit, animation, onAnimationTypeChange: realOnAnimationTypeChange } = props;

  const animationString = useMemo(() => animation?.name, [animation]);
  const onAnimationTypeChange = useCallback((name: string) => {
    if (!name) {
      setIsEdit(false);
    } else {
      realOnAnimationTypeChange(name);
    }
  }, [realOnAnimationTypeChange, setIsEdit]);
  const switcher = useMemo(() => {
    return (
      <AnimationTypeSelector
        animationName={animation.name}
        onAnimationTypeChange={onAnimationTypeChange}
      />
    );
  }, [animation.name, onAnimationTypeChange]);

  return (
    <>
      <div style={{ width: "100%", height: "100%" }}>
        <Box sx={{ display: "flex", flexDirection: "row", justifyContent: "start", alignItems: "center", }}>
          <Box sx={{
            flexGrow: 1,
            cursor: "pointer",
            pl: 1,
          }}>
            <Typography variant="caption">
              {isEdit ? switcher :
                <Box onClick={() => setIsEdit(!isEdit)}>
                  {animationString}
                </Box>
              }
            </Typography>
          </Box>
          <Box>
            <IconButton size="small" onClick={() => setIsEdit(!isEdit)}>
              <ChangeCircleIcon
                color={isEdit ? "warning" : "inherit"}
                fontSize="small"
              />
            </IconButton>
          </Box>
        </Box>
      </div>
    </>
  );
};

interface TrackDetailsProps {
  track: GroupedTrack;
  animation: MidiAnimation | null;
  onAnimationTypeChange: (name: string) => void;
  showDetails: boolean;
}

const TrackDetails: React.FC<TrackDetailsProps> = (props) => {
  const { track, animation, onAnimationTypeChange, showDetails } = props;

  const animationString = useMemo(() => animation?.name, [animation]);
  const origin = useMemo(() => animation.origin, [animation]);
  const { realTrackCount, regionCount, realTrackIdsString } = useMemo(
    () => findTrackCounts(track),
    [track]
  );
  const [isEdit, setIsEdit] = useState(false);
  const toggle = useMemo(() => {
    return (
      <AnimationTypeSelectorToggle
        animation={animation}
        onAnimationTypeChange={onAnimationTypeChange}
        isEdit={isEdit}
        setIsEdit={setIsEdit}
      />
    );
  }, [isEdit, setIsEdit, animation]);

  return (
    <Stack>
      {showDetails ? (
        <>
          <Stack spacing={0} direction={"column"}>
            <Typography variant="subtitle2">
              {track.name}
              {flags.showAnimationTypeToggle ? toggle : undefined}
            </Typography>
            <Typography variant="subtitle1">
              {track.instrument.name} | <em>{animationString}</em>
            </Typography>
            <Typography variant="caption">
              {regionCount} {pluralize("region", regionCount)}/{realTrackCount}{" "}
              {pluralize("track", realTrackCount)}: {realTrackIdsString}
            </Typography>
          </Stack>
        </>
      ) : (
        <>
          <Stack spacing={0} direction={"column"}>
            <Typography variant="subtitle2">
              {track.name}
            </Typography>
            <Typography variant="caption">
              <Stack direction={"row"} spacing={1} sx={{ alignItems: "center" }}>
                ({Math.round(origin.x)}, {Math.round(origin.y)}) -
                <em>
                  {flags.showAnimationTypeToggle
                    ? toggle
                    : animationString}
                </em>
              </Stack>
            </Typography>
          </Stack>
        </>
      )}
    </Stack>
  );
};

export default TrackDetails;
