import React from "react";
import { IMidiAnimationRegistry, Midi, MidiAnimation, MidiNoteGetter } from "@/lib/types/main";
import { AudioSource, IPlugin, IPluginDrawProps } from "@/lib/types/plugin";
import { createMidiNoteSource } from "../../midi/impl";
// import TwoTS from "twojs-ts";
// declare class Two extends TwoTS.Two { }
import Two from "two.js";

export interface WavePluginProps {
  verbose?: boolean;
}

interface Del {
  each: (callback: (anim: MidiAnimation) => void) => void;
  addMidiAnimation: (animation: MidiAnimation) => void;
  clearMidiAnimations: () => void;
  addMidiAnimations: (animations: MidiAnimation[]) => void;
}

export class BaseMidiAnimationRegistry
  implements IPlugin<Midi | undefined>, IMidiAnimationRegistry
{
  private _midiNoteGetter: MidiNoteGetter | null = null;
  private _verbose: boolean;
  private _del: Del;

  private _muted: Record<string, boolean> = {};
  private _soloed: Record<string, boolean> = {};
  private _two: React.RefObject<Two> | null = null;

  public constructor(del: Del, props?: WavePluginProps) {
    this._del = del;
    const { verbose } = props || {};
    this._verbose = verbose || false;
  }

  public get name(): string {
    throw new Error("implement name()");
  }

  // TODO: we"re calling this twice
  public async init(audioSource: AudioSource): Promise<Midi | undefined> {
    this._log("Loading midi file", audioSource);
    try {
      const audioUrl = audioSource.url;
      const ext = audioUrl.split(".").pop();
      const allButExt = audioUrl.slice(0, audioUrl.length - ext.length - 1);
      const midiUrl = `${allButExt}.mid`;
      const midiBuffer = midiUrl.startsWith("blob:")
        ? await (await (await fetch(midiUrl)).blob()).arrayBuffer()
        : await (await fetch(midiUrl)).arrayBuffer();
      const { getNotes, source } = await createMidiNoteSource(midiBuffer, {
        verbose: this._verbose,
      });
      this._midiNoteGetter = { getNotes };
      return source;
    } catch (e: any) {
      console.error("Error loading midi file", e);
    }
  }

  public async initFromUrl(midiUrl: string): Promise<Midi | undefined> {
    this._log("Loading midi file", midiUrl);
    try {
      const midiBuffer = midiUrl.startsWith("blob:")
        ? await (await (await fetch(midiUrl)).blob()).arrayBuffer()
        : await (await fetch(midiUrl)).arrayBuffer();
      const { getNotes, source } = await createMidiNoteSource(midiBuffer, {
        verbose: this._verbose,
      });
      this._midiNoteGetter = { getNotes };
      this._log(this._midiNoteGetter);
      return source;
    } catch (e: any) {
      console.error("Error loading midi file", e);
    }
  }

  public draw(props: IPluginDrawProps): void {
    const { currentTimeMillis, durationMillis, canvas, canvasElement, width, height } = props;
    if (!this._midiNoteGetter) return;
    const haveOneSoloed = Object.values(this._soloed).some((val) => val === true);
    const two = this._two?.current;
    two?.clear();
    this._del.each((animation: MidiAnimation) => {
      if (this._muted[animation.id] === true) return;
      if (haveOneSoloed && this._soloed[animation.id] !== true) return;
      const startMillis = currentTimeMillis;
      const criteria = animation.criteria;
      const notes = this._midiNoteGetter.getNotes({
        startMillis,
        durationMillis,
        criteria,
      });
      // if (notes.length) {
      animation.draw(notes, { width, height, two, canvas, canvasElement });
      // }
    });
    two?.update();
  }

  public setTwo(two: React.RefObject<Two>): void {
    if (this._two) {
      console.error("setTwo", two, "called twice");
      return;
    }
    this._two = two;
  }

  public addMidiAnimations(animations: MidiAnimation[]): void {
    this._log("addMidiAnimations", animations);
    this._del.addMidiAnimations(animations);
  }

  public addMidiAnimation(animation: MidiAnimation): void {
    this._log("addMidiAnimation", animation);
    this._del.addMidiAnimation(animation);
  }

  public clearMidiAnimations(): void {
    this._log("clearMidiAnimations");
    this._del.clearMidiAnimations();
  }

  public mute(animation: MidiAnimation, val: boolean): void {
    this._log("mute", animation, val, "cur", this._muted);
    this._muted[animation.id] = val;
  }

  public isMute(animation: MidiAnimation): boolean {
    return this._muted[animation.id] === true;
  }

  public solo(animation: MidiAnimation, val: boolean): void {
    this._log("solo", animation, val);
    this._soloed[animation.id] = val;
  }

  public isSolo(animation: MidiAnimation): boolean {
    return this._soloed[animation.id] === true;
  }

  public reset() {
    this._muted = {};
    this._soloed = {};
    this._del.clearMidiAnimations();
  }

  private _log(...args: any[]): void {
    if (this._verbose) console.log("[MidiAnimationRegistry]", ...args);
  }
}
