import React from "react";
import { FormControl, InputLabel, Select, MenuItem } from "@mui/material";
import allTemplates from "../midi/tmpl";
import { Setter } from "@/lib/types/util";

const TemplatesSelect = (props: {
  selectedTemplate: string;
  setSelectedTemplate: Setter<string>;
}) => {
  const { selectedTemplate, setSelectedTemplate } = props;

  return (
    <FormControl size="small" sx={{ width: 100 }}>
      <InputLabel id="templates-select-label">Tempate</InputLabel>
      <Select
        labelId="templates-select-label"
        id="templates-select"
        value={selectedTemplate}
        label="Display"
        onChange={(e) => setSelectedTemplate(e.target.value)}
        sx={{ textAlign: "center", minWidth: 200 }}
      >
        <MenuItem value={""}>--</MenuItem>
        {allTemplates.map((t) => (
          <MenuItem key={t.name} value={t.name}>
            {t.title}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default TemplatesSelect;
