import React from "react";
import { v4 as uuidv4 } from "uuid";
import {
  AnimationOptions,
  IMidiAnimationDrawData,
  IMidiAnimationInitData,
  Note,
  NoteFilterCriteria,
  Point,
  Size,
} from "@/lib/types/main";
import { BaseAnimationOptions } from "@/lib/types/BaseAnimationOptions";
import { OptionsPrototype } from "../../types/main";
import { scale as _scale } from "@/lib/conversion";
import * as t from "./two";
import * as u from "./util";

// import TwoTS from "twojs-ts";
// declare class Two extends TwoTS.Two { }
import Two from "two.js";
import { BaseUtil } from "./base";

export class BaseMidiAnimation<O extends BaseAnimationOptions> {
  public readonly criteria: NoteFilterCriteria;
  public readonly options: O;
  public readonly id: string;
  public readonly name: string;
  public readonly optionsPrototype: OptionsPrototype;

  public created: number = Date.now();
  private _verbose: boolean = false;
  private util: BaseUtil<O>;

  constructor(
    criteria: NoteFilterCriteria,
    options: O,
    defaults: AnimationOptions<O>,
    name: string,
    util: BaseUtil<O>,
    id?: string
  ) {
    this.criteria = criteria;
    const defaultOptions = Object.fromEntries(
      Object.entries(defaults).map(([key, value]) => [key, value.default])
    );
    const optionsPrototype = Object.fromEntries(
      Object.entries(defaults).map(([key, value]) => [key, value.type])
    );
    this.options = { ...defaultOptions, ...options };
    this.name = name;
    this.id = id || uuidv4();
    this.optionsPrototype = {
      origin: "point",
      ...optionsPrototype,
    };
    this.util = util;
  }

  public readonly draw = (notes: Note[], data: IMidiAnimationDrawData): void => {
    this._verbose && this.log("draw", notes, data);
    this.util.draw(this.options, notes, data);
  };

  public readonly drawSample = (data: IMidiAnimationDrawData) => {
    this._verbose && this.log("drawSample", data);
    this.util.drawSample(this.options, data);
  };

  public readonly setCreated = (val: number) => {
    this.created = val;
  };

  public readonly setVerbose = (val: boolean) => {
    this._verbose = val;
  };

  public get verbose() {
    return this._verbose;
  }

  protected readonly log = (prefix: string, ...args: any[]) => {
    if (this._verbose) {
      console.log(`[${this.name}]`, prefix, ...args);
    }
  };

  protected readonly makeCircle = t.makeCircle;
  protected readonly makeRectangle = t.makeRectangle;
  protected readonly makeRoundedRectangle = t.makeRoundedRectangle;
  protected readonly makeStar = t.makeStar;
  protected readonly makeEllipse = t.makeEllipse;
  protected readonly changeOpacity = u.changeOpacity;
  protected readonly sampleOpacity = (color: string) => this.changeOpacity(color, 0.7);

  protected readonly scale = (size: Size) => {
    const res = _scale(size);
    // this.log("scale", size, "=>", res);
    return res;
  };

  protected readonly getOrigin = (origin: Point | undefined, width: Size, height: Size) => {
    const w = this.scale(width);
    const h = this.scale(height);
    if (!origin) {
      return { x: w / 2, y: h / 2 };
    }
    return {
      x: (w * this.scale(origin.x)) / h,
      y: (h * this.scale(origin.y)) / w,
    };
  };
}
