import { BaseMidiAnimation } from "@/lib/midi/animations/BaseMidiAnimation";
import { RectsOptions } from "@/lib/types/BaseAnimationOptions";
import {
  AnimationOptions,
  IMidiAnimationDrawData,
  MidiAnimation,
  Note,
  NoteFilterCriteria,
} from "@/lib/types/main";
import { BaseAnimationOptions } from "../../lib/types/BaseAnimationOptions";
import { RectsUtil } from "./RectsUtil";

export interface Options extends BaseAnimationOptions, RectsOptions { }

const defaults = {
  width: { type: "size", default: 0 },
  height: { type: "size", default: 0 },
  scale: { type: "size", default: 1 },
  lineWidth: { type: "size", default: 5 },
  fillColor: { type: "color", default: "#fff" },
  lineColor: { type: "color", default: "#000" },
} as AnimationOptions<Options>;

class Rects extends BaseMidiAnimation<Options> implements MidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, defaults, RectsUtil.NAME, RectsUtil.getInstance(), id);
  }
}

export default Rects;
