import React from "react";
import { Midi as ToneJSMidi } from "@tonejs/midi";
import { FileBundle } from "@/lib/types/load";
import { MidiAnimation } from "./types/main";
import { deserializeAnimation } from "./midi/lib";
import { AnimationConfig } from "./types/animations";

const readArrayBuffer = async (url: string) => {
  if (url.startsWith("blob:")) {
    return await (await (await fetch(url)).blob()).arrayBuffer();
  }
  return await (await fetch(url)).arrayBuffer();
};

export const loadFiles = async (f: FileBundle, opts?: { verbose?: boolean }) => {
  const {
    midi: { url },
  } = f;

  const { verbose = false } = opts || {};

  if (verbose) console.log("[loadFiles]", "loading", url);

  const midiBuffer = await readArrayBuffer(url);
  const midi = new ToneJSMidi(midiBuffer);
  const midiJSON = midi.toJSON();

  if (verbose) console.log("[loadFiles]", midiJSON);

  const tracks = midi.tracks.map((track, i) => {
    const instr = track.instrument.toJSON();
    const { family, name } = instr;
    const instrument = { family, name };
    const notes = track.notes.map((note) => {
      const { midi, time, duration, velocity } = note;
      return { midi, time, duration, velocity };
    });
    return {
      index: i,
      name: track.name,
      minTimeSecs: track.notes[0].time,
      maxTimeSecs:
        track.notes[track.notes.length - 1].time + track.notes[track.notes.length - 1].duration,
      duration: track.duration,
      durationTicks: track.durationTicks,
      instrument,
      notes,
    };
  });
  return tracks;
};

export const loadAnimations = async (animationsUrl: string): Promise<MidiAnimation[]> => {
  const animBlob = await (await fetch(animationsUrl)).blob();
  const animJson = await animBlob.text();
  if (!animJson) {
    throw new Error("No animation data");
  }
  const animConfig = JSON.parse(animJson) as AnimationConfig;
  const animations = animConfig.animations.map(deserializeAnimation) as MidiAnimation[];

  return animations;
};
