import Two from "two.js";

const groupTracks = (tracks: Track[]): GroupedTrack[] => {
  const res = tracks.reduce((acc, track) => {
    const key = [track.name, track.instrument.family, track.instrument.name].join("-");
    if (!acc[key]) {
      acc[key] = { instrument: track.instrument, name: track.name, tracks: [] };
    }
    const combinedTrack = { ...track, tracks: [track] };
    acc[key].tracks.push(combinedTrack);
    return acc;
  }, []);
  return Object.values(res) as GroupedTrack[];
};

const groupAndCombineGroupedTracks = (groupedTracks: GroupedTrack[]): GroupedTrack[] => {
  const inside = (t: number, tr: { minTimeSecs: number; maxTimeSecs: number }) => {
    const min = tr.minTimeSecs - 2;
    const max = tr.maxTimeSecs + 2;
    return t >= min && t <= max;
  };
  const tracksOverlap = (t: CombinedTrack, u: CombinedTrack) => {
    return (
      inside(t.minTimeSecs, u) ||
      inside(t.maxTimeSecs, u) ||
      inside(u.minTimeSecs, t) ||
      inside(u.maxTimeSecs, t)
    );
  };
  // Create single CombinedTracks that each contain all the tracks that overlap.
  const combineGroupedTrack = (group: GroupedTrack) => {
    let changed = false;
    let times = 0;
    do {
      changed = false;
      // Try to merge any of the groups of tracks that overlap.
      for (let i = 0; i < group.tracks.length; i++) {
        const t = group.tracks[i];
        for (let j = i + 1; j < group.tracks.length; j++) {
          const u = group.tracks[j];
          if (tracksOverlap(t, u)) {
            const combinedTrack = {
              index: i,
              name: t.name,
              minTimeSecs: Math.min(t.minTimeSecs, u.minTimeSecs),
              maxTimeSecs: Math.max(t.maxTimeSecs, u.maxTimeSecs),
              duration: Math.max(t.duration, u.duration),
              instrument: t.instrument,
              tracks: [...t.tracks, ...u.tracks],
            };
            group.tracks[i] = combinedTrack;
            group.tracks.splice(j, 1);
            changed = true;
            break;
          }
        }
        if (changed) break;
      }
    } while (changed && ++times < 1000);
  };
  groupedTracks.forEach(combineGroupedTrack);
  return groupedTracks;
};

const groupAndCombineTracks = (tracks: Track[]) => {
  const groupedTracks = groupTracks(tracks);
  const groupedAndCombinedTracks = groupAndCombineGroupedTracks(groupedTracks);
  return groupedAndCombinedTracks.map((track, i) => ({
    ...track,
    index: i,
  })) as GroupedTrack[];
};

interface Props {
  tracks: Track[];
  animationsUrl: string;
  two: MutRaf<Two>;
  divElm: MutRaf<HTMLElement>;
  templateName: string | null;
}

export const useAnimations = (props: Props) => {
  const { templateName, divElm, two, tracks, animationsUrl } = props;

  const { showMessage } = useMessages();

  // const registryRef = useRef<RefBasedMidiAnimationRegistry | null>();
  const [registry, setRegistry] = useState<RefBasedMidiAnimationRegistry>();
  const [needsSaving, setNeedsSaving] = useState(false);
  const animationsRef = useRef<MidiAnimation[] | undefined>(undefined);
  const groupedAndCombinedTracks = useMemo(() => groupAndCombineTracks(tracks), [tracks]);

  const [verboseDiffs, setVerboseDiffs] = useState(true);

  useEffect(() => {
    if (!two) return;
    if (!divElm) return;
    if (!groupedAndCombinedTracks || !groupedAndCombinedTracks.length) return;

    const width = divElm.current.offsetWidth;
    const height = divElm.current.offsetHeight;

    const loadAnimations = async () => {
      let createdAnimations = false;
      let animations: MidiAnimation[];
      if (animationsUrl) {
        let animBlob;
        let animJson;
        try {
          animBlob = await (await fetch(animationsUrl)).blob();
          animJson = await animBlob.text();
          if (animJson) {
            const animConfig = JSON.parse(animJson) as AnimationConfig;
            animations = animConfig.animations.map(deserializeAnimation) as MidiAnimation[];
            showMessage("Loaded saved animations");
          }
        } catch (e: any) {
          console.error(
            "Error loading animations",
            "animationsUrl",
            animationsUrl,
            e,
            "animJson",
            animJson,
            "animBlob",
            animBlob
          );
          showMessage(`Error reading from ${animationsUrl}`, { severity: "error" });
        }
      }
      if (!animations && templateName) {
        try {
          const t = allTemplates.find((t) => t.name === templateName);
          const tmpl = new t.ctor();
          animations = tmpl.createAnimations(groupedAndCombinedTracks);
          createdAnimations = true;
          showMessage(`Loaded ${templateName} animations`);
        } catch (e: any) {
          console.error("Error loading template", "templateName", templateName, e);
          showMessage(`Error loading template ${templateName}`, { severity: "error" });
        }
      }
      if (!animations) {
        try {
          const origins = createOrigins(groupedAndCombinedTracks);
          animations = groupedAndCombinedTracks.map((track, i) => {
            return createDemoAnimation(i, track.name, origins[i]);
          });
          createdAnimations = true;
          showMessage("Loaded demo animations");
        } catch (e: any) {
          console.error("Error loading demo animations", e);
          showMessage("Error loading demo animations", { severity: "error" });
        }
      }

      if (!animations || !animations.length) {
        showMessage("Couldn't create animations, please see the dev console", {
          severity: "error",
        });
        throw new Error("No animations");
      }

      groupedAndCombinedTracks.forEach((track, i) => {
        const anim = animations[i];
        const flatTrack = flattenTrack(track);
        const data = createMidiAnimationInitData(flatTrack);
        const initData = {
          ...data,
          two: two.current,
          width,
          height,
        };
        // anim.init(initData);
      });

      animationsRef.current = animations;
      if (createdAnimations) setNeedsSaving(true);
      const registry = new RefBasedMidiAnimationRegistry(animationsRef, { verbose: true });

      setRegistry(registry);
      // registryRef.current = registry;
    };
    loadAnimations();
  }, [groupedAndCombinedTracks, animationsUrl]);

  const setAnimations = useCallback(
    (fn: (prevState: MidiAnimation[]) => MidiAnimation[]) => {
      console.log("setAnimations");
      const oldState = animationsRef.current || [];
      const a = JSON.stringify(oldState);
      const newState = fn(oldState);
      const b = JSON.stringify(newState);
      console.log("equal?", a === b);
      if (a === b) return;

      if (verboseDiffs) {
        const lhs = JSON.parse(a),
          rhs = JSON.parse(b);
        const diff = detailedDiff(lhs, rhs);
        console.log("diff", JSON.stringify(diff), diff);
      }

      animationsRef.current = newState;
    },
    [animationsRef.current, verboseDiffs]
  );

  const setAnimation = useCallback(
    (index: number, newAnimationSetter: Provider<MidiAnimation>) => {
      console.log(`setAnimation[${index}]`);
      if (!animationsRef.current) {
        throw new Error("Animations must be set before calling setAnimation");
      }
      if (index < 0) {
        throw new Error("Index must be greater than or equal to 0");
      }
      if (index >= animationsRef.current.length) {
        throw new Error("Index must be less than " + animationsRef.current.length);
      }

      const old = animationsRef.current[index];
      const newAnimation =
        typeof newAnimationSetter === "function"
          ? newAnimationSetter(old)
          : (newAnimationSetter as MidiAnimation);

      if (verboseDiffs) {
        const oldState = serializeAnimation(old);
        const a = JSON.stringify(oldState);
        const newState = serializeAnimation(newAnimation);
        const b = JSON.stringify(newState);
        console.log("equal?", a === b);
        const lhs = JSON.parse(a),
          rhs = JSON.parse(b);
        const diff = detailedDiff(lhs, rhs);
        console.log("diff", JSON.stringify(diff), diff);
      }

      animationsRef.current[index] = newAnimation;
    },
    [animationsRef.current, verboseDiffs]
  );

  return {
    setAnimation, // TODO: not tested
    setAnimations,
    // registry: registryRef,
    registry,
    groupedAndCombinedTracks,
    animationsRef,
    needsSaving,
    setVerboseDiffs,
  };
};
