import React from "react";
import { MutRaf } from "@/lib/types/util";
import { useCallback, useState } from "react";

interface Props {
  wavesurferRef: MutRaf<WaveSurfer>;
}

const useAudioPlayer = (props: Props) => {
  const { wavesurferRef } = props;

  const [durationSecs, setDurationSecs] = useState(0);

  const playAudio = useCallback(() => wavesurferRef?.current?.playPause(), []);
  const restartAudio = useCallback(() => wavesurferRef?.current?.seekTo(0), []);
  const rewindAudio = useCallback(() => wavesurferRef?.current?.skip(-5), []);
  const fastFowrardAudio = useCallback(() => wavesurferRef?.current?.skip(5), []);
  const pauseAudio = useCallback(() => wavesurferRef?.current?.pause(), []);
  const stopAudio = useCallback(() => {
    wavesurferRef?.current?.stop();
    wavesurferRef?.current?.seekTo(0);
  }, []);

  return {
    pauseAudio,
    playAudio,
    stopAudio,
    restartAudio,
    rewindAudio,
    fastFowrardAudio,
    setDurationSecs,
  };
};

export default useAudioPlayer;
