import { IMidiAnimationRegistry, Midi, MidiAnimation } from "@/lib/types/main";
import { uniqueTrackNames } from "../lib/midi/adapter";
import { createMidiAnimationInitData } from "./impl";
import { createCircles, createOrigins } from "./tmpl/rand";

export const registerDemoAnimations = (source: Midi, registry: IMidiAnimationRegistry) => {
  const anims = createDemoAnimations(source);
  anims.forEach((anim) => {
    const track = source.tracks.find((t) => t.name === anim.criteria.trackRef);
    const data = createMidiAnimationInitData(track);
    // anim.init(data);
    registry.addMidiAnimation(anim);
  });
};

export const createDemoAnimations = (source: Midi) => {
  const trackNames = uniqueTrackNames(source);
  const origins = createOrigins(trackNames.map((name) => ({ name })));
  return trackNames.map((trackName, i) => {
    const origin = origins[i % origins.length];
    return createDemoAnimation(i, trackName, origin);
  });
};

export const createDemoAnimation = (
  i: number,
  trackName: string,
  origin: { x: number; y: number }
): MidiAnimation => {
  const track = { name: trackName };
  return createCircles(track, origin);
};
