import React from "react";
export type Key = string;

export type ValidType = string | number | boolean;

export type Pref<K extends Key, T extends ValidType> = {
  key: K;
  defaultValue: T;
  enumValues?: T[];
};

export class Prefs<K extends Key> {
  private readonly defaults: Map<Key, Pref<Key, ValidType>> = new Map();

  public constructor(defaults: Pref<Key, ValidType>[]) {
    defaults.forEach((p) => this.defaults.set(p.key, p));
  }

  public get<T extends ValidType>(key: K): T | undefined {
    const s = localStorage.getItem(key);
    if (!s) {
      const val = this.defaults.get(key).defaultValue;
      if (val === undefined) {
        throw new Error(`get: No default value for key: ${key}`);
      }
      return val as T;
    }
    const val = JSON.parse(s) as T;
    const pref = this.defaults.get(key);
    if (pref.enumValues && pref.enumValues.length) {
      if (!pref.enumValues.includes(val)) {
        throw new Error(
          `get: Invalid value for key: ${key}: ${val}, valid values are: ${pref.enumValues}`
        );
      }
    }
    return val;
  }

  public put<T>(key: K, value: T) {
    if (!this.defaults.has(key)) {
      throw new Error(`put: No such key: ${key}`);
    }
    const val = JSON.stringify(value);
    localStorage.setItem(key, val);
  }
}
