import React from "react";
import { useMemo } from "react";

import { Box, Checkbox, Divider, Grid, IconButton, Tooltip, Typography } from "@mui/material";
import SaveIcon from "@mui/icons-material/Save";
import DownloadIcon from "@mui/icons-material/Download";
import FileUploadIcon from "@mui/icons-material/FileUpload";
import SettingsIcon from "@mui/icons-material/Settings";
import ChangeCircleIcon from "@mui/icons-material/ChangeCircle";
import { Setter } from "@/lib/types/util";
import ShareIcon from "@mui/icons-material/Share";
import { logicTrackMuteBackgroundColor, logicTrackSoloBackgroundColor } from "@/lib/colors";
import LetterCheckBox from "@/components/LetterCheckBox";
import AudioPlayerButtons from "../AudioPlayerButtons";

interface MetadataPanelProps {
  isPlaying: boolean;
  isLoaded: boolean;
  saveEnabled: boolean;
  audioName: string;
  midiName: string;
  midizName: string;
  workspaceId: number;
  hasStarted: boolean;
  onlyShowActive: boolean;
  play: () => void;
  stop: () => void;
  restart: () => void;
  rewind: () => void;
  fastForward: () => void;
  upload: () => void;
  saveAll: () => void;
  download: () => void;
  openSettings: () => void;
  setOnlyShowActive: Setter<boolean>;
  openTemplates: () => void;
  anySoloed: boolean;
  anyMuted: boolean;
  soloAnimations: (b: boolean) => void;
  muteAnimations: (b: boolean) => void;
}

const MetadataPanel = (props: MetadataPanelProps) => {
  const {
    isPlaying,
    isLoaded,
    saveEnabled,
    audioName,
    midiName,
    midizName,
    workspaceId,
    hasStarted,
    onlyShowActive,
    play,
    stop,
    restart,
    rewind,
    fastForward,
    upload,
    saveAll,
    download,
    setOnlyShowActive,
    openSettings,
    openTemplates,
    anySoloed,
    anyMuted,
    soloAnimations,
    muteAnimations,
  } = props;

  const disableSaveAll = useMemo(() => {
    return !saveEnabled;
  }, [saveEnabled]);

  const MidizButtons = (props: {
    saveAll: () => void;
    download: () => void;
    disableSaveAll: boolean;
  }) => {
    const { saveAll, download, disableSaveAll } = props;

    const CtrlButton = (props: {
      onClick: () => void;
      disabled?: boolean;
      children: JSX.Element;
      tooltip: string;
    }) => {
      const { onClick, disabled = false, children, tooltip } = props;
      const w = "25px !important";
      const B = () => (
        <IconButton
          size="small"
          sx={{ color: "rgba(0,0,0,.54)", width: w, minWidth: w, maxWidth: w, borderRadius: 0 }}
          disabled={disabled}
          onClick={onClick}
        >
          {children}
        </IconButton>
      );
      if (disabled) return <B />;
      return (
        <Tooltip title={tooltip}>
          <B />
        </Tooltip>
      );
    };
    return (
      <Box sx={{ justifySelf: "flex-end" }}>
        <Box>
          <CtrlButton tooltip={"Save"} disabled={disableSaveAll} onClick={saveAll}>
            <SaveIcon fontSize="small" />
          </CtrlButton>
          <CtrlButton tooltip={"Download"} disabled={!disableSaveAll} onClick={download}>
            <DownloadIcon fontSize="small" />
          </CtrlButton>
          <CtrlButton tooltip={"Upload"} onClick={upload}>
            <FileUploadIcon fontSize="small" />
          </CtrlButton>
        </Box>
      </Box>
    );
  };

  const RightWrapper = (props: { children: JSX.Element }) => {
    const { children } = props;
    return (
      <div style={{ width: "100%", height: "100%" }}>
        <Box
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "flex-end",
            alignContent: "center",
            alignItems: "center",
          }}
        >
          {children}
        </Box>
      </div>
    );
  };

  const midizRow = useMemo(() => {
    return (
      <Typography sx={{ ml: 1 }} variant="body2" component={"span"}>
        {midizName}
        {saveEnabled ? (
          <>
            <span style={{ color: "red" }}>*</span>
            <Typography sx={{ ml: 1 }} variant="caption">
              (modified)
            </Typography>
          </>
        ) : undefined}
      </Typography>
    );
  }, [midizName, saveEnabled]);

  const midizButtons = useMemo(() => {
    return (
      <RightWrapper>
        <MidizButtons saveAll={saveAll} download={download} disableSaveAll={disableSaveAll} />
      </RightWrapper>
    );
  }, [saveAll, download, disableSaveAll]);

  const audioNames = useMemo(() => {
    return (
      <Typography sx={{ ml: 1 }} variant="body2" component={"span"}>
        {audioName} // {midiName}
      </Typography>
    );
  }, [audioName, midiName]);

  const audioButtons = useMemo(() => {
    return (
      <RightWrapper>
        <AudioPlayerButtons
          isLoaded={isLoaded}
          hasStarted={hasStarted}
          isPlaying={isPlaying}
          restart={restart}
          rewind={rewind}
          stop={stop}
          fastForward={fastForward}
          play={play}
        />
      </RightWrapper>
    );
  }, [isLoaded, hasStarted, isPlaying, restart, rewind, stop, fastForward, play]);

  const settings = useMemo(() => {
    return (
      <div style={{ width: "100%", height: "100%" }}>
        <Box
          sx={{
            display: "flex",
            flexDirection: "row",
            alignContent: "center",
            alignItems: "center",
          }}
        >
          <Tooltip title={"Settings"}>
            <IconButton onClick={openSettings}>
              <SettingsIcon fontSize="small" />
            </IconButton>
          </Tooltip>
          <Tooltip title={"Templates"}>
            <IconButton onClick={openTemplates}>
              <ChangeCircleIcon fontSize="small" />
            </IconButton>
          </Tooltip>
          {/* TODO */}
          <Box
            sx={{ display: "flex", flexDirection: "row", alignSelf: "center", justifySelf: "end" }}
          >
            <Checkbox
              size={"small"}
              sx={{ m: 0, p: 0 }}
              inputProps={{ "aria-label": "only show active" }}
              onChange={(event) => setOnlyShowActive(event.target.checked)}
              checked={onlyShowActive}
            />
            <Typography variant="body2" sx={{ ml: 0.5 }}>
              Only show active tracks
            </Typography>
          </Box>
          <Box sx={{ flexGrow: 1, textAlign: "right", justifySelf: "flex-end", p: 0 }}>
            <LetterCheckBox
              letter="M"
              title="Unmute"
              checkedColor={logicTrackMuteBackgroundColor}
              onChange={() => muteAnimations(false)}
              checked={anyMuted}
              disabled={!anyMuted}
            />
            <LetterCheckBox
              letter="S"
              title="Unsolo"
              checkedColor={logicTrackSoloBackgroundColor}
              onChange={() => soloAnimations(false)}
              checked={anySoloed}
              disabled={!anySoloed}
            />
            <Tooltip title={"Share"}>
              <IconButton
                href={`/#/host?ws=${workspaceId}`}
                // target="_blank"
                // Pause on click
              >
                <ShareIcon fontSize="small" />
              </IconButton>
            </Tooltip>
          </Box>
        </Box>
      </div>
    );
  }, [openSettings, onlyShowActive, setOnlyShowActive]);

  return (
    <>
      <Box
        sx={{
          border: "1px solid rgb(238, 238, 238)",
          mb: 1,
          mr: 1,
        }}
      >
        <Grid container spacing={0}>
          <Grid item xs={9}>
            {midizRow}
          </Grid>
          <Grid item xs={3}>
            {midizButtons}
          </Grid>
          <Grid item xs={9}>
            {audioNames}
          </Grid>
          <Grid item xs={3}>
            {audioButtons}
          </Grid>
          <Grid item xs={12}>
            <Divider sx={{ mx: 1 }} />
          </Grid>
          <Grid item xs={12}>
            {settings}
          </Grid>
          {/* <Grid item xs={3}>{currentTime}</Grid> */}
        </Grid>
      </Box>
    </>
  );
};

export default MetadataPanel;
