import React from "react";
import {
  Box,
  Button,
  Divider,
  Link,
  Stack,
  Typography
} from "@mui/material";
import FileUploader from "@/components/FileUploader";
import { PropsFrom } from "@/lib/types/util";
import { useCallback, useEffect, useState } from "react";
import { useMemo } from "react";
import useFiles from "@/hooks/useFiles";
import useMessages from "@/hooks/useMessages";

type FileUploaderProps = PropsFrom<typeof FileUploader>;

type FileUploadPanelProps = {
  fileUploaderProps: Omit<FileUploaderProps, "activeStep" | "setActiveStep">;
  reset: () => void;
};

const FileUploadPanel: React.FC<FileUploadPanelProps> = (props) => {
  const { reset, fileUploaderProps } = props;

  const { clear: clearStorage, getAllFiles, getAllWorkspaces } = useFiles();

  const { showMessage } = useMessages();

  const handleClear = useCallback(async () => {
    const ok = window.confirm("Are you sure you want to clear all files and workspaces?");
    if (!ok) return;
    await clearStorage();
    reset();
  }, [clearStorage, reset]);

  const handleReset = useCallback(async () => {
    reset();
  }, [reset]);

  const anythingExistsInStorage = useMemo(async () => {
    const all = (await Promise.all([getAllFiles(), getAllWorkspaces()])).flat();
    return !!all.length;
  }, [getAllFiles, getAllWorkspaces]);

  const { audioUrl, midiUrl, midizUrl } = fileUploaderProps;

  const anyUrlsSet = useMemo(async () => {
    return !![audioUrl, midiUrl, midizUrl].filter(Boolean).length;
  }, [audioUrl, midiUrl, midizUrl]);

  const [activeStep, setActiveStep] = useState(0);
  const fileUploader = useMemo(() => {
    const haveAudio = !!audioUrl;
    const haveMidi = !!midiUrl;
    const haveMidiz = !!midizUrl;
    const initialStep =
      haveAudio && haveMidi && haveMidiz ? 3 : haveAudio && haveMidi ? 2 : haveAudio ? 1 : 0;
    setActiveStep(initialStep);
    const props = {
      ...fileUploaderProps,
      audioUrl,
      midiUrl,
      midizUrl,
      activeStep,
      setActiveStep,
    };

    return <FileUploader {...props} />;
  }, [audioUrl, midiUrl, midizUrl, activeStep, setActiveStep]);

  const [showInstructions, setShowInstructions] = useState(true);

  useEffect(() => {
    const check = async () => {
      const wss = await getAllWorkspaces();
      setShowInstructions(!wss.length);
    };
    check();
  }, [getAllWorkspaces]);

  return (
    <>
      <Typography variant="h6">Upload files</Typography>
      <Typography variant="body1">Upload some files to get started...</Typography>
      {showInstructions && (
        <>
          <Typography variant="body1" sx={{ mt: 1 }} component="div">
            It seems like you're new so here are some extra instructions you'll only see once...
            <Box sx={{ ml: 3, border: "1px solid #ccc", backgroundColor: "#fffde7", padding: 2 }}>
              <Typography variant="body1" sx={{ mt: 1 }} component="div">
                An audio and midi file, both starting with <em>branching</em> have been added to
                play around with. So, to walk you through the process:
                <ol>
                  <li>
                    <Typography variant="body1">
                      Choose an audio file by uploading one or selecting the file we added from the
                      drop-down.
                    </Typography>
                  </li>
                  <li>
                    <Typography variant="body1">
                      Choose a MIDI file by uploading one or selecting the file we added from the
                      drop-down.
                    </Typography>
                  </li>
                  <li>
                    <Typography variant="body1">
                      Click <b>CREATE</b> to generate an animation config from the MIDI file and
                      click <b>NEXT</b> to choose this file.
                    </Typography>
                  </li>
                  <li>
                    <Typography variant="body1">
                      Click <b>START ANIMATING</b> to begin...
                    </Typography>
                  </li>
                </ol>
                The next time you'll see these files show up by default and all in a "workspace"
                drop-down. You also won't see this message.
              </Typography>
            </Box>
          </Typography>
        </>
      )}
      <Box sx={{ mt: 2 }}>{fileUploader}</Box>
      <Box sx={{ mt: 2 }}></Box>
      <Box sx={{ mt: 2 }}></Box>
      <Divider />
      <Box
        sx={{
          mt: 3,
        }}
      >
        <Stack direction="row" spacing={1}>
          <Button onClick={handleReset} disabled={!anythingExistsInStorage} variant="contained">
            Reset
          </Button>
          <Button onClick={handleClear} disabled={!anyUrlsSet} variant="contained" color="error">
            Clear storage
          </Button>
        </Stack>
      </Box>
      <Box sx={{ mt: 2 }}>
        <Link href="/#/privacy">Privacy notice</Link>
      </Box>
    </>
  );
};

export default FileUploadPanel;
