import React from "react";
import { Key as BaseKey, Pref, Prefs, ValidType } from "@/lib/prefs";

type Keys = "viewMode" | "editorViewMode" | "leftPanelMode" | "showTrackDetails";

type Key = BaseKey & Keys;

const defaults = [
  {
    key: "viewMode",
    defaultValue: "laptop",
  },
  {
    key: "showTrackDetails",
    defaultValue: false,
  },
  {
    key: "editorViewMode",
    defaultValue: "tree",
    enumValues: ["tree", "view", "form", "code", "text"],
  },
  {
    key: "leftPanelMode",
    defaultValue: "normal",
    enumValues: ["small", "normal", "wide"],
  },
] as Pref<Key, ValidType>[];

const prefs = new Prefs<Key>(defaults);

export default prefs;
