import React from "react";
import { useEffect, useRef } from "react";

// Make this an internal import
import Two from "two.js";

const useTwo = <T extends HTMLElement>(
  opts?: {
    f?: (two: Two, el: T) => void;
    noExpand?: boolean;
  },
  fDeps?: React.DependencyList
) => {
  const { f, noExpand } = opts || {};

  const twoRef = useRef<Two>(
    new Two({
      type: Two.Types.canvas,
    })
  );
  const elRef = useRef<T>();

  useEffect(() => {
    if (!noExpand) {
      elRef.current.style.width = "100%";
      elRef.current.style.height = "100%";
    }
  }, [noExpand, twoRef.current]);

  useEffect(() => {
    if (!twoRef.current) return;
    if (!elRef.current) return;

    twoRef.current.appendTo(elRef.current);
    twoRef.current.width = elRef.current.clientWidth;
    twoRef.current.height = elRef.current.clientHeight;

    if (f) f(twoRef.current, elRef.current);
  }, [...(fDeps || [])]);

  return {
    two: twoRef,
    divElm: elRef,
  };
};

export default useTwo;
