import React from "react";
import { Region } from "@/components/WavesurferAudioPlayer";
import {
  logicTrackTextColor,
  logicTrackBackgroudnColor,
  previewRegionTextColor,
  previewRegionBackgroundColor,
} from "@/lib/colors";
import { CombinedTrack, GroupedTrack } from "@/lib/types/editor";
import formatDuration from "format-duration";
import { useCallback, useEffect, useState } from "react";

interface Props {
  selectedTrack: GroupedTrack | null;
  tracks: GroupedTrack[];
}

const useRegions = (props: Props) => {
  const { selectedTrack, tracks } = props;

  const [regions, setRegions] = useState<Region[]>([]);
  const [previewedTracks, setPreviewGroupedTrackNums] = useState<number[]>([]);

  useEffect(() => {
    const regions = [];
    if (selectedTrack) {
      const selectedRegions = selectedTrack?.tracks.map((track) => {
        return createRegion({
          track,
          textColor: logicTrackTextColor,
          backgroundColor: logicTrackBackgroudnColor,
        });
      });
      regions.push(...selectedRegions);
    }
    previewedTracks.forEach((trackNum, i) => {
      const track = tracks[trackNum];
      const previewRegions = track.tracks.map((track) => {
        return createRegion({
          track,
          textColor: previewRegionTextColor,
          backgroundColor: previewRegionBackgroundColor,
        });
      });
      regions.push(...previewRegions);
    });
    setRegions(regions);
  }, [selectedTrack, previewedTracks]);

  const previewTrack = useCallback(
    (trackNum: number, newVal: boolean) => {
      console.log("previewAnimation", trackNum, newVal);
      setPreviewGroupedTrackNums((prev) => {
        return newVal ? [...prev, trackNum] : prev.filter((index) => index !== trackNum);
      });
    },
    [setPreviewGroupedTrackNums]
  );

  const createRegion = (props: {
    track: CombinedTrack;
    textColor: string;
    backgroundColor: string;
    row?: number;
  }) => {
    const { track, textColor, backgroundColor, row = 0 } = props;
    const dur = formatDuration((track.duration - track.minTimeSecs) * 1000);
    const top = "10"; // ???
    const html = `
      <div style="position: relative; top: ${top}; font-weight: normal; font-size: .7em; color: ${textColor} !important; background-color: ${backgroundColor};">
        <div style="margin: 3px">
          <b style="font-weight: 900">${track.name}</b> - <em>${dur}</em>
        </div>
      </div>
      `;
    const content = document.createElement("div");
    content.innerHTML = html;
    return {
      id: `${track.name}-${track.index}`,
      start: track.minTimeSecs,
      end: track.maxTimeSecs,
      color: "rgb(0,0,0,0)", //backgroundColor,
      content,
      drag: false,
      data: {
        systemRegionId: -1, // Set this so we don"t re-add this
      },
    };
  };

  return {
    previewTrack,
    previewedTracks,
    regions,
    setRegions,
  };
};

export default useRegions;
