import React from "react";
import { Link } from "@mui/material";
import { QRCodeSVG } from "qrcode.react";

interface QrCodeProps {
  hostId: string;
  hostName?: string;
}

const getPublicUrl = () => {
  if (process.env.NODE_ENV === "development") {
    // Need to link to local IP rather than localhost
    return "http://192.168.1.157:3000/midiz";
  }
  const publicUrl = process.env.PUBLIC_URL;
  if (publicUrl.startsWith("/")) {
    return document.location.origin + publicUrl;
  }
  return publicUrl;
};

const QrCode = (props: QrCodeProps) => {
  const { hostId, hostName } = props;
  const path = `${getPublicUrl()}/#/guest`;
  const url = path + `?hostId=${hostId}&hostName=${hostName}`;
  return (
    <Link href={url} target={"_blank"}>
      <QRCodeSVG width={"100%"} height={"100%"} value={`${url}`} />
    </Link>
  );
};

export default QrCode;
