import React from "react";
import {
  Box,
  Typography,
  Grid,
  FormControl,
  MenuItem,
  Select,
  SelectChangeEvent,
} from "@mui/material";
import { useCallback } from "react";
import { Setter } from "@/lib/types/util";
import { ResolvedWorkspace } from "@/lib/types/files";

const WorkspaceChooser = (props: {
  workspaces: ResolvedWorkspace[];
  selected: ResolvedWorkspace | null;
  setSelected: Setter<ResolvedWorkspace | null>;
  showTemplateItems?: boolean;
}) => {
  const { workspaces, selected, setSelected, showTemplateItems = false } = props;

  const handleChange = useCallback(
    async (event: SelectChangeEvent<number>) => {
      const ws = workspaces.find((f) => f.key === event.target.value);
      setSelected(ws || null);
    },
    [workspaces, setSelected]
  );

  const formatDate = (iso: string) => {
    if (!iso) return "";
    // If it's today, return the time
    const date = new Date(iso);
    const now = new Date();
    if (date.toDateString() === now.toDateString()) {
      return date.toLocaleTimeString();
    }
    return date.toISOString().split("T")[0];
  };

  return (
    <>
      <Box component="span" sx={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
        <FormControl fullWidth size="small">
          <Select
            id="workspace-chooser"
            value={selected?.key ?? ""}
            onChange={handleChange}
            displayEmpty
          >
            <MenuItem value={""}>--</MenuItem>
            {workspaces.map((ws) => (
              <MenuItem
                key={`workspace-chooser-menu-item=${ws.key}`}
                value={ws.key}
                sx={{ minWidth: 300 }}
                title={`date: ${ws.createdISO} audio: ${ws.audio.idForDebugging} midi: ${ws.midi.idForDebugging} midiz: ${ws.midiz.idForDebugging} template: ${ws.creationTemplate}`}
              >
                <Grid container sx={{ width: "100%" }}>
                  <Grid item md={1}>
                    <Typography variant="subtitle2">#{ws.id}</Typography>
                  </Grid>
                  <Grid item md={2}>
                    <Typography variant="body2">{formatDate(ws.createdISO)}</Typography>
                  </Grid>
                  <Grid item md={3}>
                    <Typography variant="body2">{ws.audio.name}</Typography>
                  </Grid>
                  <Grid item md={3}>
                    <Typography variant="body2">{ws.midi.name}</Typography>
                  </Grid>
                  <Grid item md={3}>
                    <Typography variant="body2">{ws.midiz.name}</Typography>
                  </Grid>
                  <Grid item md={1}>
                    {showTemplateItems ? (
                      <Typography variant="body2">
                        {ws.creationTemplate ? `[${ws.creationTemplate}]` : "none"}
                      </Typography>
                    ) : null}
                  </Grid>
                </Grid>
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      </Box>
    </>
  );
};

export default WorkspaceChooser;
