import React from "react";
import { forwardRef, useCallback, useEffect, useMemo, useState } from "react";

import {
  IconButton,
  List,
  ListItem as MuiListItem,
  ListItemSecondaryAction, Box,
  ListItemText,
  Tooltip,
  ListItemAvatar
} from "@mui/material";
import { GroupedTrack } from "@/lib/types/editor";
import TrackDetails from "./TrackDetails";
import { styled } from "@mui/material/styles";
import hacks, { verboseTrackList } from "@/lib/hacks";
import * as flags from "@/lib/flags";
import { MidiAnimation, ViewMode } from "@/lib/types/main";
import { MutRaf } from "@/lib/types/util";
import { ListItemProps } from "@mui/material/ListItem";
import EditIcon from "@mui/icons-material/Edit";
import EditNoteIcon from "@mui/icons-material/EditNote";
import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import VolumeUpIcon from "@mui/icons-material/VolumeUp";
import VolumeMuteIcon from "@mui/icons-material/VolumeMute";
import {
  logicTrackBackgroudnColor,
  logicTrackMuteBackgroundColor,
  logicTrackSoloBackgroundColor,
  previewRegionBackgroundColor,
  verboseBackgroundColor,
} from "../../lib/colors";
import LetterCheckBox from "../LetterCheckBox";
import useAnimationSample from "@/hooks/useAnimationSample";

export type EditSurface = "dialog" | "inline" | "drawer" | "complexDrawer";

type ActiveEditSurface = { id: string, editSurface: EditSurface };
interface TrackListProps {
  tracks: GroupedTrack[];
  animations: MutRaf<MidiAnimation[]>;
  selectedTrack: number;
  setSelectedTrack: (track: number) => void;
  edit: (index: number, surface: EditSurface) => void;
  doneEdit: () => void;
  mute: (id: string, muted: boolean) => void;
  solo: (id: string, soloed: boolean) => void;
  preview: (index: number, previewed: boolean) => void;
  verbose: (id: string, val: boolean) => void;
  changeAnimationType: (index: number, type: string) => void;
  mutedAnimationIds: string[];
  soloedAnimationIds: string[];
  activeTrackNums: number[];
  previewTrackNums: number[];
  verboseAnimationIds: string[];
  activeEditSurfaceAnimationIds: ActiveEditSurface[];
  onlyShowActive: boolean;
  showTrackDetails: boolean;
  viewMode: ViewMode;
  height: number;
}

const TrackList = (props: TrackListProps) => {
  const {
    tracks,
    animations,
    selectedTrack,
    setSelectedTrack,
    edit,
    doneEdit,
    mute,
    solo,
    preview,
    verbose,
    changeAnimationType,
    mutedAnimationIds,
    soloedAnimationIds,
    activeTrackNums,
    previewTrackNums,
    verboseAnimationIds,
    activeEditSurfaceAnimationIds,
    onlyShowActive,
    showTrackDetails,
    viewMode,
    height,
  } = props;

  if (verboseTrackList) console.log("TrackList render");

  const handleSingleSelection = useCallback(
    (value: number) => {
      setSelectedTrack(value);
    },
    [setSelectedTrack]
  );

  const [expanded, setExpanded] = useState<number | false | undefined>();

  const handleExpandClick = (panel: number) => {
    console.log("handleExpandClick");
    setExpanded((prev) => (prev === panel ? false : panel));
  };

  useEffect(() => {
    if (expanded === undefined) return;
    if (expanded === false) {
      console.log("doneEdit");
      doneEdit();
      return;
    }
    edit(expanded, "inline");
  }, [expanded]);

  const TrackListItem = (props: {
    track: GroupedTrack;
    muted?: boolean;
    soloed?: boolean;
    active?: boolean;
    previewed: boolean;
    verbosed: boolean;
    activeEditSurface: EditSurface | undefined;
  }) => {
    const { track, muted, soloed, previewed, active, verbosed, activeEditSurface, } = props;

    const i = track.index;

    const { selected, animation } = useMemo(() => {
      if (!animations.current) {
        return {
          selected: false,
          animation: {
            id: "",
            name: "",
            mute: false,
            solo: false,
            preview: false,
            verbose: false,
            origin: { x: 0, y: 0 },
            drawSample: undefined,
          },
        };
      }
      const selected = selectedTrack === i;
      const anim = (animations.current ?? [])[i] || null;
      const animation = {
        id: anim?.id ?? "",
        name: anim?.name ?? "",
        mute: muted,
        solo: soloed,
        preview: previewed,
        verbose: verbosed,
        origin: anim?.options.origin ?? { x: 0, y: 0 },
        drawSample: anim?.drawSample,
      };
      return {
        selected,
        animation,
      };
    }, [i, selectedTrack, animations.current]);

    const iconSize = 40;

    const { divElm } = useAnimationSample({
      animation,
      width: iconSize,
      height: iconSize,
    });

    const MyListItemAvatar = forwardRef<HTMLDivElement>((props, ref) => {
      return (
        <ListItemAvatar {...props}>
          <div
            ref={ref}
            style={{
              width: iconSize,
              height: iconSize,
            }}
          />
        </ListItemAvatar>
      );
    });

    const [EditDrawerIcon, EditComplexDrawerIcon, EditDialogIcon] =
      flags.showDialogEditInTrackList
        ? [EditNoteIcon, EditIcon, OpenInNewIcon]
        : flags.showInlineEditInTrackList
          ? [EditIcon, OpenInNewIcon, null]
          : [null, EditIcon, null];

    return (
      <ListItem
        key={`track-${i}`}
        onClick={selected ? undefined : () => handleSingleSelection(track.index)}
        sx={{
          cursor: selected ? "initial" : "pointer",
          backgroundColor: selected ? "rgba(0, 0, 0, .1)" : undefined,
        }}
        dense
      >
        <MyListItemAvatar ref={divElm} />
        <ListItemText>
          <TrackDetails
            track={track}
            animation={animation}
            onAnimationTypeChange={(name) => changeAnimationType(i, name)}
            showDetails={showTrackDetails}
          />
        </ListItemText>
        {/*
  TODO: PUt these on the next line when the screen is small
  */}
        <ListItemSecondaryAction>
          <Box
            sx={{ display: "flex", alignItems: "center", flexDirection: "row", p: 0, pl: 1, pr: 1 }}
          >
            {flags.showInlineEditInTrackList ?
              <Tooltip title={"Edit as text"}>
                <IconButton
                  size={"small"}
                  sx={{ m: 0, mr: 0.5, p: 0 }}
                  onClick={(e) => {
                    e.preventDefault();
                    edit(i, "drawer");
                  }}
                >
                  <EditDrawerIcon fontSize="small" />
                </IconButton>
              </Tooltip>
              : undefined}
            <Tooltip title={"Edit"}>
              <IconButton
                size={"small"}
                sx={{ m: 0, mr: 0.5, p: 0 }}
                onClick={(e) => {
                  e.preventDefault();
                  edit(i, "complexDrawer");
                }}
              >
                <EditComplexDrawerIcon
                  // XXX:Get this to look like LetterCheckBox
                  sx={{ borderRadius: 2, m: 0, p: 0, width: 25, height: 25, backgroundColor: activeEditSurface ? "#d5b60A" : "inherit" }}
                  htmlColor={activeEditSurface ? "#fff" : "inherit"}
                  fontSize="small"
                />
              </IconButton>
            </Tooltip>
            {flags.showDialogEditInTrackList ? <Tooltip title={"Open"}>
              <IconButton
                size={"small"}
                sx={{ m: 0, p: 0 }}
                onClick={(e) => {
                  e.preventDefault();
                  edit(i, "dialog");
                }}
              >
                <EditDialogIcon fontSize="small" />
              </IconButton>
            </Tooltip>
              : undefined}
            <LetterCheckBox
              letter="M"
              title="Mute"
              checked={muted}
              checkedColor={logicTrackMuteBackgroundColor}
              onChange={() => mute(animation.id, !muted)}
              sx={{ mx: 0.05 }}
            />
            <LetterCheckBox
              letter="S"
              title="Solo"
              checkedColor={logicTrackSoloBackgroundColor}
              onChange={() => solo(animation.id, !soloed)}
              checked={soloed}
              sx={{ mx: 0.05 }}
            />
            <LetterCheckBox
              letter="P"
              title="Preview"
              checkedColor={previewRegionBackgroundColor}
              onChange={() => preview(track.index, !previewed)}
              checked={previewed}
              disabled={selected}
              sx={{ mx: 0.05 }}
            />
            <LetterCheckBox
              letter="V"
              title="Verbose"
              checkedColor={verboseBackgroundColor}
              onChange={() => verbose(animation.id, !verbosed)}
              checked={verbosed}
              sx={{ mx: 0.05 }}
            />
            {flags.showActiveIcon &&
              (active ? (
                <VolumeUpIcon
                  fontSize="small"
                  sx={{
                    color: logicTrackBackgroudnColor,
                  }}
                />
              ) : (
                <VolumeMuteIcon fontSize="small" />
              ))}
          </Box>
        </ListItemSecondaryAction>
      </ListItem>
    );
  };

  const filteredTracks = useMemo(() => {
    if (!onlyShowActive) return tracks;
    return tracks.filter((t) => activeTrackNums.includes(t.index));
  }, [onlyShowActive, tracks, activeTrackNums]);

  return (
    <>
      <List
        sx={{
          overflow: "auto",
          height: height - 48,
          p: 0,
          m: 0,
        }}
      >
        {filteredTracks.map((track) => {
          const anim = (animations.current ?? [])[track.index]!;
          if (!anim) return;
          const muted = mutedAnimationIds.includes(anim.id);
          const soloed = soloedAnimationIds.includes(anim.id);
          const active = activeTrackNums.includes(track.index);
          const previewed = previewTrackNums.includes(track.index);
          const verbosed = verboseAnimationIds.includes(anim.id);
          const activeEditSurface = activeEditSurfaceAnimationIds.find(({ id, editSurface }) => id === anim.id)?.editSurface;
          return (
            <TrackListItem
              key={`TrackListItem-${track.index}`}
              track={track}
              muted={muted}
              soloed={soloed}
              active={active}
              previewed={previewed}
              verbosed={verbosed}
              activeEditSurface={activeEditSurface}
            />
          );
        })}
      </List>
    </>
  );
};

// Make the list item a little more like an accordion
// https://mui.com/material-ui/react-accordion/#customization
const ListItem = styled((props: ListItemProps) => <MuiListItem {...props} />)(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "dark" ? "rgba(255, 255, 255, .05)" : "rgba(0, 0, 0, .03)",
  border: `1px solid ${theme.palette.divider}`,
  "&:not(:last-child)": {
    borderBottom: 0,
  },
  "&::before": {
    display: "none",
  },
}));

export default TrackList;
