import React from "react";
﻿// TODO: Remove wavd
import { useEffect } from "react";
import { registerDemoAnimations } from "../../midi/demo";

// TODO: This dpoesn't work

function Play() {
  // TODO: Remove wavd
  // const data = useWavejs();
  const { wave, audioElm, canvasElm, registry } = {
    wave: null,
    audioElm: null,
    canvasElm: null,
    registry: null,
  };

  useEffect(() => {
    if (!wave) return;
    if (!registry) return;

    const init = async () => {
      const source = await wave.addPlugin(registry, { eagerInit: true });

      registerDemoAnimations(source, registry);
    };
    init();
  }, [registry, wave]);

  return (
    <main className={"p-home"}>
      <section className={"__display"}>
        <div className={"__canvas"}>
          <canvas ref={canvasElm}></canvas>
        </div>
      </section>
      <audio
        controls
        src={`${process.env.PUBLIC_URL}/assets/nogo.mp3`}
        ref={audioElm}
        style={{
          width: "98%",
          margin: "auto",
        }}
      ></audio>
    </main>
  );
}

export default Play;
