import React from "react";
import { useState, useCallback, useEffect } from "react";
import { useSearchParams } from "react-router-dom";
import useFiles from "./useFiles";
import { NamedUrl } from "@/lib/types/load";

interface Result {
  urls?: {
    audioUrl: NamedUrl;
    midiUrl: NamedUrl;
    midizUrl: NamedUrl;
    workspaceId: number;
    createdISO: string;
  };
  found: boolean;
  description: string;
  error?: string;
}

const safeParseInt = (s: string) => {
  const n = parseInt(s);
  return isNaN(n) ? undefined : n;
};

const useWorkspaceFromSearchParams = () => {
  const files = useFiles();
  const [searchParams, setSearchParams] = useSearchParams();

  const [workspaceId, setWorkspaceId] = useState<number | undefined>();
  const [result, setResult] = useState<Result | undefined>();

  useEffect(() => {
    const find = async () => {
      if (!searchParams) return;
      const ws = searchParams.get("ws");
      if (ws === null || ws === undefined) {
        return;
      }
      const workspaceId = safeParseInt(ws);
      if (workspaceId === undefined) {
        return;
      }

      const workspace = await files.getWorkspaceById(workspaceId);
      const { audio, midi, midiz } = await files.getWorkspace(workspace);
      return {
        audioUrl: audio,
        midiUrl: midi,
        midizUrl: midiz,
        workspaceId,
        createdISO: workspace.created,
      };
    };
    find()
      .then((res) => {
        if (!res) {
          setResult({
            description: "No workspace found",
            found: false,
          });
          return;
        }
        setResult({
          urls: res,
          found: true,
          description: `Loaded from workspace #${res.workspaceId}`,
        });
      })
      .catch((e) => {
        console.error("Error loading workspace from params", e);
        setResult({
          description: "Error loading workspace from params",
          found: false,
          error: e.message,
        });
      });
  }, [setResult]);

  useEffect(() => {
    const { urls } = result || {};
    if (!urls) return;
    const { workspaceId } = urls;
    setSearchParams((prev) => {
      const ws = workspaceId ? workspaceId.toString() : "demo";
      const sp = new URLSearchParams(prev);
      sp.set("ws", ws);
      return sp;
    });
  }, [result, setSearchParams]);

  return result;
};

export default useWorkspaceFromSearchParams;
