import Two from "two.js";

interface Options {
  verbose?: boolean;
  debugJSON?: boolean;
  debugTracks?: boolean;
  debugNotes?: boolean;
  debugEachNotes?: boolean;
  noOriginal?: boolean;
}

export const createMidi = (input: ArrayBuffer, opts?: Options): Midi => {
  const { noOriginal = false } = opts || {};

  const midi = new ToneJSMidi(input);
  const midiJSON = midi.toJSON();

  const tracks = midiJSON.tracks.map((track, i) => {
    const newNotes = track.notes.map((note) => {
      return {
        ...note,
        trackRef: i,
      };
    }) as Note[];
    const summary = {
      noteCount: newNotes.length,
      minTime: newNotes.reduce((acc, note) => Math.min(acc, note.time), Infinity),
      maxTime: newNotes.reduce((acc, note) => Math.max(acc, note.time), -Infinity),
      instrumentName: track.instrument.name,
      noteSet: Array.from(new Set(newNotes.map((note) => note.midi))).sort(),
    };
    const _original = { ...track } as TrackJSON;
    delete _original.notes;
    return {
      ...track,
      index: i,
      notes: newNotes,
      instrumentRef: track.instrument.number,
      summary,
      _original: noOriginal ? undefined : _original,
    };
  }) as Track[];

  const instrumentJSONs = midi.tracks.map((track) => track.instrument.toJSON());
  const instrumentMap = Object.fromEntries(
    instrumentJSONs.map((instrument, i) => [instrument.number, instrument])
  );
  const instruments = Object.values(instrumentMap) as Instrument[];

  const res = {
    tracks,
    instruments,
    _original: noOriginal ? undefined : midiJSON,
  } as Midi;

  return res;
};

export const getNotesFromSource = (source: Midi, props: GetNotesProps) => {
  const { startMillis, durationMillis, criteria } = props;

  if (criteria.trackRef === undefined) return;

  const { trackRef } = criteria;

  const selTracks = source.tracks.filter((track) => {
    return track.index === trackRef || track.name === trackRef;
  });

  const notes = selTracks.flatMap((track) => {
    return track.notes.filter((note) => {
      const noteStartMillis = note.time * 1000;
      const noteEndMillis = noteStartMillis + note.duration * 1000;
      const endMillis = startMillis + durationMillis;
      if (noteEndMillis < startMillis) return false;
      if (noteStartMillis > endMillis) return false;
      return true;
    });
  });
  return notes;
};

export interface MidiNotesSource {
  getNotes: (props: GetNotesProps) => Note[];
  source: Midi;
}

export const createMidiNoteSource = async (input: ArrayBuffer, opts?: Options) => {
  const { debugJSON, debugTracks = false, debugNotes, debugEachNotes } = opts || {};

  const source = createMidi(input, opts);

  if (debugJSON) {
    console.log("Printing JSON");
    console.log(JSON.stringify(source, null, 2));
  }
  if (debugTracks) {
    console.log("Printing tracks");
    source.tracks.forEach((track, i) => {
      console.log(` - ${i}: ${track.name} - ${JSON.stringify(track.summary)}`);
      if (debugNotes) {
        track.notes.forEach((note) => {
          console.log("   - ", formatDuration(note.time * 1000), note);
        });
      }
    });
  }

  const getNotes = (props: GetNotesProps) => {
    const { startMillis, durationMillis, criteria } = props;
    const notes = getNotesFromSource(source, { startMillis, durationMillis, criteria });
    if (debugEachNotes) {
      const selTracks = Array.from(new Set(notes.map((n) => n.trackRef)));
      console.log(
        "startMillis",
        startMillis,
        "durationMillis",
        durationMillis,
        "selTracks",
        selTracks,
        "notes",
        notes
      );
    }
    return notes;
  };

  return { getNotes, source } as MidiNotesSource;
};

interface FlatTrackNote {
  midi: number;
  time: number;
  duration: number;
  velocity: number;
}

interface FlatTrack {
  notes: FlatTrackNote[];
}

export const flattenTrack = (track: GroupedTrack): FlatTrack => {
  const notes = track.tracks.flatMap((t) => t.tracks.flatMap((n) => n.notes));
  const flatTrack = { notes };
  return flatTrack;
};

export const createMidiAnimationInitData = (track: FlatTrack): IMidiAnimationInitData => {
  const notesMap = {} as Record<number, FlatTrackNote[]>;
  for (const note of track.notes) {
    const { midi } = note;
    if (!notesMap[midi]) {
      notesMap[midi] = [];
    }
    notesMap[midi].push(note);
  }

  const numberStats = (values: number[]) => {
    const count = values.length;
    const min = Math.min(...values);
    const max = Math.max(...values);
    const total = values.reduce((acc, val) => acc + val, 0);
    const mean = total / count;
    const sorted = values.sort((a, b) => a - b);
    const median =
      count % 2 === 0 ? (sorted[count / 2 - 1] + sorted[count / 2]) / 2 : sorted[(count - 1) / 2];
    return { count, min, max, mean, median, total };
  };

  const noteStats = Object.entries(notesMap).map(([midi, notes]) => {
    const times = notes.map((note) => note.time);
    const time = numberStats(times);
    const velocities = notes.map((note) => note.velocity);
    const velocity = numberStats(velocities);
    const durations = notes.map((note) => note.duration);
    const duration = numberStats(durations);
    const stats = {
      time,
      velocity,
      duration,
    };
    return {
      midi: parseInt(midi),
      stats,
    };
  });
  const noteSet = Array.from(new Set(track.notes.map((note) => note.midi)));
  const width = 1000;
  const height = 1000;
  return {
    noteStats,
    noteSet,
    width,
    height,
  };
};
