import {
  IMidiAnimationDrawData,
  Note
} from "@/lib/types/main";
import { BaseUtil } from "@/lib/midi/animations/base";
import { Options } from "./Stars";
export class StarsUtil extends BaseUtil<Options> {
  public static NAME = "stars";

  public static getInstance = () => new StarsUtil();

  protected doDrawSample(options: Options, data: IMidiAnimationDrawData) {
    const { two, width, height } = data;
    const { lineWidth, fillColor, lineColor } = options;
    const { x, y } = { x: width / 2, y: height / 2 };
    const fill = this.sampleOpacity(fillColor);
    const ir = Math.min(width, height) / 4;
    const or = Math.min(width, height) / 2;
    const sides = options.sides;
    const star = this.makeStar(two, x, y, ir, or, sides, {
      fill,
      linewidth: lineWidth,
      stroke: lineColor,
    });
    this.log("star drawSample", star);
  }

  protected doDraw(options: Options, notes: Note[], data: IMidiAnimationDrawData) {
    const { two, width, height } = data;

    const { scale, fillColor, lineColor } = options;

    const { x, y } = this.getOrigin(options.origin, width, height);

    notes.forEach((note) => {
      const linewidth = this.scale(options.lineWidth) * note.velocity;
      const nudge = note.midi * this.scale(scale);
      const ir = this.scale(options.ir) + nudge;
      const or = this.scale(options.or) + nudge;
      const sides = options.sides;
      const star = this.makeStar(two, x, y, ir, or, sides, {
        fill: fillColor,
        linewidth,
        stroke: lineColor,
      });
      this.log("star draw", star);

    });
  }
}
