import React from "react";
import { MidiAnimation, Point } from "@/lib/types/main";
import Circles from "../animations/Circles";

const rand = (max: number) => Math.round(Math.random() * max);

const maxDiameter = 15;
const maxScale = 5;

export const createCircles = (
  track: { name: string },
  origin: { x: number; y: number }
): MidiAnimation => {
  const { name: trackName } = track;
  const scale = maxScale / 3 + rand((2 * maxScale) / 3);
  const lineWidth = 3 + rand(5);
  const diameter = maxDiameter / 3 + rand((2 * maxDiameter) / 3);
  const randomColor = () => {
    const r = rand(255);
    const g = rand(255);
    const b = rand(255);
    return `rgb(${r},${g},${b},.3)`;
  };
  // Make sure it's on the screen
  const radius = diameter / 2;
  if (origin.x - radius <= 0) origin.x += maxDiameter * maxScale;
  if (origin.y - radius <= 0) origin.y += maxDiameter * maxScale;
  return new Circles(
    { trackRef: trackName },
    {
      lineColor: randomColor(),
      fillColor: randomColor(),
      lineWidth,
      diameter,
      origin,
      scale,
    }
  );
};

export const createOrigins = <T extends { name: string }>(tracks: T[]): Required<Point>[] => {
  const startX = maxDiameter * maxScale + 50,
    endX = 800;
  const startY = maxDiameter * maxScale + 50,
    endY = 800;
  const trackNames = Array.from(new Set(tracks.map((track) => track.name)));
  const xs = trackNames.map((trackName, i) => {
    const x = startX + ((endX - startX) * i) / trackNames.length;
    return x;
  });
  const ys = trackNames.map((trackName, i) => {
    const y = startY + ((endY - startY) * i) / trackNames.length;
    return y;
  });
  const origins = xs.flatMap((x) => ys.map((y) => ({ x, y })));
  // Randomize the order
  origins.sort(() => Math.random() - 0.5);

  return origins;
};
