import React from "react";
import { useEffect, useState } from "react";

import { MidiAnimation } from "@/lib/types/main";
import useTwo from "@/hooks/useTwo";
import Two from "two.js";


type DrawsSample = Pick<MidiAnimation, "drawSample">;

const useAnimationSample = (props: {
  animation: DrawsSample | null;
  width: number;
  height: number;
}) => {
  const { animation, width, height } = props;
  // TODO: This should be a forward ref instead.
  const { two, divElm } = useTwo<HTMLDivElement>({ noExpand: true }, [
    animation,
  ]);

  useEffect(() => {
    if (!divElm.current) return;
    if (!animation?.drawSample) return;
    if (!two.current) return;
    // @ts-ignore
    two.current.clear();
    animation.drawSample({
      canvas: null,
      canvasElement: null,
      width,
      height,
      two: two.current,
    });
    // @ts-ignore
    two.current.update();
  }, [divElm.current, animation?.drawSample]);

  return {
    divElm,
  };
};

export default useAnimationSample;
