import { CirclesUtil as Circles } from "./CirclesUtil";
import { CubesUtil as Cubes } from "./CubesUtil";
import { RectsUtil as Rects } from "./RectsUtil";
import { StarsUtil as Stars } from "./StarsUtil";

const all = {
  [Circles.NAME]: Circles.getInstance(),
  [Cubes.NAME]: Cubes.getInstance(),
  [Rects.NAME]: Rects.getInstance(),
  [Stars.NAME]: Stars.getInstance(),
};

export const getDrawUtil = (name: string) => {
  const util = all[name];
  if (!util) {
    throw new Error(`Unknown draw util: ${name}`);
  }
  return util;
};