const useRandomName = () => {
  const randomName = () => {
    const names = [
      "Pickle Jar Hero",
      "The Great Potato",
      "Sexy Tofu",
      "Stained Undeeeez",
      "Loveable Misfit",
      "Overweight Unicorn",
      "Yellow Snowball",
      "Red Acorn Lover",
      "Blueberry Muffin",
      "Green Bean Machine",
      "Jose Wakefield III",
      "Hansel McBride",
      "Silly Goose",
      "Crazy Cat Lady",
      "Dancing Banana",
      "Mysterious Shadow",
      "Captain Crunch",
      "Rainbow Unicorn",
      "Moonlight Dreamer",
      "Sparkling Star",
      "Whispering Wind",
      "Giggling Gummy Bear",
      "Sneaky Ninja",
      "Bubblegum Popper",
      "Electric Eel",
      "Fluffy Cloud",
      "Golden Snitch",
      "Whirling Dervish",
      "Sunny Side Up",
      "Midnight Owl",
      "Charming Chimera",
      "Enigmatic Enigma",
    ];
    const name = names[Math.floor(Math.random() * names.length)];
    const number = Math.floor(Math.random() * 1000);
    return `${name} ${number}`;
  };

  return { randomName };
};

export default useRandomName;
