import React from "react";
import { IPlugin } from "@/lib/types/plugin";
import { MidiAnimation, IMidiAnimationRegistry, Midi } from "../lib/types/main";
import { BaseMidiAnimationRegistry, WavePluginProps } from "../lib/midi/registry_base";

class Del {
  private _animationsRef: React.MutableRefObject<MidiAnimation[]>;

  constructor(animationsRef: React.MutableRefObject<MidiAnimation[]>) {
    this._animationsRef = animationsRef;
  }

  public get animations() {
    return this._animationsRef.current;
  }

  public each(callback: (anim: MidiAnimation) => void) {
    for (const anim of this._animationsRef.current) {
      callback(anim);
    }
  }

  public addMidiAnimation(animation: MidiAnimation): void {
    this._animationsRef.current.push(animation);
  }

  public clearMidiAnimations(): void {
    this._animationsRef.current = [];
  }

  public addMidiAnimations(animations: MidiAnimation[]): void {
    this._animationsRef.current = animations;
  }
}

export class RefBasedMidiAnimationRegistry
  extends BaseMidiAnimationRegistry
  implements IPlugin<Midi | undefined>, IMidiAnimationRegistry
{
  public constructor(
    animationsRef: React.MutableRefObject<MidiAnimation[]>,
    props?: WavePluginProps
  ) {
    super(new Del(animationsRef), props);
  }

  public get name(): string {
    return "RefBasedMidiAnimationRegistry";
  }
}
