import React from "react";
import { FormControl, InputLabel, Select, MenuItem, Typography } from "@mui/material";
import { animationMetadata } from "../../lib/midi/lib";

const AnimationTypeSelector = (props: {
  animationName: string;
  onAnimationTypeChange: (name: string) => void;
}) => {
  const { animationName, onAnimationTypeChange } = props;
  return (
    <FormControl size="small" sx={{ m: 0, p: 0 }}>
      <InputLabel id="view-mode-select-label">Animation Type</InputLabel>
      <Select
        labelId="view-mode-select-label"
        id="view-mode-select"
        value={animationName}
        label="Animation Type"
        onChange={(e) => onAnimationTypeChange(e.target.value)}
        sx={{ textAlign: "center" }}
      >
        <MenuItem value="">
          <Typography variant="caption">
            (close)
          </Typography>
        </MenuItem>
        {animationMetadata.map((a, i) => (
          <MenuItem key={i} value={a.name}>
            <Typography variant="caption">
              {a.title}
            </Typography>
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
};

export default AnimationTypeSelector;
