import React from "react";
import { Ref } from "@/lib/types/util";
import { useEffect, useRef, useState } from "react";
import { v4 as uuid } from "uuid";
export const tickDurationMillis = 1000 / 60;

interface CallbackProps {
  audioBufferData: Uint8Array;
  currentTimeMillis: number;
  durationMillis: number;
}

interface Callback {
  tick: (props: CallbackProps) => void;
  onPlayStopped?: (reason: string, secs: number) => void;
  onPlayStarted?: (reason: string, secs: number) => void;
}

export default function useAudio(opts?: { callback?: Callback; ignoreCreateErrors?: boolean }) {
  const { callback, ignoreCreateErrors = false } = opts || {};

  const audioElm = useRef<HTMLAudioElement>();
  const [wrapper, setWrapper] = useState<AudioWrapper>();

  useEffect(() => {
    if (!audioElm.current) return;

    const wrapper = new AudioWrapper(audioElm.current, { ignoreCreateErrors });
    if (callback) {
      wrapper.setCallback(callback);
    }
    setWrapper(wrapper);
  }, [audioElm.current, setWrapper]);

  return {
    audioElm,
    wrapper,
  };
}

class AudioWrapper {
  private _audioElement: HTMLAudioElement;
  private _audioContext: AudioContext | null;
  private _audioSource: MediaElementAudioSourceNode | MediaStreamAudioSourceNode | null;
  private _audioAnalyser: AnalyserNode;
  private _interacted: boolean;

  private _isPlaying: boolean;
  private _callback: Callback;
  private _ignoreCreateErrors: boolean;

  private _id: string = uuid();

  public setCallback(callback: Callback) {
    if (this._callback) {
      // throw new Error("Callback already set");
      console.error("Callback already set");
    }
    this._callback = callback;
  }

  constructor(
    audioElement: HTMLAudioElement,
    opts: { ignoreCreateErrors: boolean },
    callback?: Callback
  ) {
    this._audioElement = audioElement;
    this._ignoreCreateErrors = opts.ignoreCreateErrors;

    const isSafari = /^((?!chrome|android).)*safari/i.test(navigator.userAgent);
    if (isSafari) {
      const events = ["touchstart", "touchmove", "touchend", "mouseup", "click"];
      events.forEach((event) => {
        document.body.addEventListener(event, () => this.start(), { once: true });
      });
    } else {
      const onPlayChanged = (reason: string, isPlaying?: boolean) => {
        if (isPlaying !== undefined) {
          this._isPlaying = isPlaying;
        }
        const fn = this._isPlaying ? this._callback?.onPlayStarted : this._callback?.onPlayStopped;
        fn && fn(reason, this._audioElement.currentTime);
      };
      this._audioElement.addEventListener("play", () => {
        onPlayChanged("play", true);
        this.start();
      });
      this._audioElement.addEventListener("pause", () => onPlayChanged("paused", false));
      this._audioElement.addEventListener("playing", () => onPlayChanged("playing", true));
      this._audioElement.addEventListener("seeked", () => onPlayChanged("seeked"));
      this._audioElement.addEventListener("seeking", () => onPlayChanged("seeking"));
      this._audioElement.addEventListener("ended", () => onPlayChanged("ended", false));
    }
  }

  public get id() {
    return this._id;
  }

  private connecting: boolean;

  private log = (...args: any[]) => {
    console.log(">>>>> AudioWrapper", ...args);
  };

  private start(): void {
    this.log("start");
    if (!this._interacted) {
      this.connecting = true;
      this._interacted = true;
      this._audioContext = new AudioContext();
      try {
        this._audioSource = this._audioContext.createMediaElementSource(this._audioElement);
        this._audioAnalyser = this._audioContext.createAnalyser();
        if (this._audioSource) {
          this._audioSource.connect(this._audioAnalyser);
          this._audioSource.connect(this._audioContext.destination);
        }
      } catch (e: any) {
        if (!this._ignoreCreateErrors) {
          throw e;
        }
        console.error(`createMediaElementSource: ${this._audioElement}`, e);
        return;
      }
      this._audioAnalyser.smoothingTimeConstant = 0.85;
      this._audioAnalyser.fftSize = 1024;
      this.connecting = false;
    }
    this.play();
  }

  private async play(): Promise<void> {
    this.log("play", this._isPlaying);
    if (this.connecting) {
      console.log("play called while still connecting");
      return;
    }

    const audioBufferData = new Uint8Array(this._audioAnalyser.frequencyBinCount);

    const tickDurationMillis = 1000 / 60;
    const tick = () => {
      const currentTimeMillis = this._audioElement.currentTime * 1000;
      this._audioAnalyser.getByteFrequencyData(audioBufferData);
      const props = {
        audioBufferData,
        currentTimeMillis,
        durationMillis: tickDurationMillis,
      };
      this._callback.tick(props);
      if (this._isPlaying) {
        window.requestAnimationFrame(tick);
      }
    };
    tick();
  }
}
