import { useState, useEffect } from "react";

export const tickDurationMillis = 1000 / 60;

interface CallbackProps {
  currentTimeMillis: number;
  durationMillis: number;
  isPlaying: boolean;
}

export interface TickCallback {
  tick: (props: CallbackProps) => void;
}

interface LastTime {
  dateTime: number;
  currentTimeMillis: number;
  ticksSinceSetting: number;
  reason: string;
}

class Wrapper {
  private _lastTime: LastTime = {
    dateTime: 0,
    currentTimeMillis: 0,
    ticksSinceSetting: 0,
    reason: "",
  };
  private _isPlaying: boolean = false;
  private _callback: TickCallback;

  public setCallback(callback: TickCallback) {
    if (this._callback) {
      // throw new Error("Callback already set");
      console.error("Callback already set");
    }
    this._callback = callback;
  }

  public get isPlaying() {
    return this._isPlaying;
  }

  public tick = () => {
    if (!this._callback) return;

    const currentTimeMillis = this._lastTime.currentTimeMillis;
    const props = {
      currentTimeMillis,
      durationMillis: tickDurationMillis,
      isPlaying: this._isPlaying,
    };
    this._callback.tick(props);
    this._lastTime.currentTimeMillis += tickDurationMillis;
    this._lastTime.ticksSinceSetting += 1;
    if (this._isPlaying) {
      window.requestAnimationFrame(this.tick);
    }
  };

  public setCurrentTimeMillis = (currentTimeMillis: number, playing: boolean, reason: string) => {
    // Calculate the skew
    const predictedCurrentTimeMillis =
      this._lastTime.currentTimeMillis + tickDurationMillis * this._lastTime.ticksSinceSetting;
    const skew = currentTimeMillis - predictedCurrentTimeMillis;
    const res = {
      skew,
      currentTimeMillis,
      predictedCurrentTimeMillis,
      ticksSinceSetting: this._lastTime.ticksSinceSetting,
      lastUpdateTimeMillis: this._lastTime.currentTimeMillis,
      wasPlaying: this._isPlaying,
      isPlaying: playing,
    };
    this._lastTime = {
      dateTime: Date.now(),
      currentTimeMillis,
      ticksSinceSetting: 0,
      reason,
    };
    const wasPlaying = this._isPlaying;
    this._isPlaying = playing;
    if (!wasPlaying && playing) {
      this.tick();
    }
    return res;
  };
}

export type TickManager = Wrapper;

export default function useLooper(): { wrapper: TickManager } {
  const [wrapper, setWrapper] = useState<Wrapper>();

  useEffect(() => {
    setWrapper(new Wrapper());
  }, []);

  return { wrapper };
}
