import React from "react";
import { Box, Button, ButtonGroup, Tooltip } from "@mui/material";
import PlayIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
import RestartAltIcon from "@mui/icons-material/RestartAlt";
import StopIcon from "@mui/icons-material/Stop";
import { FastForward, FastRewind } from "@mui/icons-material";

const AudioPlayerButtons = (props: {
  isLoaded: boolean;
  hasStarted: boolean;
  isPlaying: boolean;
  restart: () => void;
  rewind: () => void;
  stop: () => void;
  fastForward: () => void;
  play: () => void;
  color?: string;
}) => {
  const {
    isLoaded,
    hasStarted,
    isPlaying,
    restart,
    rewind,
    stop,
    fastForward,
    play,
    color = "rgba(0,0,0,.54)",
  } = props;

  const CtrlButton = (props: {
    onClick: () => void;
    disabled?: boolean;
    children: JSX.Element;
    tooltip: string;
  }) => {
    const { onClick, disabled = false, children, tooltip } = props;
    const w = "25px !important";
    const B = () => (
      <Button
        variant="text"
        size="small"
        sx={{ color: color, width: w, minWidth: w, maxWidth: w, borderRadius: 0 }}
        disabled={disabled}
        onClick={onClick}
      >
        {children}
      </Button>
    );
    if (disabled) return <B />;
    return (
      <Tooltip title={tooltip}>
        <B />
      </Tooltip>
    );
  };
  return (
    <Box sx={{ justifySelf: "flex-end", alignContent: "center", alignItems: "center" }}>
      <ButtonGroup>
        <CtrlButton tooltip={"Restart"} disabled={!isLoaded || !hasStarted} onClick={restart}>
          <RestartAltIcon />
        </CtrlButton>
        <CtrlButton tooltip={"Rewind"} disabled={!isLoaded || !hasStarted} onClick={rewind}>
          <FastRewind />
        </CtrlButton>
        <CtrlButton tooltip={"Stop"} disabled={!isLoaded || !isPlaying} onClick={stop}>
          <StopIcon />
        </CtrlButton>
        <CtrlButton
          tooltip={"Fast forward"}
          disabled={!isLoaded} // TODO: Save the duration of the audio
          onClick={fastForward}
        >
          <FastForward />
        </CtrlButton>
        <CtrlButton tooltip={isPlaying ? "Pause" : "Play"} disabled={!isLoaded} onClick={play}>
          {isPlaying ? <PauseIcon /> : <PlayIcon />}
        </CtrlButton>
      </ButtonGroup>
    </Box>
  );
};

export default AudioPlayerButtons;
