import React from "react";
import { resolveFile, resolveWorkspace } from "@/lib/files";
import { FileType, StoredFile, StoredWorkspace } from "@/lib/types/files";
import { PartialNamedUrl } from "@/lib/types/load";
import dayjs from "dayjs";
import { useIndexedDB } from "react-indexed-db-hook";

const useFiles = () => {
  const files = useIndexedDB("file");
  const ws = useIndexedDB("workspace");

  const ok = !!files && !!ws;

  if (!files) {
    throw new Error("couldn't load files");
  }
  if (!ws) {
    throw new Error("couldn't load ws");
  }

  const updateWorkspace = async (props: {
    src: StoredWorkspace;
    update: Partial<StoredWorkspace>;
  }) => {
    const { src, update } = props;
    const updated = dayjs().toISOString();
    const updatedWorkspace = {
      ...src,
      ...update,
      updated,
    };
    const updatedWorkspaceId = await ws.update(updatedWorkspace);
    return updatedWorkspaceId;
  };

  const clear = async () => {
    await Promise.all([ws.clear(), files.clear()]);
  };

  const getAllFiles = async () => {
    const all = await files.getAll();
    return all as StoredFile[];
  };

  const getAllWorkspaces = async () => {
    const all = await ws.getAll();
    return all as StoredWorkspace[];
  };

  const getAllResolveWorkspaces = async () => {
    const all = await ws.getAll();
    return await Promise.all(all.map((w) => resolveWorkspace(w, getFileById)));
  };

  const getWorkspaceById = async (id: number) => {
    return (await ws.getByID(id)) as StoredWorkspace;
  };

  const getFileById = async (id: number) => {
    return (await files.getByID(id)) as StoredFile;
  };

  const getFilesForType = async (type: string) => {
    const allFiles = await files.getAll();
    return allFiles.filter((f) => f.type === type) as StoredFile[];
  };

  const getLatestWorkspace = async () => {
    const workspaces = await ws.getAll();
    if (!workspaces.length) return;

    const workspacesSortedByDate = workspaces.sort((a, b) => dayjs(b.date).diff(dayjs(a.date)));
    const latestWorkspace = workspacesSortedByDate[workspacesSortedByDate.length - 1];
    return latestWorkspace as StoredWorkspace;
  };

  const getWorkspace = async (
    w: StoredWorkspace,
    fileResolver?: (id: number) => Promise<StoredFile | null>
  ) => {
    const r = await ws.getByID(w.id);
    if (!fileResolver) fileResolver = getFileById;
    return await resolveWorkspace(r, fileResolver);
  };

  const getFile = async (s: StoredFile) => {
    const f = await getFileById(s.id);
    return resolveFile(f);
  };

  const findFiles = async (w: StoredWorkspace) => {
    const findFile = async (id: number) => {
      const f = await getFileById(id);
      if (!f) return;
      return resolveFile(f);
    };
    const [audioUrl, midiUrl, midizUrl] = await Promise.all([
      findFile(w.audio_id),
      findFile(w.midi_id),
      findFile(w.midiz_id),
    ]);
    return { audioUrl, midiUrl, midizUrl };
  };

  const fileExists = async (props: { checksum: string; name: string; type: string }) => {
    const { checksum, name, type } = props;

    // TODO: How to index?
    // const existing = await files.getByIndex("checksum", checksum);
    const existing = (await files.getAll()).filter((f) => f.checksum === checksum)[0];
    if (!existing) {
      return;
    }
    if (existing.name !== name) {
      console.warn("checksum sum but names differ", { existing, name });
      return;
    }
    if (existing.type !== type) {
      console.warn("checksum sum but types differ", { existing, type });
      return;
    }
    const updated = dayjs().toISOString();
    const f = {
      ...existing,
      updated,
    };
    const update = await files.update(f);
    console.log("update for existing", name, " = ", update, f);
    return f;
  };

  const fileExistsWithName = async (props: { name: string; type?: string }) => {
    const { name, type } = props;
    const existing = (await files.getAll()).filter(
      (f) => f.name === name && (!type || f.type === type)
    )[0];
    return !!existing;
  };

  const md5Checksum = async (blob: Blob) => {
    const md5 = await crypto.subtle.digest("SHA-256", await blob.arrayBuffer());
    const checksum = new TextDecoder("utf-8").decode(md5);
    return checksum;
  };

  const saveFileById = async (props: { fileId: number; content: any }) => {
    const { content, fileId } = props;
    const existing = await getFileById(fileId);
    const updated = dayjs().toISOString();
    const blob = new Blob([JSON.stringify(content, null, 2)], { type: "application/json" });
    const f = {
      ...existing,
      updated,
      blob,
    };
    const update = await files.update(f);
    console.log("update for existing", existing.name, " = ", update, f);
    return updated;
  };

  const findOrCreateFile = async (props: {
    input: PartialNamedUrl;
    type: string;
    mimeType?: string;
  }) => {
    const {
      input: { url, name },
      type,
      mimeType,
    } = props;
    let blob = await (await fetch(url)).blob();
    if (mimeType) {
      // https://stackoverflow.com/questions/18998543/set-content-type-on-blob
      blob = blob.slice(0, blob.size, mimeType);
    }
    const checksum = await md5Checksum(blob);
    const existing = await fileExists({ checksum, name, type });
    if (existing) {
      const fileId = existing.id as number;
      const createdISO = existing.created as string;
      const updatedISO = existing.updated as string;
      const dbInfo = {
        fileId,
      };
      const file = {
        dbInfo,
        createdISO,
        updatedISO,
      };
      return { file, existing: true };
    }
    const updated = dayjs().toISOString();
    const created = dayjs().toISOString();
    const fileId = await files.add({
      name,
      type,
      blob,
      checksum,
      created,
      updated,
    });
    const createdISO = created as string;
    const updatedISO = updated as string;
    const dbInfo = {
      fileId,
    };
    const file = {
      dbInfo,
      createdISO,
      updatedISO,
    };
    return { file, existing: false };
  };

  const getAvailableFileName = async (props: { name: string; type: FileType }) => {
    const { name, type } = props;
    const exists = await fileExistsWithName({ name, type });
    if (!exists) return name;
    const ext = name.split(".").pop();
    const base = name.replace(`.${ext}`, "");
    for (let i = 1; ; i++) {
      const name = `${base}-${i}.${ext}`;
      const exists = await fileExistsWithName({ name, type });
      if (!exists) {
        return name;
      }
    }
  };

  const createFile = async (props: {
    input: Blob;
    name: string;
    type: FileType;
    overwrite?: boolean;
  }) => {
    const { input: blob, name, type, overwrite = false } = props;
    const nameToUse = overwrite ? name : await getAvailableFileName({ name, type });
    const checksum = await md5Checksum(blob);
    const updated = dayjs().toISOString();
    const created = dayjs().toISOString();
    const added = await files.add({
      name: nameToUse,
      type,
      blob,
      checksum,
      created,
      updated,
    });
    return added;
  };

  const findOrCreateWorkspace = async (props: {
    audioFileId: number;
    midiFileId: number;
    midizFileId: number;
    template: string | null;
  }): Promise<number> => {
    const { audioFileId, midiFileId, midizFileId, template } = props;

    const existing = (await ws.getAll()).filter(
      (w) =>
        w.audio_id &&
        w.audio_id === audioFileId &&
        w.midi_id &&
        w.midi_id === midiFileId &&
        w.midiz_id &&
        w.midiz_id === midizFileId
    );
    const existingWorkspace = existing ? existing[0] : undefined;
    if (existingWorkspace) {
      return existingWorkspace.id as number;
    }
    const now = dayjs().toISOString();
    const workspace = (await ws.add({
      key: 0, // will replace
      created: now,
      updated: now,
      audio_id: audioFileId,
      midi_id: midiFileId,
      midiz_id: midizFileId,
      creation_template: template,
    })) as number;
    await ws.update({
      id: workspace,
      key: workspace,
      created: now,
      updated: now,
      audio_id: audioFileId,
      midi_id: midiFileId,
      midiz_id: midizFileId,
      creation_template: template,
    });
    console.log("created workspace", workspace);
    return workspace;
  };

  return {
    ok,
    clear,
    createFile,
    findOrCreateFile,
    fileExists,
    getFile,
    getFileById,
    getFilesForType,
    getAllFiles,
    getAllWorkspaces,
    getAllResolveWorkspaces,
    getAvailableFileName,
    getLatestWorkspace,
    getWorkspace,
    findFiles,
    findOrCreateWorkspace,
    updateWorkspace,
    getWorkspaceById,
    saveFileById,
  };
};

export default useFiles;
