import {
  MidiAnimation,
  IMidiAnimationDrawData,
  Note,
  NoteFilterCriteria,
  AnimationOptions,
} from "@/lib/types/main";
import { CircleOptions } from "@/lib/types/BaseAnimationOptions";
import { BaseAnimationOptions } from "../../lib/types/BaseAnimationOptions";
import { BaseMidiAnimation } from "@/lib/midi/animations/BaseMidiAnimation";
import { CirclesUtil } from "./CirclesUtil";

export interface Options extends BaseAnimationOptions, CircleOptions { }

const defaults = {
  diameter: { type: "size", default: 0 },
  scale: { type: "size", default: 1 },
  lineWidth: { type: "size", default: 5 },
  fillColor: { type: "color", default: "#fff" },
  lineColor: { type: "color", default: "#000" },
} as AnimationOptions<Options>;

class Circles extends BaseMidiAnimation<Options> implements MidiAnimation {
  constructor(criteria: NoteFilterCriteria, options: Options, id?: string) {
    super(criteria, options, defaults, CirclesUtil.NAME, CirclesUtil.getInstance(), id);
  }
}

export default Circles;
