import { useCallback, useEffect, useRef, useState } from "react";

import { MidiAnimation } from "../lib/types/main";
import { Track } from "@/lib/types/editor";
import { BaseAnimationOptions } from "@/lib/types/BaseAnimationOptions";
import { FileBundle, NamedUrl } from "@/lib/types/load";
import { createFileSource, deserializeAnimation, serializeAnimation } from "../lib/midi/lib";
import useAudio from "@/hooks/useAudio";
import useTwo from "@/hooks/useTwo";
import { useAnimations } from "@/hooks/useAnimations";
import { useNavigate } from "react-router-dom";
import { RefBasedMidiAnimationRegistry } from "@/midi/registry_with_ref";
import useFiles from "./useFiles";
import useMessages from "./useMessages";
import debounce from "debounce";
interface UseEditorProps {
  fileBundle: Required<FileBundle>;
  flatTracks: Track[];
}

const useEditor = (props: UseEditorProps) => {
  const {
    fileBundle: {
      midi: midiUrl,
      audio: audioUrl,
      midiz: intialMidizUrl,
      selectedTemplate: templateName,
    },
    flatTracks,
  } = props;

  const navigate = useNavigate();
  const fs = useFiles();
  const { showMessage } = useMessages();

  const [animationToEdit, setAnimationToEdit] = useState<MidiAnimation | null>(null);
  const [saveEnabled, setSaveEnabled] = useState(false);
  const [midizUrl, setMidizUrl] = useState<NamedUrl>(intialMidizUrl);
  const [isPlaying, setIsPlaying] = useState(false);
  const { two, divElm } = useTwo<HTMLDivElement>();

  const registryRef = useRef<RefBasedMidiAnimationRegistry>();
  const canvasElm = useRef<HTMLCanvasElement | null>(null);

  const { audioElm, wrapper } = useAudio();

  useEffect(() => {
    setMidizUrl(intialMidizUrl);
  }, [intialMidizUrl]);

  const {
    setAnimations,
    groupedAndCombinedTracks: tracks,
    registry,
    animationsRef: animations, // TODO: Don't need this
    needsSaving,
  } = useAnimations({
    templateName,
    divElm,
    two,
    tracks: flatTracks,
    animationsUrl: midizUrl?.url || "",
  });

  const saveToDB = useCallback(
    async (props: { content: any; dbInfo: { fileId: number } }) => {
      const { content, dbInfo } = props;
      const { fileId } = dbInfo;
      return await fs.saveFileById({ fileId, content });
    },
    [setMidizUrl]
  );

  const createAnimationConfig = useCallback(() => {
    const animationJSONs = animations.current.map((anim) => serializeAnimation(anim));
    // midizUrl is null???
    const thisSource = createFileSource({ name: midizUrl.name });
    const audioSource = createFileSource({ name: audioUrl.name });
    const midiSource = createFileSource({ name: midiUrl.name });
    const config = {
      animations: animationJSONs,
      audioSource,
      midiSource,
      thisSource,
    };
    return config;
  }, [animations.current, midizUrl, audioUrl, midiUrl]);

  const saveAll = useCallback(async () => {
    const animationConfig = createAnimationConfig();
    console.log("animationConfig", animationConfig);
    const updatedISO = await saveToDB({ content: animationConfig, dbInfo: midizUrl.dbInfo });
    setSaveEnabled(false);
    setMidizUrl((prev: NamedUrl) => {
      return {
        ...prev,
        updatedISO,
      };
    });
    showMessage(`Saved ${animationConfig.thisSource.name}`, { severity: "success" });
  }, [saveToDB, setSaveEnabled, createAnimationConfig, showMessage, setMidizUrl]);

  useEffect(() => {
    if (!saveAll) return;
    if (!needsSaving) return;
    console.log("would save all");
    debounce(() => saveAll(), 5000);
  }, [needsSaving, saveAll]);

  useEffect(() => {
    if (!canvasElm.current) return;

    canvasElm.current.style.width = "100%";
    canvasElm.current.style.height = "100%";
    canvasElm.current.width = canvasElm.current.offsetWidth;
    canvasElm.current.height = canvasElm.current.offsetHeight;
  }, [canvasElm.current]);

  useEffect(() => {
    if (!wrapper) return;
    if (!registry) return;
    if (registryRef.current) return;

    registryRef.current = registry;
    if (!two.current) {
      throw new Error("two.current is null");
    }
    registryRef.current.setTwo(two);

    const tick = (audioProps: {
      audioBufferData: Uint8Array;
      currentTimeMillis: number;
      durationMillis: number;
      isPlaying: boolean;
    }) => {
      const canvasContext = canvasElm.current?.getContext("2d");
      canvasContext?.clearRect(0, 0, canvasContext.canvas.width, canvasContext.canvas.height);
      const { width, height } = canvasContext.canvas;
      const props = {
        ...audioProps,
        canvasElement: canvasElm.current,
        canvas: canvasContext,
        two,
        width,
        height,
      };
      registryRef.current.draw(props);
    };
    const callback = {
      tick,
      onPlayStopped: () => setIsPlaying(false),
      onPlayStarted: () => setIsPlaying(true),
    };
    wrapper.setCallback(callback);
    const init = async () => {
      await registryRef.current.initFromUrl(midiUrl.url);
    };
    init();
  }, [wrapper, registry, midiUrl?.url]);

  const convertToAnimation = (anim: MidiAnimation, type: string) => {
    const json = serializeAnimation(anim);
    json.name = type;
    const newAnim = deserializeAnimation(json);
    return newAnim;
  };

  const changeAnimationType = useCallback((animationIndex: number, type: string) => {
    console.log("changeAnimationType type", type);

    setAnimationToEdit((prev) => {
      if (!prev) {
        prev = animations.current[animationIndex];
      }
      return convertToAnimation(prev, type);
    });
    setAnimations((prev) => {
      if (!prev) return prev;
      const anim = convertToAnimation(prev[animationIndex], type);
      console.log("converted to", anim);
      prev[animationIndex] = anim;
      return [...prev];
    });
    setSaveEnabled(true);
  },
    [setAnimationToEdit, setAnimations]
  );

  const changeAnimationToEditType = useCallback((type: string) => {
    if (!animationToEdit) return;

    const id = animationToEdit.id;
    const index = animations.current.findIndex((anim) => anim.id === id);
    changeAnimationType(index, type);
  },
    [animationToEdit, changeAnimationType]
  );

  const doneEditAnimation = useCallback(() => {
    console.log("doneEditAnimation");
    setAnimationToEdit(null);
  }, [setAnimationToEdit]);

  const saveAnimation = useCallback(
    (animationIndex: number, newOptions: BaseAnimationOptions) => {
      console.log("saveAnimation newOptions", newOptions);

      setAnimationToEdit((prev) => {
        if (!prev) return prev;
        prev.options = newOptions;
        return { ...prev };
      });
      setAnimations((prev) => {
        if (!prev) return prev;
        const anim = prev[animationIndex];
        anim.options = newOptions;
        return [...prev];
      });
      setSaveEnabled(true);
    },
    [setAnimationToEdit, setAnimations, setSaveEnabled]
  );

  const moveAnimation = useCallback(
    (animationIndex: number, dx: number, dy: number) => {
      const oldOrigin = animationToEdit?.options?.origin || { x: 0, y: 0 };
      const newOrigin = {
        x: oldOrigin.x + dx,
        y: oldOrigin.y + dy,
      };
      console.log("moveAnimation", dx, dy, animationToEdit?.options?.origin, "->", newOrigin);

      saveAnimation(animationIndex, {
        ...animationToEdit?.options,
        origin: newOrigin,
      });
    },
    [animationToEdit?.options?.origin, saveAnimation]
  );

  const moveAnimationToEdit = useCallback(
    (dx: number, dy: number) => {
      if (!animationToEdit) return;

      const id = animationToEdit.id;
      const index = animations.current.findIndex((anim) => anim.id === id);
      moveAnimation(index, dx, dy);
    },
    [animationToEdit, moveAnimation]
  );

  const download = useCallback(() => {
    const animationConfig = createAnimationConfig();
    const mdzName = animationConfig.thisSource.name;
    console.log("animationConfig", animationConfig);

    const content = JSON.stringify(animationConfig, null, 2);
    const blob = new Blob([content], { type: "application/json" });
    const url = URL.createObjectURL(blob);
    const a = document.createElement("a");
    a.href = url;
    a.download = mdzName;
    a.click();
    showMessage(`Downloaded to ${mdzName}`);
  }, [createAnimationConfig]);

  const upload = useCallback(() => {
    navigate("/edit");
  }, []);

  const muteAnimationById = useCallback((id: string, newVal: boolean) => {
    if (!registryRef.current || !animations.current) return false;

    const anim = animations.current.find((anim) => anim.id === id);
    registryRef.current.mute(anim, newVal);
    return true;
  }, []);

  const soloAnimationById = useCallback((id: string, newVal: boolean) => {
    if (!registryRef.current || !animations.current) return false;

    const anim = animations.current.find((anim) => anim.id === id);
    registryRef.current.solo(anim, newVal);
    return true;
  }, []);

  const verboseAnimationById = useCallback((id: string, newVal: boolean) => {
    if (!registryRef.current || !animations.current) return false;

    const anim = animations.current.find((anim) => anim.id === id);
    anim.setVerbose(newVal);
    return true;
  }, []);

  return {
    audioElm,
    divElm,
    canvasElm,
    midizUrl,
    upload,
    saveAll,
    download,
    moveAnimationToEdit,
    changeAnimationToEditType,
    doneEditAnimation,
    changeAnimationType,
    saveEnabled,
    setSaveEnabled,
    animationToEdit,
    setAnimationToEdit,
    tracks,
    animations,
    setAnimations,
    registry: registryRef,
    muteAnimationById,
    soloAnimationById,
    verboseAnimationById,
    isPlaying,
    setIsPlaying,
  };
};

export default useEditor;
