export const makeRoundedRectangle = (
  two: Two,
  x: Coord,
  y: Coord,
  width: Size,
  height: Size,
  diameter: Diameter,
  opts?: Opts
) => {
  const { fill, linewidth, stroke } = opts ?? {};

  const s = two.makeRoundedRectangle(
    scale(x),
    scale(y),
    scale(width),
    scale(height),
    scale(diameter) / 2
  );

  if (fill !== undefined) s.fill = fill;
  if (linewidth !== undefined) s.linewidth = scale(linewidth);
  if (stroke !== undefined) s.stroke = stroke;

  return s;
};

