import React from "react";
import {
  StoredFile,
  FileType,
  ResolvedFile,
  StoredWorkspace,
  ResolvedWorkspace,
} from "./types/files";

export const uploadToBlob = async (file: File): Promise<string> => {
  return new Promise((resolve, reject) => {
    const reader = new FileReader();
    reader.onload = () => {
      const blobUrl = URL.createObjectURL(file);
      resolve(blobUrl);
    };
    reader.onerror = () => {
      reject(new Error("Failed to read file"));
    };
    reader.readAsArrayBuffer(file);
  });
};

export const resolveFile = (f: StoredFile) => {
  const toFull = (url: string) => {
    return {
      idForDebugging: f.id,
      url,
      name: f.name,
      type: f.type as FileType,
      createdISO: f.created,
      updatedISO: f.updated,
      dbInfo: {
        fileId: f.id,
      },
    };
  };

  const blob = f.blob;
  const blobUrl = URL.createObjectURL(blob);
  const res = toFull(blobUrl);
  return res as ResolvedFile;
};

export const resolveWorkspace = async (
  w: StoredWorkspace,
  fileResolver: (id: number) => Promise<StoredFile>
): Promise<ResolvedWorkspace> => {
  const { id, key, created, updated, audio_id, midi_id, midiz_id, creation_template } = w;

  const toFullFile = (id: number) => fileResolver(id).then(resolveFile);

  const [audio, midi, midiz] = await Promise.all([
    toFullFile(audio_id),
    toFullFile(midi_id),
    toFullFile(midiz_id),
  ]);

  return {
    id: id,
    key,
    createdISO: created,
    updatedISO: updated,
    audio,
    midi,
    midiz,
    creationTemplate: creation_template,
  } as ResolvedWorkspace;
};
