import React from "react";
import { WS_URL } from "@/lib/websocket";
import { useEffect, useState } from "react";
import useWebSocket from "react-use-websocket";
import { legacyProtocol, User } from "./common";

const isUsersEvent = (message: any) => {
  const parsedMessage = JSON.parse(message.data);
  return parsedMessage.type === "users" || parsedMessage.type === "groupUpdate";
};

const useUsers = () => {
  const [users, setUsers] = useState<User[]>([]);
  const { lastJsonMessage } = useWebSocket(WS_URL, {
    onOpen: () => {
      console.log("[useUsers] WebSocket connection established.");
    },
    share: true,
    filter: isUsersEvent,
    retryOnError: true,
    shouldReconnect: () => true, // Check this
  });

  useEffect(() => {
    if (lastJsonMessage) {
      const { type, data } = lastJsonMessage as any;
      console.log("useUsers", type, data);
      if (legacyProtocol && type === "users") {
        const { users } = data || {};
        const newUsers = users.map(({ userId, username, userType }) => {
          return { id: userId, name: username, userType };
        }) as User[];
        setUsers(newUsers);
      }
      if (type === "groupUpdate") {
        const { host, guests } = data || {};
        const newUsers = [host, ...guests].map(({ userId, username, userType }) => {
          return { id: userId, name: username, userType };
        }) as User[];
        setUsers(newUsers);
      }
    }
  }, [lastJsonMessage, setUsers]);

  return { users };
};

export default useUsers;
