import React from "react";
import { useRef, useCallback, useState, useMemo, useEffect } from "react";
import { PropsFrom, MutRaf, Setter } from "@/lib/types/util";
import { WaveSurfer as BaseWaveSurfer, WaveForm, Region as WSRegion } from "wavesurfer-react";
import { WaveSurfer as WaveSurferRef } from "wavesurfer-react/dist/utils/createWavesurfer";
import { Box, Button, ButtonGroup, IconButton, Typography } from "@mui/material";

import RegionsPlugin from "wavesurfer.js/dist/plugins/regions";
import TimelinePlugin from "wavesurfer.js/dist/plugins/timeline";

import PlayIcon from "@mui/icons-material/PlayArrow";
import PauseIcon from "@mui/icons-material/Pause";
import RestartAltIcon from "@mui/icons-material/RestartAlt";
import StopIcon from "@mui/icons-material/Stop";
import CurrentTime from "./CurrentTime";
import { GroupedTrack } from "../lib/types/editor";
import { canvasBackgroundColor } from "../routes/share/common";

export interface Region {
  id: string;
  start: number;
  end: number;
  color: string;
  data?: {
    systemRegionId?: number;
  };
}

export const WaveSurfer = BaseWaveSurfer;

const arrayEquals = (a: any[], b: any[]) => {
  if (a === b) return true;
  if (a == null || b == null) return false;
  if (a.length !== b.length) return false;
  for (let i = 0; i < a.length; i++) {
    if (a[i] !== b[i]) return false;
  }
  return true;
};

interface WavesurferAudioPlayerProps {
  audioElm: MutRaf<HTMLAudioElement>;
  src: string;
  regions: Region[];
  setRegions: (regions: Region[]) => void;
  wavesurferRef: MutRaf<WaveSurfer>;
  isLoaded: boolean;
  setIsLoaded: Setter<boolean>;
  setIsPlaying: Setter<boolean>;
  height: number;
  setActiveTrackNums: Setter<number[]>;
  tracks: GroupedTrack[];
  setDurationSecs: Setter<number>;
}

const WavesurferAudioPlayer: React.FC<WavesurferAudioPlayerProps> = (props) => {
  const {
    audioElm,
    src,
    regions,
    setRegions,
    wavesurferRef,
    isLoaded,
    setIsLoaded,
    setIsPlaying,
    height,
    setActiveTrackNums,
    tracks,
    setDurationSecs,
  } = props;

  const [currentTimeSecs, setCurrentTimeSecs] = useState(0);

  // use regions ref to pass it inside useCallback
  // so it will use always the most fresh version of regions list
  const regionsRef = useRef(regions);

  useEffect(() => {
    regionsRef.current = regions;
  }, [regions]);

  useEffect(() => {
    if (currentTimeSecs === undefined) {
      setActiveTrackNums([]);
      return;
    }
    const nums = tracks
      .map((t) => {
        const actives = t.tracks.filter((track) => {
          return track.minTimeSecs <= currentTimeSecs && currentTimeSecs <= track.maxTimeSecs;
        });
        const active = !!actives.length;
        if (active) return t.index;
      })
      .filter((i) => i !== undefined) as number[];
    setActiveTrackNums((prev) => {
      if (arrayEquals(prev, nums)) return prev;
      return nums;
    });
  }, [currentTimeSecs, tracks]);

  const handleRegionUpdate = useCallback((region, smth) => {
    console.log("region-update-end --> region:", region);
    console.log(smth);
  }, []);

  const regionCreatedHandler = useCallback(
    (region) => {
      console.log("region-created --> region:", region);

      if (region.data.systemRegionId) return;

      setRegions([
        ...regionsRef.current,
        { ...region, data: { ...region.data, systemRegionId: -1 } },
      ]);
    },
    [regionsRef]
  );

  const handleWSMount = useCallback(
    (waveSurfer: WaveSurferRef | null): void => {
      // @ts-ignore
      wavesurferRef.current = waveSurfer;

      if (wavesurferRef.current) {
        // @ts-ignore
        wavesurferRef.current.backend = "MediaElement";
        wavesurferRef.current.load(src);

        wavesurferRef.current.on("region-created", regionCreatedHandler);

        wavesurferRef.current.on("ready", () => {
          console.log("WaveSurfer is ready");
          setIsLoaded(true);
          audioElm.current = wavesurferRef.current.media;
          setCurrentTimeSecs(0);
          setDurationSecs(wavesurferRef.current.getDuration());
        });

        wavesurferRef.current.on("region-removed", (region) => {
          console.log("region-removed --> ", region);
        });
        wavesurferRef.current.on("play", () => {
          setIsPlaying(true);
        });
        wavesurferRef.current.on("pause", () => {
          setIsPlaying(false);
        });
        wavesurferRef.current.on("pause", () => {
          setIsPlaying(false);
        });
        wavesurferRef.current.on("finish", () => {
          setIsPlaying(false);
          setCurrentTimeSecs(undefined);
        });
        wavesurferRef.current.on("timeupdate", () => {
          const secs = wavesurferRef.current.getCurrentTime();
          setCurrentTimeSecs(secs);
        });
        //

        wavesurferRef.current.on("loading", (data) => {});

        if (window) {
          // window.surferidze = wavesurferRef.current;
        }
      }
    },
    [setIsPlaying, setIsLoaded, setCurrentTimeSecs, regionCreatedHandler, setDurationSecs]
  );

  const plugins = [
    {
      key: "regions",
      plugin: RegionsPlugin,
      options: {
        dragSelection: false,
      },
    },
    {
      key: "timeline",
      plugin: TimelinePlugin,
      options: {
        height: 20,
      },
    },
  ];

  return (
    <>
      <WaveSurfer
        // @ts-ignore
        plugins={plugins}
        onMount={handleWSMount}
        // @ts-ignore
        container="#waveform"
        height={height}
        normalize
        backend="MediaElement"
      >
        <WaveForm id="waveform">
          {isLoaded &&
            regions.map((regionProps) => (
              <WSRegion onUpdateEnd={handleRegionUpdate} key={regionProps.id} {...regionProps} />
            ))}
        </WaveForm>
        <div id="timeline" />
      </WaveSurfer>
      <Box
        sx={{
          m: 1,
          mt: -4,
          p: 0.5,
          backgroundColor: "rgba(0,0,0,.1)",
          maxWidth: 50,
          width: "auto",
          textAlign: "center",
        }}
      >
        {/* <Typography variant="subtitle2" sx={{ p: 0, m: 0 }}>
        {audioName}
      </Typography> */}
        <Typography variant="caption" sx={{ p: 0, m: 0 }}>
          <CurrentTime currentTimeSecs={currentTimeSecs} />
        </Typography>
      </Box>
    </>
  );
};

export default WavesurferAudioPlayer;
