import React from "react";
import {
  Box,
  Button,
  Typography,
  Grid,
  Stepper,
  Step,
  StepContent,
  StepLabel,
  FormControl,
  MenuItem,
  Select,
  SelectChangeEvent,
  Stack,
  Checkbox,
} from "@mui/material";
import { useState, useMemo, useCallback, useEffect } from "react";
import { NamedUrl, PartialNamedUrl } from "@/lib/types/load";
import dayjs from "dayjs";
import { Setter } from "@/lib/types/util";
import { resolveWorkspace, uploadToBlob } from "@/lib/files";
import { ResolvedWorkspace, StoredFile, StoredWorkspace } from "@/lib/types/files";
import useFiles from "@/hooks/useFiles";
import TemplatesSelect from "./TemplatesSelect";
import { useSearchParams } from "react-router-dom";
import allTemplates from "@/midi/tmpl";
import useMessages from "@/hooks/useMessages";
import WorkspaceChooser from "./WorkspaceChooser";

interface FileUploaderProps {
  midiUrl?: NamedUrl;
  midizUrl?: NamedUrl;
  audioUrl?: NamedUrl;
  selectedTemplate?: string;
  selectedWorkspaceId: number | undefined;
  setAudioUrl: Setter<NamedUrl>;
  setMidiUrl: Setter<NamedUrl>;
  setMidizUrl: Setter<NamedUrl>;
  setWorkspaceId: Setter<number>;
  setSelectedTemplate: Setter<string | undefined>;
  setShouldLoadFiles: Setter<boolean>;
  activeStep: number;
  setActiveStep: Setter<number>;
}

const FileUploader: React.FC<FileUploaderProps> = ({
  audioUrl: pageAudioUrl,
  midiUrl: pageMidiUrl,
  midizUrl: pageMidizUrl,
  selectedTemplate: pageSelectedTemplate,
  selectedWorkspaceId,
  setAudioUrl: pageSetAudioUrl,
  setMidiUrl: pageSetMidiUrl,
  setMidizUrl: pageSetMidizUrl,
  setWorkspaceId: pageSetWorkspaceId,
  setSelectedTemplate: pageSetSelectedTemplate,
  setShouldLoadFiles,
  activeStep: initialActiveStep,
}) => {
  const {
    createFile,
    findOrCreateFile,
    findOrCreateWorkspace,
    getFilesForType,
    getFile,
    getAvailableFileName,
    getAllWorkspaces,
    getWorkspace,
    getFileById,
  } = useFiles();

  const [searchParams, setSearchParams] = useSearchParams();

  const { showMessage } = useMessages();

  type CreationVerbs = { past: string; present: string };
  const defaultCreationVerbs = { past: "uploaded", present: "uploading" };
  const existingCreationVerbs = { past: "existing", present: "uploading" };

  const [audioUrl, setAudioUrl] = useState<PartialNamedUrl | undefined>(pageAudioUrl);
  const [midiUrl, setMidiUrl] = useState<PartialNamedUrl | undefined>(pageMidiUrl);
  const [midizUrl, setMidizUrl] = useState<PartialNamedUrl | undefined>(pageMidizUrl);
  const [selectedTemplate, setSelectedTemplate] = useState<string | undefined>();
  const [activeStep, setActiveStep] = useState(initialActiveStep);
  const [audioCreationVerbs, setAudioCreationVerbs] = useState<CreationVerbs>(
    pageAudioUrl ? existingCreationVerbs : defaultCreationVerbs
  );
  const [midiCreationVerbs, setMidiCreationVerbs] = useState<CreationVerbs>(
    pageMidiUrl ? existingCreationVerbs : defaultCreationVerbs
  );
  const [midizCreationVerbs, setMidizCreationVerbs] = useState<CreationVerbs>(
    pageMidizUrl ? existingCreationVerbs : defaultCreationVerbs
  );
  const [selectedWorkspace, setSelectedWorkspace] = useState<ResolvedWorkspace | null>(null);
  const [shouldAutoload, setShouldAutoload] = useState(true);
  const [workspaces, setWorkspaces] = useState<ResolvedWorkspace[]>([]);

  const files = useFiles();

  const showTemplateItems = useMemo(() => allTemplates.length > 1, [allTemplates]);

  useEffect(() => {
    if (!selectedWorkspaceId) return;
    const resolve = async () => {
      const workspace = await files.getWorkspaceById(selectedWorkspaceId);
      const resolved = await files.getWorkspace(workspace);
      return resolved;
      setSelectedWorkspace((prev) => (prev ? prev : resolved));
    };
    resolve()
      .then((resolved) => {
        setSelectedWorkspace((prev) => (prev ? prev : resolved));
      })
      .catch((e) => {
        console.error("Error resolving workspace", e);
      });
  }, [selectedWorkspaceId, setSelectedWorkspace]);

  useEffect(() => setActiveStep(initialActiveStep), [initialActiveStep]);
  useEffect(() => setAudioUrl(pageAudioUrl), [pageAudioUrl, setAudioUrl]);
  useEffect(() => setMidiUrl(pageMidiUrl), [pageMidiUrl, setAudioUrl]);
  useEffect(() => setMidizUrl(pageMidizUrl), [pageMidizUrl, setMidizUrl]);

  const createMidizFile = useCallback(
    async (props?: { workspaceId?: number }) => {
      const { workspaceId = 0 } = props || {};
      const midizBlob = new Blob([""], { type: "application/json" });
      const name = await getAvailableFileName({
        name: midiUrl.name.replace(/\.mid$/, ".mdz.json"),
        type: "midiz",
      });
      const fileId = await createFile({ input: midizBlob, name, type: "midiz" });
      const midizBlobUrl = URL.createObjectURL(midizBlob);
      setMidizUrl({
        url: midizBlobUrl,
        name,
        createdISO: dayjs().toISOString(),
        updatedISO: dayjs().toISOString(),
        dbInfo: {
          fileId,
        },
        type: "midiz",
      });
      return fileId;
    },
    [midiUrl, setMidizUrl]
  );

  const handleFileChange = async (
    setter: Setter<PartialNamedUrl>,
    type: string,
    event: React.ChangeEvent<HTMLInputElement>
  ) => {
    if (event.target.files && event.target.files.length > 0) {
      const f = event.target.files[0];
      setter({
        url: "",
        name: f.name,
        type,
      });
      const url = await uploadToBlob(f);
      setter({
        url,
        name: f.name,
        type,
      });
    }
  };

  const saveToDB = async (props: {
    audioUrl: PartialNamedUrl;
    midiUrl: PartialNamedUrl;
    midizUrl: PartialNamedUrl;
  }) => {
    const { audioUrl, midiUrl, midizUrl } = props;

    const [audio, midi, midiz] = await Promise.all([
      findOrCreateFile({ input: audioUrl, type: "audio" }),
      findOrCreateFile({ input: midiUrl, type: "midi" }),
      findOrCreateFile({ input: midizUrl, type: "midiz", mimeType: "application/json" }),
    ]);

    showMessage(`Using ${audio.existing ? "existing" : "new"} audio file ${audioUrl.name}`, {
      variant: "info",
    });
    showMessage(`Using ${midi.existing ? "" : "new"} midi file ${midiUrl.name}`, {
      variant: "info",
    });
    showMessage(`Using ${midiz.existing ? "" : "new"} animations file ${midizUrl.name}`, {
      variant: "info",
    });

    const workspaceId = await findOrCreateWorkspace({
      audioFileId: audio.file.dbInfo.fileId,
      midiFileId: midi.file.dbInfo.fileId,
      midizFileId: midiz.file.dbInfo.fileId,
      template: selectedTemplate || null,
    });

    return {
      workspaceId,
      audio: {
        ...audio.file,
        url: audioUrl.url,
        name: audioUrl.name,
        dbInfo: {
          ...audio.file.dbInfo,
        },
        type: "audio",
      },
      midi: {
        ...midi.file,
        url: midiUrl.url,
        name: midiUrl.name,
        dbInfo: {
          ...midi.file.dbInfo,
        },
        type: "midi",
      },
      midiz: {
        ...midiz.file,
        url: midizUrl.url,
        name: midizUrl.name,
        dbInfo: {
          ...midiz.file.dbInfo,
        },
        type: "midiz",
      },
    };
  };

  const handleEditClick = useCallback(async () => {
    console.log("handleEditClick", { audioUrl, midiUrl, midizUrl });
    if (!audioUrl || !audioUrl.url || !audioUrl.name) return;
    if (!midiUrl || !midiUrl.url || !midiUrl.name) return;
    if (!midizUrl || !midizUrl.url || !midizUrl.name) return;

    const { audio, midi, midiz, workspaceId } = await saveToDB({ audioUrl, midiUrl, midizUrl });

    if (selectedWorkspace) {
      setSearchParams((prev) => {
        if (shouldAutoload) prev.set("autoload", "1");
        prev.set("ws", selectedWorkspace.id.toString());
        return prev;
      });
    } else if (workspaceId) {
      setSearchParams((prev) => {
        if (shouldAutoload) prev.set("autoload", "1");
        prev.set("ws", workspaceId.toString());
        return prev;
      });
    }

    // TODO: Should do these all at once as a file bundle
    pageSetWorkspaceId(workspaceId);
    pageSetAudioUrl(audio);
    pageSetMidiUrl(midi);
    pageSetMidizUrl(midiz);
    pageSetSelectedTemplate(selectedTemplate);
    setShouldLoadFiles(true);
  }, [
    audioUrl,
    midiUrl,
    midizUrl,
    selectedTemplate,
    pageSetAudioUrl,
    pageSetMidiUrl,
    pageSetMidizUrl,
    setShouldLoadFiles,
    pageSetSelectedTemplate,
    selectedWorkspace,
    setSearchParams,
    shouldAutoload,
  ]);

  const handleClearClick = useCallback(() => {
    setAudioUrl(undefined);
    setMidiUrl(undefined);
    setMidizUrl(undefined);
    setSelectedTemplate(undefined);
    pageSetAudioUrl(undefined);
    pageSetMidiUrl(undefined);
    pageSetMidizUrl(undefined);
    pageSetWorkspaceId(undefined);
    pageSetSelectedTemplate(undefined);
    setActiveStep(0);
    setSelectedWorkspace(undefined);
    setSelectedTemplate(undefined);
  }, [
    setAudioUrl,
    setMidiUrl,
    setMidizUrl,
    setSelectedTemplate,
    pageSetAudioUrl,
    pageSetMidiUrl,
    pageSetMidizUrl,
    pageSetWorkspaceId,
    pageSetSelectedTemplate,
    setActiveStep,
    setSelectedWorkspace,
    setSelectedTemplate,
  ]);

  const haveAll = useMemo(
    () => !!audioUrl || !!midiUrl || !!midizUrl,
    [audioUrl, midiUrl, midizUrl]
  );

  useEffect(() => {
    if (!haveAll) return;
    // setShouldLoadFiles(true);
  }, [haveAll, setShouldLoadFiles]);

  const handleNext = useCallback(() => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  }, [setActiveStep]);

  const handleBack = useCallback(() => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  }, [setActiveStep]);

  const handleReset = useCallback(() => {
    setActiveStep(0);
  }, [setActiveStep]);

  const setCreatedMidizCreatedVerbs = useCallback(async () => {
    let suffix = "";
    if (selectedTemplate) {
      suffix = ` from template ${selectedTemplate}`;
    }
    setMidizCreationVerbs({ past: "created" + suffix, present: "creating" + suffix });
  }, [setMidizCreationVerbs, selectedTemplate]);

  const handleNextForMidiz = useCallback(async () => {
    console.log("handleNextForMidiz", midizUrl);
    if (!midizUrl) {
      const created = await createMidizFile();
      setCreatedMidizCreatedVerbs();
      console.log("created midiz file", created);
    }
    handleNext();
  }, [midizUrl, createMidizFile, setCreatedMidizCreatedVerbs]);

  const handleNewMidiz = useCallback(async () => {
    console.log("handleNewMidiz", midizUrl);
    setMidizUrl(undefined);
    const created = await createMidizFile();
    setCreatedMidizCreatedVerbs();
    console.log("created midiz file", created);
  }, [setMidizUrl, createMidizFile, setCreatedMidizCreatedVerbs]);

  const handleAudioFileChange = useCallback(
    async (event: React.ChangeEvent<HTMLInputElement>) =>
      handleFileChange(setAudioUrl, "audio", event),
    [setAudioUrl]
  );

  const handleMidiFileChange = useCallback(
    async (event: React.ChangeEvent<HTMLInputElement>) =>
      handleFileChange(setMidiUrl, "midi", event),
    [setMidiUrl]
  );

  const handleMidizFileChange = useCallback(
    async (event: React.ChangeEvent<HTMLInputElement>) =>
      handleFileChange(setMidizUrl, "midiz", event),
    [setMidizUrl]
  );

  const handleClearAudioClick = useCallback(() => {
    console.log("handleClearAudioClick");
    setAudioUrl(undefined);
    pageSetAudioUrl(undefined);
  }, [setAudioUrl, pageSetAudioUrl]);

  const handleClearMidiClick = useCallback(() => {
    console.log("handleClearMidiClick");
    setMidiUrl(undefined);
    pageSetMidiUrl(undefined);
  }, [setMidiUrl, pageSetMidiUrl]);

  const handleClearMidizClick = useCallback(() => {
    console.log("handleClearMidizClick");
    setMidizUrl(undefined);
    pageSetMidizUrl(undefined);
  }, [setMidizUrl, pageSetMidizUrl]);

  const findWorkspace = useCallback(
    async (w: StoredWorkspace) => {
      return getWorkspace(w);
    },
    [getWorkspace]
  );

  useEffect(() => {
    const find = async () => {
      const workspaces = await getAllWorkspaces();
      const resolvedWorkspaces = await Promise.all(workspaces.map(findWorkspace));
      setWorkspaces(resolvedWorkspaces);
    };
    find();
  }, []);

  const FileChooser = (props: {
    type: string;
    selected: StoredFile | null;
    setSelected: Setter<StoredFile | null>;
    setUrl: Setter<PartialNamedUrl>;
    onChange: () => void;
  }) => {
    const { type, setUrl, setSelected, selected, onChange } = props;

    const [files, setFiles] = useState<StoredFile[]>([]);

    useEffect(() => {
      const find = async () => {
        const files = await getFilesForType(type);
        setFiles(files);
      };
      find();
    }, [setFiles]);

    const handleChange = useCallback(
      async (event: SelectChangeEvent<number>) => {
        const file = files.find((f) => f.id === event.target.value);
        setSelected(file || null);
        if (file) {
          const f = await getFile(file);
          setUrl(f);
          if (f) onChange();
        }
      },
      [files, setUrl, setSelected, onChange]
    );

    if (!files.length) {
      return;
    }

    const formatDate = (u: StoredFile) => {
      const iso = u.updated || u.created;
      if (!iso) return "";
      // If it's today, return the time
      const date = new Date(iso);
      const now = new Date();
      if (date.toDateString() === now.toDateString()) {
        return date.toLocaleTimeString();
      }
      return date.toISOString().split("T")[0];
    };

    return (
      <>
        <Box component="span" sx={{ display: "flex", flexDirection: "row", alignItems: "center" }}>
          <Typography component="span" variant="body1">
            or choose:
          </Typography>
          <FormControl sx={{ m: 1, minWidth: 240 }} size="small">
            <Select
              id="file-chooser"
              value={selected?.id ?? ""}
              onChange={handleChange}
              displayEmpty
            >
              <MenuItem value={""}>--</MenuItem>
              {files.map((f) => (
                <MenuItem
                  key={`file-chooser-menu-item=${f.id}`}
                  value={f.id}
                  sx={{ minWidth: 300 }}
                >
                  <Grid container sx={{ width: "100%" }}>
                    <Grid item md={6}>
                      <Typography variant="subtitle1">{f.name}</Typography>
                    </Grid>
                    <Grid item md={6}>
                      <Typography variant="subtitle2">{formatDate(f)}</Typography>
                    </Grid>
                  </Grid>
                </MenuItem>
              ))}
            </Select>
          </FormControl>
        </Box>
      </>
    );
  };

  const StepDescription = (props: { children: JSX.Element | string }) => (
    <Typography variant="body1" sx={{ mb: 2 }}>
      {props.children}
    </Typography>
  );

  const FileStep = ({
    label,
    url,
    setUrl,
    description,
    handleNext,
    handleFileChange,
    accept,
    type,
    handleClear,
    activeStep,
    allowEmpty = false,
    creationVerbs,
    setCreationVerbs,
    children,
  }: {
    label: string;
    url: PartialNamedUrl | null;
    setUrl: Setter<PartialNamedUrl>;
    description: string;
    handleNext: () => void;
    handleFileChange: (event: React.ChangeEvent<HTMLInputElement>) => void;
    accept: string;
    type: string;
    handleClear: () => void;
    activeStep: number;
    allowEmpty?: boolean;
    creationVerbs: CreationVerbs;
    setCreationVerbs: Setter<CreationVerbs>;
    children?: JSX.Element[];
  }) => {
    const [selectedFile, setSelectedFile] = useState<StoredFile | null>(null);

    useEffect(() => {
      if (!url) {
        setSelectedFile(null);
      }
    }, [url]);

    return (
      <>
        <StepLabel>
          <Stack direction={"row"} spacing={1} sx={{ alignItems: "center" }}>
            <Typography variant="subtitle2">{label}</Typography>
            <Typography variant="subtitle1" sx={{ ml: 1 }}>
              {url ? (
                <>
                  <em>{url.name}</em>
                  <Typography variant="caption" sx={{ ml: 1 }}>
                    ({url.url ? creationVerbs.past : `${creationVerbs.present}...`})
                  </Typography>
                </>
              ) : (
                <em>None</em>
              )}
            </Typography>
          </Stack>
        </StepLabel>
        <StepContent>
          <StepDescription>{description}</StepDescription>
          <Box sx={{ mb: 2 }}>
            <div style={{ height: "100%", width: "100%" }}>
              <Box
                component="span"
                sx={{ minHeight: 50, display: "flex", flexDirection: "row", alignItems: "center" }}
              >
                <Box component="span">
                  <Button
                    disabled={!allowEmpty && !url}
                    onClick={(e) => {
                      e.preventDefault();
                      handleNext();
                      setSelectedFile(null);
                    }}
                  >
                    Next
                  </Button>
                </Box>
                {url ? undefined : (
                  <Box component="span">
                    <input
                      type="file"
                      accept={accept}
                      onChange={(e) => {
                        e.preventDefault();
                        handleFileChange(e);
                        handleNext();
                        setSelectedFile(null);
                      }}
                      style={{ display: "none" }}
                      id={`${type}-file-input`}
                    />
                    <label htmlFor={`${type}-file-input`}>
                      <Button component="span">Upload</Button>
                    </label>
                  </Box>
                )}
                {url ? undefined : (
                  <Box component="span">
                    <FileChooser
                      type={type}
                      selected={selectedFile}
                      setSelected={(f) => {
                        setSelectedFile(f);
                        setCreationVerbs(existingCreationVerbs);
                      }}
                      setUrl={setUrl}
                      onChange={() => {
                        handleNext();
                        setSelectedFile(null);
                      }}
                    />
                  </Box>
                )}
                {activeStep > 0 && (
                  <Box component="span">
                    <Button
                      disabled={false}
                      onClick={(e) => {
                        e.preventDefault();
                        handleBack();
                        setSelectedFile(null);
                      }}
                    >
                      Back
                    </Button>
                  </Box>
                )}
                {!url ? undefined : (
                  <Box component="span">
                    <Button
                      disabled={!url}
                      onClick={(e) => {
                        e.preventDefault();
                        handleClear();
                        setSelectedFile(null);
                      }}
                    >
                      Clear
                    </Button>
                  </Box>
                )}
                {children}
              </Box>
            </div>
          </Box>
        </StepContent>
      </>
    );
  };

  const audioStep = useMemo(
    () => (
      <FileStep
        activeStep={activeStep}
        label="Audio"
        url={audioUrl}
        setUrl={setAudioUrl}
        description="Upload or choose an audio file (e.g. mp3 or wav)"
        handleNext={handleNext}
        handleFileChange={handleAudioFileChange}
        accept="audio/wav, audio/mp3"
        type="audio"
        handleClear={handleClearAudioClick}
        creationVerbs={audioCreationVerbs}
        setCreationVerbs={setAudioCreationVerbs}
      />
    ),
    [
      activeStep,
      audioUrl,
      setAudioUrl,
      handleNext,
      handleAudioFileChange,
      handleClearAudioClick,
      audioCreationVerbs,
      setAudioCreationVerbs,
    ]
  );

  const midiStep = useMemo(
    () => (
      <FileStep
        activeStep={activeStep}
        label="MIDI"
        url={midiUrl}
        setUrl={setMidiUrl}
        description="Upload or choose a MIDI file. Why? Your browser never has the instruments in your MIDI file, so playing the MIDI file doesn't sound good."
        handleNext={handleNext}
        handleFileChange={handleMidiFileChange}
        accept=".mid"
        type="midi"
        handleClear={handleClearMidiClick}
        creationVerbs={midiCreationVerbs}
        setCreationVerbs={setMidiCreationVerbs}
      />
    ),
    [
      activeStep,
      midiUrl,
      setMidiUrl,
      handleNext,
      handleMidiFileChange,
      handleClearMidiClick,
      midiCreationVerbs,
      setMidiCreationVerbs,
    ]
  );

  const haveMidizUrl = useMemo(() => !!midizUrl, [midizUrl]);

  const midizStep = useMemo(
    () => (
      <FileStep
        activeStep={activeStep}
        label="Animations"
        url={midizUrl}
        setUrl={setMidizUrl}
        description="[Optional] Upload an animations config (*.mdz) file or click 'Next' and we'll create one for you."
        handleNext={handleNextForMidiz}
        handleFileChange={handleMidizFileChange}
        accept="application/json"
        type="midiz"
        handleClear={handleClearMidizClick}
        allowEmpty
        creationVerbs={midizCreationVerbs}
        setCreationVerbs={setMidizCreationVerbs}
      >
        <Box component="span">
          <Button
            disabled={haveMidizUrl}
            onClick={(e) => {
              e.preventDefault();
              handleNewMidiz();
              handleNextForMidiz();
            }}
          >
            {haveMidizUrl ? "Recreate" : "Create"}
          </Button>
        </Box>
        {showTemplateItems && !haveMidizUrl && (
          <Stack direction="column" sx={{ maxWidth: 400 }}>
            <TemplatesSelect
              selectedTemplate={selectedTemplate}
              setSelectedTemplate={setSelectedTemplate}
            />
            <Typography variant={"body2"}>
              You can choose a template, if not you'll get random animations to start if you don't
              choose a file.
            </Typography>
          </Stack>
        )}
      </FileStep>
    ),
    [
      activeStep,
      midizUrl,
      setMidizUrl,
      handleNextForMidiz,
      handleMidizFileChange,
      handleClearMidizClick,
      midizCreationVerbs,
      setMidizCreationVerbs,
      selectedTemplate,
      setSelectedTemplate,
      haveMidizUrl,
    ]
  );

  const finalStep = useMemo(
    () => (
      <>
        <StepLabel>Finish</StepLabel>
        <StepContent>
          <StepDescription>Use these files to edit animations</StepDescription>
          <Box sx={{ mb: 2 }}>
            <div>
              <Box
                sx={{
                  display: "flex",
                  flexDirection: "row",
                  alignItems: "left",
                }}
              >
                {midiUrl && audioUrl ? (
                  <>
                    <Box
                      sx={{
                        display: "flex",
                        flexDirection: "column",
                        alignItems: "left",
                      }}
                    >
                      <Button
                        variant="contained"
                        onClick={handleEditClick}
                        disabled={!haveAll}
                        color="success"
                      >
                        {selectedWorkspace ? "Continue" : "Start"} Animating
                      </Button>
                      <Stack direction="row" alignItems={"center"}>
                        <Checkbox
                          size={"small"}
                          inputProps={{ "aria-label": "Autoload this workspace" }}
                          onChange={(event) => setShouldAutoload(event.target.checked)}
                          checked={shouldAutoload}
                        />
                        <Typography variant="body2" sx={{ ml: 0.5 }}>
                          Autoload this workspace
                        </Typography>
                      </Stack>
                    </Box>
                    <Button disabled={false} onClick={handleBack}>
                      Back
                    </Button>
                    <Button
                      onClick={() => {
                        handleClearClick();
                        handleReset();
                      }}
                      disabled={!haveAll}
                    >
                      Clear All
                    </Button>
                  </>
                ) : (
                  <>
                    <Button disabled={false} onClick={handleBack}>
                      Back
                    </Button>
                  </>
                )}
              </Box>
            </div>
          </Box>
        </StepContent>
      </>
    ),
    [
      handleEditClick,
      haveAll,
      midiUrl,
      audioUrl,
      handleBack,
      handleClearClick,
      handleReset,
      selectedWorkspace,
      shouldAutoload,
    ]
  );

  const onWorkspaceSelected = useCallback(
    (ws: ResolvedWorkspace) => {
      setSelectedWorkspace(ws);
      setAudioUrl(ws.audio);
      setMidiUrl(ws.midi);
      setMidizUrl(ws.midiz);
      const nextStep = ws ? 3 : 0;
      setActiveStep(nextStep);
    },
    [setSelectedWorkspace, setAudioUrl, setMidiUrl, setMidizUrl, setActiveStep]
  );

  return (
    <>
      <Box>
        {!!workspaces.length && (
          <Box sx={{ my: 2 }}>
            <Typography variant="subtitle2">Workspaces</Typography>
            {workspaces.length ? (
              <WorkspaceChooser
                workspaces={workspaces}
                selected={selectedWorkspace}
                setSelected={onWorkspaceSelected}
                showTemplateItems={showTemplateItems}
              />
            ) : undefined}
          </Box>
        )}
        <Box sx={{ my: 2 }}>
          <Typography variant="subtitle2">Files</Typography>
          <Stepper activeStep={activeStep} orientation="vertical">
            <Step>{audioStep}</Step>
            <Step>{midiStep}</Step>
            <Step>{midizStep}</Step>
            <Step>{finalStep}</Step>
          </Stepper>
        </Box>
      </Box>
    </>
  );
};

export default FileUploader;
