import { GroupedTrack } from "../../types/editor";
import { OptionsPrototype } from "../../types/main";
import { createMidiAnimationInitData, flattenTrack } from "../../../midi/impl";
import allTemplates from "../../../midi/tmpl";
import parseColor from "parse-color";

// import TwoTS from "twojs-ts";
// declare class Two extends TwoTS.Two { }
import Two from "two.js";

const optionsPrototypeDefaults = {
  string: "",
  size: 0,
  color: "#fff",
  number: 0,
  boolean: false,
  point: { x: 0, y: 0 },
};

export const toOptionsPrototypeDefaults = (o: OptionsPrototype) => {
  return Object.fromEntries(
    Object.entries(o).map(([key, type]) => {
      const val = optionsPrototypeDefaults[type];
      return [key, val];
    })
  );
};

export const createAnimationsFromTemplate = (templateName: string, tracks: GroupedTrack[]) => {
  // TODO Mkae two required
  if (!templateName) return;
  const t = allTemplates.find((t) => t.name === templateName);
  const tmpl = new t.ctor();
  const anims = tmpl.createAnimations(tracks);
  anims.forEach((anim, i) => {
    const track = tracks.find((t) => t.name === anim.criteria.trackRef);
    const flatTrack = flattenTrack(track);
    const data = createMidiAnimationInitData(flatTrack);
    // anim.init(data);
  });
  return anims;
};

export const changeOpacity = (color: string, val: number) => {
  const {
    rgb: [r, g, b],
  } = parseColor(color);
  return `rgb(${r}, ${g}, ${b}, ${val})`;
};
